<template>
  <div></div>
</template>
<script>
/* eslint-disable */
import { Map, View, Feature, Overlay } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";
import * as olEasing from "ol/easing";
import * as olObservable from "ol/Observable";

import { locationEvent } from "./index.js";
import { memoEvent } from "@/modules/MemoLayer/index.js";

var ol = {};
ol.Map = Map;
ol.View = View;
ol.layer = olLayer;
ol.control = olControl;
ol.interaction = olInteraction;
ol.proj = olProj;
ol.source = olSource;
ol.style = olStyle;
ol.geom = olGeom;
ol.Feature = Feature;
ol.Overlay = Overlay;
ol.easing = olEasing;
ol.Observable = olObservable;

const icons_location = {
  0: require("./assets/location_circle_blue@0,5x.png"),
  1: require("./assets/location_people_blue@0,5x.png"),
  2: require("./assets/location_car_blue@0,5x.png"),
  3: require("./assets/location_heli_blue@0,5x.png"),
  10: require("./assets/location_circle_gray@0,5x.png"),
  11: require("./assets/location_people_gray@0,5x.png"),
  12: require("./assets/location_car_gray@0,5x.png"),
  13: require("./assets/location_heli_gray@0,5x.png"),
  99: require("./assets/location_base.png"),
};

const icons_location_click = {
  1: require("./assets/location_people_click.png"),
  2: require("./assets/location_car_click.png"),
  3: require("./assets/location_heli_click.png"),
  11: require("./assets/location_people_click.png"),
  12: require("./assets/location_car_click.png"),
  13: require("./assets/location_heli_click.png"),
  99: require("./assets/location_base_click.png"),
};

export default {
  data: function () {
    return {
      locationLayer: null,
      commuDialogTimerId: null,
      iconMenuOverlay: null,
      // iconPopupOverlay: null,
      mapMenuTimer: null,
      personalDest: null,
      personalDestCan: null,
      beaconTimer: null,
      tel_type: null,
      tel_code: null,
    };
  },
  computed: {
    users() {
      return this.$store.getters["amt/users"];
    },
    map() {
      return this.$store.state.map;
    },
    myId() {
      return this.$store.state.myId;
    },
    sip() {
      return this.$store.state.sip;
    },
    sip_connecting() {
      return this.$store.state.sip_connecting;
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
  },
  methods: {
    // ロケーションレイヤー
    createLocationLayer() {
      //const users = this.$store.state.users;
      const getLocationIcon = (location) => {
        if (icons_location[location]) {
          return icons_location[location];
        } else {
          return icons_location[1]; // "./assets/location_people_blue@0,5x.png";
        }
      };
      const getRotation = (heading) => {
        if (heading == null) {
          return 0;
        } else {
          return heading * (Math.PI / 180);
        }
      };
      const iconMapStyleFunction = (feature) => {
        const style = [
          new ol.style.Style({
            image: new ol.style.Icon({
              src: getLocationIcon(feature.get("location")),
              scale: 1.0,
              anchor: [0.5, 0.5],
              //opacity: 0.7
            }),
            text: new ol.style.Text({
              fill: new ol.style.Fill({ color: "#ff0000" }),
              stroke: new ol.style.Stroke({ color: "#ffffff", width: 3 }),
              //scale: 1.6,
              textAlign: "center",
              textBaseline: "top",
              offsetY: 13,
              text: feature.get("user_name"),
              font: "bold 15px Courier New, monospace",
            }),
          }),
          new ol.style.Style({
            image: new ol.style.Icon({
              src: require("./assets/location_point@0,5x.png"),
              scale: 1.0,
              anchor: [0.5, 2.1],
              rotation: getRotation(feature.get("heading")),
              opacity: feature.get("heading") == null ? 0 : 0.7,
            }),
          }),
        ];
        return style;
      };
      let iconFeatures = [];

      const iconSource = new ol.source.Vector({
        features: iconFeatures,
      });
      return new ol.layer.Vector({
        source: iconSource,
        style: iconMapStyleFunction,
        opacity: 1,
        visible: true,
      });
    },
    //位置アイコンの種類(1-3)
    getLocationKind(user, userId) {
      if (user.myIcon == this.$store.state.BASE_ICON_ID) {
        return this.$store.state.BASE_ICON_ID;
      } else {
        if (userId == this.myId) {
          return user.is_valid ? 0 : 10;
        } else {
          return user.is_valid ? user.myIcon : user.myIcon + 10;
        }
      }
    },
    //地図上アイコンの変更
    changeMapIcon(new_user, userId) {
      const user = this.$store.state.users[userId];
      const feature = this.locationLayer
        .getSource()
        .getFeatures()
        .find((f) => f.get("id") == userId);
      if (feature) {
        const iconPoint = new ol.geom.Point([
          new_user.lng,
          new_user.lat,
        ]).transform("EPSG:4326", "EPSG:3857");
        if (new_user.lng != user.lng || new_user.lat != user.lat) {
          feature.set("geometry", iconPoint);
        }

        if (
          new_user.user_name != user.user_name ||
          new_user.timestamp != user.timestamp
        ) {
          feature.set(
            "user_name",
            `${new_user.user_name}\n${new Date(
              new_user.timestamp
            ).toLocaleTimeString()}\n${Math.round(new_user.speed * 3.6)}km/h`
          );
        }

        if (new_user.heading != user.heading) {
          feature.set(
            "heading",
            Math.round(new_user.speed * 3.6) < 10 ? null : new_user.heading
          );
        }

        if (new_user.is_valid != user.is_valid) {
          feature.set("location", this.getLocationKind(new_user, userId));
        }
      }
    },

    // 地図上アイコンの削除
    deleteMapIcon(userId) {
      const source = this.locationLayer.getSource();
      const feature = source.getFeatures().find((f) => f.get("id") == userId);
      if (feature) {
        source.removeFeature(feature);
      }
    },
    // 地図上アイコン追加
    addMapIcon(user, userId) {
      if (user.lng == null) {
        return;
      }
      const source = this.locationLayer.getSource();
      const iconPoint = new ol.geom.Point([user.lng, user.lat]).transform(
        "EPSG:4326",
        "EPSG:3857"
      );
      // デフォルト位置を表示する場合はGPS時刻が空なので「--:--:--」を表示する
      const ts = user.timestamp
        ? new Date(user.timestamp).toLocaleTimeString()
        : "--:--:--";

      let newFeature = null;
      newFeature = new ol.Feature({
        geometry: iconPoint,
        name: "icon",
        id: userId,
        user_name:
          user.myIcon == this.$store.state.BASE_ICON_ID
            ? `${user.user_name}`
            : `${user.user_name}\n${ts}\n${Math.round(user.speed * 3.6)}km/h`,
        heading: Math.round(user.speed * 3.6) < 10 ? null : user.heading,
        location: this.getLocationKind(user, userId),
        tel_type: user.tel_type, // 2021.03.31 tel-mode
        tel_code: user.tel_code, // 2021.03.31 tel-mode
      });
      if (newFeature) source.addFeature(newFeature);
      // 自分の位置を示すアイコンの表示・非表示を制御
      if (userId == this.myId) {
        if (!this.$store.state.amt.myPosition) {
          const source = this.locationLayer.getSource();
          const feature = source
            .getFeatures()
            .find((f) => f.get("id") == userId);
          feature.setStyle(new ol.style.Style({}));
          return;
        }
      }
    },

    updateIcons(newUsers) {
      const features = this.locationLayer
        .getSource()
        .getFeatures()
        .filter((f) => f.get("name") == "icon");

      if (features) {
        features.forEach((f) => {
          this.deleteMapIcon(f.get("id"));
        });
      }

      Object.keys(newUsers).forEach((id) => {
        this.addMapIcon(newUsers[id], id);
      });
    },

    // 初期化
    initLocationLayer() {
      this.locationLayer = this.createLocationLayer();
      this.locationLayer.setZIndex(2);
      this.locationLayer.setOpacity(0.7);
      this.locationLayer.setVisible(true);
      this.map.addLayer(this.locationLayer);

      this.iconMenuOverlay = new ol.Overlay({
        element: document.getElementById("iconMenu"),
        positioning: "center-center",
      });
      // this.iconPopupOverlay = new ol.Overlay({
      //   element: document.getElementById("iconPopup"),
      //   positioning: "center-center"
      // });

      // let last_feature = null;
      // let original_style = null;
      this.map.on("click", (e) => {
        // const pos = this.$refs.map_popup.getPos();
        // if (pos) {
        //   this.$refs.map_popup.hide();
        // }
        // if (last_feature && original_style) {
        //   last_feature.setStyle(original_style);
        // }

        // var coord = ol.proj.transform(e.coordinate, 'EPSG:3857', 'EPSG:4326');
        // var lon = coord[0];
        // var lat = coord[1];
        // alert(lon);
        // alert(lat);

        this.map.forEachFeatureAtPixel(e.pixel, (feature, layer) => {
          // 地図上のクリック対象がアイコン且つ自分でない場合　＝＞　個別通話の発呼
          // この発呼の条件配下に「自分が個別通話中」の場合、Toastで警告を追加する。
          // ※2021/07/13表示条件追加：メモ・共有・手書きサブメニュー表示中は一覧表示NG
          if (
            feature.get("name") == "icon" &&
            feature.get("id") != this.myId &&
            !this.isOpenSubMenu()
          ) {
            if (this.$store.getters["ptt/pttActive"]) {
              this.$bvToast.toast("送信中です。", {
                title: "AirMultiTalk",
                toaster: "b-toaster-top-center",
                variant: "danger",
                autoHideDelay: 3000,
                appendToast: false,
              });
            } else if (this.$store.getters["ptt/pttRecv"]) {
              this.$bvToast.toast("着信中です。", {
                title: "AirMultiTalk",
                toaster: "b-toaster-top-center",
                variant: "danger",
                autoHideDelay: 3000,
                appendToast: false,
              });
            } else if (this.$store.state.per == "person") {
              this.$bvToast.toast("あなたは既に個別通話中です。", {
                title: "AirMultiTalk",
                toaster: "b-toaster-top-center",
                variant: "danger",
                autoHideDelay: 3000,
                appendToast: false,
              });
            } else {
              const dest = feature.get("id");
              const user = this.$store.state.users[dest];

              if (user.per == "person") {
                this.$bvToast.toast(`${user.user_name}は個別通話中です。`, {
                  title: "AirMultiTalk",
                  toaster: "b-toaster-top-center",
                  variant: "danger",
                  autoHideDelay: 3000,
                  appendToast: false,
                });
              } else {
                // this.$store.state.sound_on();
                // const hisCoord = feature.getGeometry().getCoordinates();
                // this.map.getView().setCenter(hisCoord);
                // this.iconMenuOverlay.setPosition(hisCoord);
                // // this.iconPopupOverlay.setPosition(hisCoord);
                // this.map.addOverlay(this.iconMenuOverlay);
                // // this.map.addOverlay(this.iconPopupOverlay);

                this.personalDestCan = feature.get("id");
                this.tel_type = feature.get("tel_type"); // 2021.03.31 tel-mode
                this.tel_code = feature.get("tel_code"); // 2021.03.31 tel-mode

                // const videoBtn = document.getElementById("mapVideoBtn");
                // const telBtn = document.getElementById("mapTelBtn");
                // const sipBtn = document.getElementById("mapSipBtn");
                // videoBtn.style.display = "none";
                // telBtn.style.display = "none";
                // sipBtn.style.display = "none";

                // if (user && user.page == "map") {
                //   videoBtn.style.backgroundColor = "black";
                //   telBtn.style.backgroundColor = "black";
                // } else {
                //   videoBtn.style.backgroundColor = "gray";
                //   telBtn.style.backgroundColor = "gray";
                // }

                // if (user && this.sip_connecting) {
                //   sipBtn.style.backgroundColor = "black";
                // } else {
                //   sipBtn.style.backgroundColor = "gray";
                // }

                // // スマートフォン表示での不具合へ対応
                // // チャタリングのような現象でメニューの表示→通話orビデオ通話の開始が発生していたので遅延を挟んだ

                // if (user.user_type == "amt") {
                //   setTimeout(() => (videoBtn.style.display = "block"), 100);
                //   setTimeout(() => (telBtn.style.display = "block"), 100);
                // }
                // if (this.tel_type == "1" && this.sip) { // 相手が電話番号を持ち、自分がSIP登録がある。
                //   setTimeout(() => (sipBtn.style.display = "block"), 100);
                // }

                if (user.user_type != "amt" && !this.sip) {
                  this.$bvToast.toast(`${user.user_name}とは通話出来ません`, {
                    title: "AirMultiTalk",
                    toaster: "b-toaster-top-center",
                    variant: "danger",
                    autoHideDelay: 3000,
                    appendToast: false,
                  });
                } else {
                  const self = this;
                  let click_info = {};
                  click_info.id = user.user_name;
                  click_info.type = "img";
                  click_info.click = function () {
                    // 個別通話を開始すると一覧を閉じるので開始前に変更を反映
                    for (let key in self.$store.state.memo_update) {
                      if (
                        0 ===
                        Object.keys(self.$store.state.memo_update[key]).length
                      ) {
                        // 削除の場合
                        memoEvent.$emit("DEL_MEMO", key);
                      } else {
                        memoEvent.$emit(
                          "UPDATE_MEMO",
                          self.$store.state.memo_update[key]
                        );
                      }
                    }
                    // 地図に戻る時にクリア
                    self.$store.commit("resetMemoUpdate");
                    // 詳細画面を表示
                    self.$store.commit("setViewLayersDeviceInfo", click_info);
                    self.$store.commit("setViewLayersDevice", true);
                    // クリック時は下の要素が透けて見える為、非表示へ
                    self.$store.commit("hiddenClickList");
                  };
                  click_info.img = undefined;
                  if (icons_location_click[user.myIcon]) {
                    click_info.img = icons_location_click[user.myIcon];
                  } else {
                    click_info.img = icons_location_click[1]; // "./assets/location_people_click.png";
                  }
                  click_info.info = user.user_name;
                  // 個別通話で使用
                  click_info.personalDestCan = feature.get("id");
                  this.$store.commit("addClickTargets", click_info);
                  this.$store.commit("displayClickList");
                }

                this.mapMenuTimer = setTimeout(() => {
                  this.map.removeOverlay(this.iconMenuOverlay);
                  // this.map.removeOverlay(this.iconPopupOverlay);
                  this.tel_type = null;
                  this.tel_code = null;
                }, 3000);
              }
            }
          }
        });
      });

      this.$store.state.mapChangeEvent.$on("StartPersonalPhone", (userId) => {
        this.log("StartPersonalPhone: " + userId);
      });
    },
    getPersonalDest() {
      return this.$store.state.personalDest;
    },
    setPersonalDest(val) {
      this.$store.commit("setPersonalDest", val);
      this.$store.state.userLocation.sendRenewNow();
    },
    clearPersonalDest() {
      this.$store.commit("clearPersonalDest");
      this.$store.state.userLocation.sendRenewNow();
    },
    onMapVideoBtn() {
      this.$store.state.sound_on();
      // 個別通話（映像）のボタンを押下した際の処理
      clearTimeout(this.mapMenuTimer);
      this.map.removeOverlay(this.iconMenuOverlay);
      this.$refs.rtc_private.call_to_remote(this.personalDestCan, "video");
    },
    onMapTelBtn() {
      this.$store.state.sound_on();
      // 個別通話（通話）のボタンを押下した際の処理
      clearTimeout(this.mapMenuTimer);
      this.map.removeOverlay(this.iconMenuOverlay);
      this.$refs.rtc_private.call_to_remote(this.personalDestCan, "audio");
    },
    onMapSipBtn() {
      this.$store.state.sound_on();
      // 個別通話（SIP）のボタンを押下した際の処理
      clearTimeout(this.mapMenuTimer);
      this.map.removeOverlay(this.iconMenuOverlay);
      this.$refs.tel_private.call_to_remote(this.tel_code);
      this.tel_code = null;
      this.tel_type = null;
    },
    // 特定のサブメニューが開いているかチェック
    isOpenSubMenu() {
      var layers = this.$store.getters["amt/layers"];
      var memoLayer = layers["MemoLayer"];
      var shareLayer = layers["ShareLayer"];
      var drawLayer = layers["DrawLayer"];

      if (
        memoLayer.layer.getActive() ||
        shareLayer.layer.getActive() ||
        drawLayer.layer.getActive()
      ) {
        // メモ・共有・手書きのサブメニュー表示中
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    // this.$store.state.mapChangeEvent.$on("closePersonal", dataOrForce => {
    //   if (
    //     this.getPersonalDest() != null &&
    //     (dataOrForce === true || this.getPersonalDest() == dataOrForce.src)
    //   ) {
    //     //this.personalDest = null;
    //     this.clearPersonalDest();
    //   }
    // });
    // this.$store.state.mapChangeEvent.$on("commuRejected", kind => {
    //   const body =
    //     (kind == "audio" ? "個人通話が" : "テレビ電話が") + "断られました。";
    //   this.$store.commit("alertOn", { header: "", body: body });
    // });

    locationEvent.$on("SETOPACITY", (val) => {
      if (this.locationLayer) {
        this.locationLayer.setOpacity(val);
      }
    });

    locationEvent.$on("SETVISIBLE", (val) => {
      if (this.locationLayer) {
        this.locationLayer.setVisible(val);
      }
    });

    locationEvent.$on("SETZINDEX", (val) => {
      if (this.locationLayer) {
        this.locationLayer.setZIndex(val);
      }
    });
  },
  beforeDestroy() {},
};
</script>
