export function loggerMaker($store) {
  let logLevel = 1; //0:all(debug, info, error), 1:info, error, 2: error only
  let logMode = 1; //0: console, 1: server, 2: both

  function exec(logMsg, level) {
    if (level >= logLevel || !$store.state.socketio) {
      if (logMode == 0 || logMode == 2) {
        if (level <= 1) {
          console.log(logMsg);
        } else {
          console.error(logMsg);
        }
      }
      if (logMode == 1 || logMode == 2) {
        try {
          $store.state.socketio.emit(
            "log",
            JSON.stringify({
              user_id: $store.state.myId,
              level: level,
              log: logMsg,
            })
          );
        } catch {
          console.log(logMsg);
        }
      }
    }
  }

  return {
    setLevel(level) {
      logLevel = level;
    },
    getLevel() {
      return logLevel;
    },
    setMode(mode) {
      logMode = mode;
    },
    getMode() {
      return logMode;
    },
    debug(logMsg) {
      exec(logMsg, 0);
    },
    info(logMsg) {
      exec(logMsg, 1);
    },
    error(logMsg) {
      exec(logMsg, 2);
    },
  };
}
