<template>
  <div>
    <audio ref="audio_el"></audio>
    <modalDialog v-if="is_dialog_visible">
      <span slot="header"></span>
      <span slot="body">{{ dialog_msg }}</span>
      <div slot="footer">
        <button class="btn btn-primary" @click="close_msg_dialog">はい</button>
      </div>
    </modalDialog>
  </div>
</template>

<script>
/* eslint-disable */
import { v4 as uuidv4 } from "uuid";
import ModalDialog from "@/components/ModalDialog.vue";
import Rec_pcm from "@/components/rec_pcm.js";

export default {
  components: {
    modalDialog: ModalDialog,
  },
  data() {
    return {
      recorder: null,
      recognition: null,
      url: "/api/callrec",
      is_dialog_visible: false,
      dialog_msg: "",
      rec_pcm: null
    };
  },
  mounted() {
    // this.get_audio_stream();
    if (this.ptt_type == "callrec" || this.ptt_type == "webrtc") {
      this.socketio.on("message", this.on_msg);
    }
    if (this.speech_recognition) this.init_recognition();
    this.rec_pcm = Rec_pcm();
  },
  computed: {
    socketio() {
      return this.$store.getters["socketio"];
    },
    at_send() {
      return this.$store.getters["ptt/pttActive"];
    },
    ptt_type() {
      return this.$store.state.ptt_type;
    },
    callrec_display() {
      return this.$store.state.callrec_display;
    },
    myId() {
      return this.$store.getters["amt/myId"];
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
    users() {
      return this.$store.getters["amt/users"];
    },
    user() {
      return this.myId && this.users[this.myId];
    },
    sound_on() {
      return this.$store.state.sound_on;
    },
    speech_recognition() {
      return this.$store.getters["speech_recognition"];
    },
    auto_play() {
      return this.$store.getters["auto_play"];
    },
    // group_name() {
    //   const groups = this.$store.state.groups;
    //   const group = Object.values(groups).find(gr => {
    //     return gr.group_id == this.group_id;
    //   });
    //   return group && group.group_name ? group.group_name : "";
    // }
  },
  watch: {
    // 送信用音声ストリームが切替った事を検出している。
    at_send(val) {
      if (val) {
        this.file_name = this.gen_file_name();
        const stream = this.$store.getters["rtc/local_audio_stream"];
        // this.init_rec(stream);
        // if (this.recognition) this.recognition.start();

        // 音声認識用にPCM（16bitモノラル16kHzサンプリング）にて録音を行う。
        this.rec_pcm.start(stream);
      } else {
        // if (this.recorder) this.recorder.stop();
        // if (this.recognition) this.recognition.stop();

        this.rec_pcm.stop(async (blob) => {
          const file_name_wav = this.file_name + ".wav";
          if (blob.size > 8192) {
            try {
              const res = await this.post_file(file_name_wav, blob);
              await this.after_post(res);
            } catch (err) {
              console.log(err);
            }
          }
        });
      }
    },
  },
  methods: {
    // close_msg_dialog() {
    //   this.is_dialog_visible = false;
    //   this.$refs.audio_el.src = this.sound_file;
    //   this.$refs.audio_el.play();
    // },

    on_msg(msg) {
      const re = /.+_.+_(.+)_.+.webm/.exec(msg.file);
      const _d = re ? `${re[1]} : ` : "";
      const txt = `${_d} ${msg.src}よりメッセージが着信がありました。　`;
      console.log(`FILE: ${msg.audio_file}`);

      if (this.ptt_type == "callrec") {
        const speech = new SpeechSynthesisUtterance();
        speech.text = txt;

        speech.volume = 1.0; // 音量: 0.0～1.0
        speech.rate = 1.0; // 速度: 0.1～10.0
        speech.pitch = 1.0; // 音程: 0.0～2.0
        speech.lang = "ja-JP"; // 言語

        // var voices = window.speechSynthesis.getVoices();
        // speech.voice = voices[0];
        speechSynthesis.speak(speech);
        // speech.onend = () => this.message_play(`/callrec/${msg.file}`);
        speech.onend = () => this.sound_on(`/callrec/${msg.audio_file}`);
      }

      if (this.auto_play) {
        this.sound_on(`/callrec/${msg.audio_file}`);
      }

      const toast_id = uuidv4();
      const play_btn = this.$createElement(
        "b-button",
        {
          on: {
            click: () => {
              this.sound_on(`/callrec/${msg.audio_file}`);
              this.$bvToast.hide(toast_id);
            },
          },
        },
        "PLAY"
      );
      const text_btn = this.speech_recognition
        ? this.$createElement(
            "b-button",
            {
              on: {
                click: () => {
                  this.$bvToast.hide(toast_id);
                  try {
                    fetch(`/callrec/${msg.text_file}`).then((res) => {
                      if (res.status == 200) {
                        return res.text().then((text) => {
                          this.dialog_msg = text;
                          this.is_dialog_visible = true;
                        });
                      } else {
                        this.dialog_msg = "(記録無し)";
                        this.is_dialog_visible = true;
                      }
                    });
                  } catch (err) {
                    console.log(err);
                  }
                },
              },
            },
            "TEXT"
          )
        : null;
      const hiden_link = this.$createElement(
        "b-link",
        {
          href: "#",
          on: {
            click: () => {
              this.$root.$bvToast.hide();
            },
          },
        },
        "全て閉じる"
      );
      const vnodes = [txt, [play_btn, text_btn, hiden_link]];

      // 以下の処理は、Callrec.vueが読み込まれている事が前提なので、必ず走る
      // ptt_typeが何であるかは関係無く、callrec_displayの値次第で、
      // 再生ボタンの表示を行うか、を決める
      if (this.callrec_display) {
        this.$bvToast.toast(vnodes, {
          id: toast_id,
          title: "AirMultiTalk",
          toaster: "b-toaster-top-center",
          variant: "danger",
          noAutoHide: true,
          appendToast: true,
        });
      } else {
        this.$bvToast.toast(txt, {
          id: toast_id,
          title: "AirMultiTalk",
          toaster: "b-toaster-top-center",
          variant: "danger",
          autoHideDelay: 6000,
          appendToast: true,
        });
      }
    },
    close_msg_dialog() {
      this.is_dialog_visible = false;
      this.dialog_msg = "";
    },
    init_rec(stream) {
      let chunks = [];
      const myType = "audio/webm; codecs=opus";
      this.recorder = new MediaRecorder(stream);
      this.recorder.ondataavailable = (ev) => {
        chunks.push(ev.data);
      };
      this.recorder.onstop = async () => {
        let blob = new Blob(chunks, { type: myType });

        const file_name_webm = this.file_name + ".webm";
        if (blob.size > 8192) {
          try {
            const res = await this.post_file(file_name_webm, blob);
            await this.after_post(res);
          } catch (err) {
            console.log(err);
          }
        }
        chunks = [];
        this.recorder = null;
      };
      this.recorder.start();
    },
    init_recognition() {
      const myType = "text/plain";
      const SpeechRecognition =
        window.webkitSpeechRecognition || window.SpeechRecognition;
      this.recognition = new SpeechRecognition();

      this.recognition.lang = "ja-JP";
      this.recognition.interimResults = true;
      this.recognition.continuous = true;

      this.recognition.onresult = async (ev) => {
        this.speech_text = "";
        for (let i = ev.resultIndex; i < ev.results.length; i++) {
          let transcript = ev.results[i][0].transcript;
          if (ev.results[i].isFinal) {
            this.speech_text += transcript;
          }
        }
        if (this.speech_text != "") {
          console.log(this.speech_text);
          let blob = new Blob([this.speech_text], { type: myType });

          const file_name_txt = this.file_name + ".txt";
          try {
            await this.post_file(file_name_txt, blob);
          } catch (err) {
            console.log(err);
          }
        }
      };
    },
    gen_file_name() {
      const _d = new Date().toLocaleString().split(/\/|:|\s/gi);
      const cur_date = `${_d[0]}年${_d[1]}月${_d[2]}日${_d[3]}時${_d[4]}分${_d[5]}秒`;
      if (this.last_date == cur_date) {
        this.count_date++;
      } else {
        this.count_date = 0;
      }
      this.last_date = cur_date;
      return `通話_${this.$store.state.myName}_${cur_date}_${this.count_date}`;
    },
    async post_file(file_name, b64) {
      let formData = new FormData();
      formData.append("data", b64, encodeURIComponent(file_name));
      formData.append("info", JSON.stringify({
          id: this.myId,
          group_id: this.group_id,
          user_name: this.user.user_name
        })
      );
      formData.append("group_id", this.$store.getters["group_id"]);
      console.log(...formData.entries());

      return fetch(this.url, {
        method: "post",
        mode: "no-cors",
        body: formData,
      });
      // .then(response => {
      //   if (response.status == 200) {
      //     console.log(`成功:${response}`);
      //     this.socketio.emit("message", {
      //       type: "callrec",
      //       src: this.$store.getters["amt/users"][
      //         this.$store.getters["amt/myId"]
      //       ].user_name,
      //       group_id: this.$store.getters["group_id"],
      //       file: file_name
      //     });
      //   } else {
      //     console.log(`失敗:${response}`);
      //   }
      // })
      // .catch(error => console.log(error.message));
    },
    async after_post(response) {
      if (response.status == 200) {
        console.log(`成功:${response}`);
        this.socketio.emit("message", {
          type: "callrec",
          src: this.$store.getters["amt/users"][this.$store.getters["amt/myId"]]
            .user_name,
          group_id: this.$store.getters["group_id"],
          // audio_file: this.file_name + ".webm",
          audio_file: this.file_name + ".wav",
          text_file: this.speech_recognition ? this.file_name + ".txt" : "",
        });
      } else {
        console.log(`失敗:${response}`);
      }
    },
  },
  beforeDestroy() {
    this.socketio.off("message", this.on_msg);
  },
};
</script>
<style scoped>
.b-button {
  padding: 10px;
}
</style>
