import $store from "@/store";
import hazardMapComponent from "./hazardMapComponent.vue";

import Vue from "vue";
export const hazardMapEvent = new Vue();

export function hazardMapLayer() {
  const noop = () => {};

  function add() {
    $store.commit("amt/addComponent", hazardMapComponent);
    setTimeout(() => {
      hazardMapEvent.$emit("INIT", null);
    }, 1000);
  }

  function setOpacity(val) {
    hazardMapEvent.$emit("SETOPACITY", val);
  }

  function setVisible(val) {
    hazardMapEvent.$emit("SETVISIBLE", val);
  }

  function setZIndex(val) {
    hazardMapEvent.$emit("SETZINDEX", val);
  }

  return {
    add, // マップ初期化時に呼び出し
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity,
    setVisible,
    setZIndex,
    setActive: noop, // メニュー操作でトグル
  };
}
