/* eslint-disable */
import $store from "@/store";
import io from "socket.io-client";

// GPSサーバとのコネクション
let gps_socket = null;

// 機種判断
let isAndroid = false;
let isIPAD = false;
let isIOS = false;
let isUnknown = false;

const ua = navigator.userAgent.toLowerCase();
if (ua.indexOf("android") != -1) {
  isAndroid = true;
} else if(ua.indexOf('ipad') > -1 || ua.indexOf('macintosh') > -1 && 'ontouchend' in document){
  isIPAD = true;
} else if (/(iphone|ipod|macintosh)/g.test(ua) && "ontouchend" in document) {
  isIOS = true;
} else {
  isUnknown = true;
}

export default function userLocationMaker(socketio, geoOptions) {
  const position = {}; //自分の位置
  const gps_server_addr = $store.state.gps_server;
  const gps_timeout = $store.state.gps_timeout_ms;

  const logDebug = function (msg) {
    $store.state.logger.debug(msg);
  };
  const log = function (msg) {
    $store.state.logger.info(msg);
  };
  const logError = function (msg) {
    $store.state.logger.error(msg);
  };

  //---------------------------------------------------------------------------
  // GPS位置
  //---------------------------------------------------------------------------

  if (geoOptions == null) {
    geoOptions = {
      enableHighAccuracy: true,
      maximumAge: 0,
      timeout: 10000,
    };
  }

  function sendRenewNow() {
    position.group_id =
      $store.state.activeId.kind == "group" ? $store.state.activeId.id : null;
    position.page = $store.state.page;
    position.icon = $store.state.myIcon;
    position.status = $store.state.workStatus;
    position.group_id = $store.state.group_id;
    socketio.emit("renew", JSON.stringify(position));
  }

  const errorMessage = {
    0: "原因不明のエラーが発生しました。",
    1: "位置情報の取得が許可されませんでした。",
    2: "電波状況などで位置情報が取得できませんでした。",
    3: "位置情報の取得に時間がかかり過ぎてタイムアウトしました。",
  };

  function pos_rounddown(pos) {
    const digit = 6;

    // 小数点第7位を四捨五入
    return Math.round(pos * Math.pow(10, digit)) / Math.pow(10, digit);
  }

  function initPos(uid, uname) {
    //GPS前に送信
    position.user_id = uid;
    position.user_type = $store.state.user_type; // 2021.04.07 user_typeを追加（そもそも、ココで正しいか？）
    position.user_name = uname;
    position.is_valid = false;
    position.timestamp = $store.state.initial_time;
    // 固定局は最初から固定位置を通知、移動局は初回の測位結果が分かるまで緯度経度を通知しない
    position.lat = $store.state.fixed_pos
      ? pos_rounddown($store.state.default_lat)
      : pos_rounddown($store.state.initial_lat);
    position.lng = $store.state.fixed_pos
      ? pos_rounddown($store.state.default_lng)
      : pos_rounddown($store.state.initial_lng);
    position.heading = null;
    position.speed = null;
    position.group_id = $store.state.group_id;
    position.page = $store.state.page;
    position.myIcon = $store.state.myIcon;
    position.status = $store.state.workStatus;
    position.volume = $store.state.volume;
    position.expires = 0;
    position.tel_type = $store.state.tel_type; // 2021.03.31 tel-mode
    position.tel_code = $store.state.tel_code; // 2021.03.31 tel-mode

    socketio.emit("renew", JSON.stringify(position));

    // 既に位置情報監視中ならいったん解除
    if ($store.state.watch_timerId) {
      log(`-------CLEAR TIMER (WATCH ID:${$store.state.watch_timerId})-------`);

      (isAndroid === true) ?
      navigator.geolocation.clearWatch($store.state.watch_timerId) :
      clearInterval($store.state.watch_timerId);
    }

    // 既にGPSサーバ接続済みならいったん切断
    if (gps_socket) {
      gps_socket.off();
      gps_socket.close();
    }

    // 固定局の場合、そもそも、GPS測位を行わない。
    if ($store.state.fixed_pos) return;

    // GPSサーバから位置情報受信
    if (gps_server_addr) {
      gps_socket = io(gps_server_addr);
      if (gps_socket.connected) {
        // GPSサーバと接続できている
        gps_socket.on("gps", (data) => {
          if (data.fix && data.lat != null && data.lng != null) {
            if (position.timestamp != data.timestamp) {
              // タイムスタンプ更新時、このデータの有効期限を延長
              position.expires = Date.now() + gps_timeout;
            }
            position.lat = pos_rounddown(data.lat);
            position.lng = pos_rounddown(data.lng);
            position.heading = data.heading;
            // @todo 本来はgps_serverからm/sに直したspeedが通知されてくるはずが
            // gps_server側の処理が抜けているため、ここでkm/hからにm/sに変換している
            // 将来的にはgps_serverでの変換に変更し、その際は通知されるJSONオブジェクトに
            // インタフェースバージョンを追加してエアマルチサーバ側でgps_serverバージョンの
            // 混在を許容できるようにする(バージョンなしはkm/hで通知とする)
            position.speed = data.speed / 3.6;
            position.timestamp = data.timestamp;
            position.altitude = data.alt;
            position.is_valid = true;
          } else {
            // 測位するまで初期位置を通知
            if (position.lat == null && position.lng == null) {
              position.lat = pos_rounddown($store.state.initial_lat);
              position.lng = pos_rounddown($store.state.initial_lng);
              position.timestamp = $store.state.initial_time;
            }
          }
          $store.commit("amt/position", position);
        });

        // GPSサーバと接続できていればwatchPosition()を呼び出さない
        return;
      } else {
        // GPSサーバと接続できなければGeolocation APIから位置情報を取得する
        logError(`GPSサーバ(${gps_server_addr})と接続できませんでした。`);
        gps_socket = null; // connect(open)できていないのでdisconnect(close)しない
      }
    }

    const pos_success = (pos) => {
      // @todo Geolocation APIの仕様上、コールバックされたら必ず位置情報は取得
      // できているような気もするが、万が一でlat,lngに無効値がセットされて地図表示
      // がおかしくなるのを防ぐ
      if (pos.coords.latitude != null && pos.coords.longitude != null) {
        if (position.timestamp != pos.timestamp) {
          // タイムスタンプ更新時、このデータの有効期限を延長
          position.expires = Date.now() + gps_timeout;
        }
        position.lat = pos_rounddown(pos.coords.latitude);
        position.lng = pos_rounddown(pos.coords.longitude);
        position.heading = pos.coords.heading;
        position.speed = pos.coords.speed;
        position.timestamp = pos.timestamp;
        position.altitude = pos.coords.altitude;
        position.accuracy = pos.coords.accuracy;
        position.altitudeAccuracy = pos.coords.altitudeAccuracy;
        position.is_valid = true;
        $store.commit("amt/position", position);
      }
    };

    const pos_error = (err) => {
      // 2022/08/04 失敗なんだからis_valid = false;
      position.is_valid = false;
      if (errorMessage[err.code]) {
        logError("GEO_ERROR:" + errorMessage[err.code]);
      }
      // 測位するまで初期位置を通知
      if (position.lat == null && position.lng == null) {
        position.lat = pos_rounddown($store.state.initial_lat);
        position.lng = pos_rounddown($store.state.initial_lng);
        position.timestamp = $store.state.initial_time;
      }
    };

    // Geolocation APIによる位置情報監視
    $store.commit(
      "setWatchTimerId", (isAndroid === true) ?
      navigator.geolocation.watchPosition(pos_success, pos_error, geoOptions) :
      setInterval(() => {
        navigator.geolocation.getCurrentPosition(pos_success, pos_error, geoOptions)
      }, 2000)
    );
  }

  // 1秒間隔で定期的にGPS測位状態を送信する
  if ($store.state.renew_timerId) {
    log(`-------CLEAR TIMER (RENEW ID:${$store.state.renew_timerId})-------`);
    clearInterval($store.state.renew_timerId);
  }

  $store.commit(
    "setRenewTimerId",
    setInterval(() => {
      position.group_id =
        $store.state.activeId.kind == "group" ? $store.state.activeId.id : null;
      position.page = $store.state.page;
      position.myIcon = $store.state.myIcon;
      position.status = $store.state.workStatus;
      position.per = $store.state.per;
      position.volume = $store.state.volume;

      // user_configに設定されたタイムアウト時間を経過したら位置情報無効
      // position.timestampとの比較だと端末の時刻がずれている場合に誤判定するため
      // あえてGPSデータの時刻ではなくGPSデータを受信した時刻を基準にしている
      const now_time = Date.now();
      if (position.is_valid && now_time > position.expires) {
        log("position data is expired");
        position.is_valid = false;
      }
      if ($store.state.rtc.local_audio_stream) {
        socketio.emit("renew", JSON.stringify(position));
      }
    }, 1000)
  );

  return {
    initPos,
    getMyPosition() {
      return position;
    },
    setPage(pageName) {
      position.page = pageName;
    },
    sendRenewNow,
  };
}
