<template>
  <div class="remote_box">
    <template v-if="is_group_in_local">
      <div class="videoTitle" :style="titleStyleObj">
        <div class="videoTitleLeft">
          <img :src="img_menu" class="icon_img" />
        </div>
        <div class="videoTitleCenter">
          {{ user && user.user_name + talk_mode }}
        </div>
        <div class="videoTitleRight" @click="onTitleClick">
          <img
            :src="downButtonSrc"
            class="icon_img"
            :class="{ rotate90: !show_video }"
          />
        </div>
      </div>
    </template>
    <template v-if="is_private_in_local">
      <div class="videoTitle">
        <div class="videoTitleLeft">
          <img :src="img_menu" class="icon_img" />
        </div>
        <div class="videoTitleCenter">{{ user && user.user_name }}:個別</div>
        <div class="videoTitleRight" @click="stop" title="通話終了">
          <img :src="img_close" />
        </div>
      </div>
    </template>
    <level-meter :audio_level="audio_level" class="levelMeter"></level-meter>
    <div v-if="is_loading" style="width: 100%; z-index: 100">
      <div class="loaderContainer">
        <div class="spinner-border text-light" role="status">
          <span class="sr-only">Loading...</span>
        </div>
        <div style="color: white">
          {{ connectionState }}/{{ signalingState }}
        </div>
      </div>
    </div>
    <div v-show="show_video && !is_loading" class="remote_video_box">
      <video
        ref="remote_video"
        :class="{ remote_video_none: onC360play }"
        class="remote_video"
        muted
        autoplay
        playsinline
        @dblclick="fullscreen"
      ></video>
      <c-360-elm
        class="remote_canvas"
        v-if="onC360play"
        :videoElm="$refs.remote_video"
        :width="settings_height"
        :height="settings_height"
      ></c-360-elm>
      <div class="remote_settings">
        {{ settings_height }}@{{ settings_frameRate }}fps
      </div>
    </div>
    <div
      v-show="show_audio && !is_loading"
      class="remote_audio_box"
      :style="style_remote_audio"
      @mousedown.stop="audio_on_off"
      @touchstart.stop="audio_on_off"
    >
      <img :src="img_mic" class="img_mic" />
    </div>
    <audio ref="remote_audio" autoplay></audio>
  </div>
</template>

<script>
import LevelMeter from "./LevelMeter";
import P2P from "./P2P";
import c360Elm from "@/components/C360Elm.vue";

export default {
  data() {
    return {
      audio_level: 0,
      audio_on_once: false,
      last_audio_level: 0,
      show_video: false,
      show_audio: false,
      is_loading: false,
      connectionState: "",
      signalingState: "",
      img_mic: require("@/assets/mic.png"),
      audio_on_off_flag: false,
      start_audio_once: false,
      offer_video_peer: null,
      offer_audio_peer: null,
      answer_video_peer: null,
      answer_audio_peer: null,
      audio_sender: null,
      clearId: {},
      img_close: require("@/assets/etc_close@1x.png"),
      img_menu: require("./assets/menu@1x.png"),
      onC360play: false,
      remote_video_settings: null,
      remote_settings_id: null,
    };
  },
  components: {
    levelMeter: LevelMeter,
    c360Elm,
  },
  props: ["remote_id", "user", "mode"],
  mounted() {
    console.log("★★★★★★★★★  REMOTE_VIDEO MOUNTED  ★★★★★★★★★★★★★");

    this.offer_video_peer = new P2P({
      kind: "video",
      target_id: this.myId,
      myId: this.myId,
      remote_id: this.remote_id,
      mode: this.mode,
      stream: this.local_video_stream,
      socketio: this.socketio,
      stun_server: this.stun_server,
      video_rate: this.video_rate,
      video_encode: this.video_encode,
      callback: {
        ontrack: (ev) => {
          this.on_track(ev);
          this.$refs.remote_video.srcObject = new MediaStream([ev.track]);
          this.show_video = true;
          // -------------------------------------------------------------
          // android対策
          // 何故か、trackがendedになっている時がある。
          ev.track.onended = () => {
            this.show_video = false;
            this.start_stop_video();
          };
          // -------------------------------------------------------------
          // iOS対策
          // 何故か、autoplayが効かない時がある。
          setTimeout(() => {
            this.$refs.remote_video.play();
          }, 500);
        },
        // on_failed: () => {
        //   this.stop();
        // },
        con_state_emit: (ev) => {
          this.connectionState = ev.connectionState;
          if (ev.connectionState == "connected") {
            this.is_loading = false;
          }
        },
        sig_state_emit: (ev) => {
          this.signalingState = ev.signalingState;
        },
      },
    });
    this.answer_video_peer = new P2P({
      kind: "video",
      target_id: this.remote_id,
      myId: this.myId,
      remote_id: this.remote_id,
      mode: this.mode,
      stream: this.local_video_stream,
      socketio: this.socketio,
      stun_server: this.stun_server,
      video_rate: this.video_rate,
      video_encode: this.video_encode,
      callback: {
        ontrack: (ev) => {
          this.on_track(ev);
          this.$refs.remote_video.srcObject = new MediaStream([ev.track]);
          this.show_video = true;
        },
        // on_failed: () => {
        //   this.stop();
        // },
        con_state_emit: (ev) => {
          if (this.is_private_in_local) {
            this.connectionState = ev.connectionState;
            if (ev.connectionState == "connected") {
              this.$store.state.toast_on(
                `${this.user.user_name}が閲覧を開始しました`,
                "success"
              );
            } else {
              this.is_loading = false;
            }
          }
        },
        sig_state_emit: (ev) => {
          if (this.is_private_in_local) {
            this.signalingState = ev.signalingState;
          }
        },
      },
    });
    // --------------------------------------------
    this.offer_audio_peer = new P2P({
      kind: "audio",
      target_id: this.myId,
      myId: this.myId,
      remote_id: this.remote_id,
      mode: this.mode,
      stream: this.local_audio_stream,
      socketio: this.socketio,
      stun_server: this.stun_server,
      check_connection_period: this.check_connection_period,
      callback: {
        ontrack: (ev) => {
          this.$refs.remote_audio.srcObject = new MediaStream([ev.track]);
          // const tr = ev.transceiver;
          // tr.sender.track.enabled = true;
          // this.audio_sender = tr.sender;
          // this.img_mic = require("@/assets/mic.png");
          if (this.mode == "private") this.show_audio = true;
          this.start_get_audio_level(this.offer_audio_peer, "offer_audio");
        },
        on_failed: () => {
          this.audio_restart();
        },
        con_state_emit: (ev) => {
          if (ev.connectionState == "connected") {
            this.is_loading = false;
          }
        },
      },
    });
    this.answer_audio_peer = new P2P({
      kind: "audio",
      target_id: this.remote_id,
      myId: this.myId,
      remote_id: this.remote_id,
      mode: this.mode,
      stream: this.local_audio_stream,
      socketio: this.socketio,
      stun_server: this.stun_server,
      check_connection_period: this.check_connection_period,
      callback: {
        ontrack: (ev) => {
          this.$refs.remote_audio.srcObject = new MediaStream([ev.track]);
          // const tr = ev.transceiver;
          // tr.sender.track.enabled = true;
          // this.audio_sender = tr.sender;
          // this.img_mic = require("@/assets/mic.png");
          if (this.mode == "private") this.show_audio = true;
          this.start_get_audio_level(this.answer_audio_peer, "answer_audio");
        },
        // on_failed: () => {
        //   this.stop();
        // },
        con_state_emit: (ev) => {
          if (ev.connectionState == "connected") {
            this.is_loading = false;
          }
        },
      },
    });
    if (this.mode == "group" && this.start_audio_once == false) {
      this.start_audio_once = true;
      setTimeout(() => {
        this.offer_audio_peer.sv_start();
        // this.offer_audio_peer.start();
        console.log("こっちは、mountedの中で起動されたsv_startだよ～");
      }, 3000);
    }
    if (this.$refs.remote_video) {
      this.$refs.remote_video.oncanplay = () => {
        this.is_loading = false;
        console.log("★★★★★★ CAN PLAY ★★★★★★★");
      };
    }
    if (this.is_private_in_local) {
      this.is_loading = true;
    }

    // this.socketio.on("publish", this.recved_msg);
    // setTimeout(() => {
    //   this.start_offer("audio");
    // }, 3000);
  },
  watch: {
    at_send(val) {
      if (this.ptt_type == "webrtc" && this.is_group_in_local) {
        const sender = this.offer_audio_peer.get_sender();

        if (sender) {
          if (val) {
            if (this.offer_audio_peer.connectionState != "connected") {
              this.$store.state.toast_on(
                `${this.user.user_name}と接続されていません。接続後に呼び出しを行ってください。`
              );
            }

            const [audio_track] = this.local_audio_stream.getAudioTracks();
            sender.replaceTrack(audio_track);
          } else {
            sender.replaceTrack(null);
          }
        }
      }
    },
    local_video_stream() {
      this.answer_video_peer.change_stream(this.local_video_stream);

      const answer_video_peer_sender = this.answer_video_peer.get_sender();
      const offer_video_peer_sender = this.offer_video_peer.get_sender();
      const sender = answer_video_peer_sender
        ? answer_video_peer_sender
        : offer_video_peer_sender;

      if (this.local_video_stream && sender) {
        const [video_track] = this.local_video_stream.getVideoTracks();
        sender.replaceTrack(video_track);
      }
    },
    // 2021/12/01 by noriaki.nakamura
    // 映像共有ボタンOFF時に映像共有を停止させる。
    is_showed() {
      if (this.is_showed == false && this.is_group_in_local) {
        this.offer_video_peer && this.offer_video_peer.stop();
        this.show_video = false;
      }
    },
    remote_video_id() {
      console.log("切り替わったよ～");
    },
  },
  computed: {
    // is_loading() {
    //   if (this.show_video || this.show_audio) {
    //     return false;
    //   } else {
    //     return this.signalingState != "" || this.connectionState != "";
    //   }
    // },
    settings_height() {
      return this.remote_video_settings ? this.remote_video_settings.height : 0;
    },
    settings_frameRate() {
      const frameRate = this.remote_video_settings && this.remote_video_settings.frameRate;
      return !isNaN(frameRate) ? Math.floor(frameRate) : "-";
    },
    remote_video_id() {
      return this.$refs.remote_video && this.$refs.remote_video.srcObject
        ? this.$refs.remote_video.srcObject.getVideoTracks()[0].id
        : null;
    },
    is_offer_video_available() {
      return this.signalingState == "stable" || this.signalingState == "";
    },
    check_connection_period() {
      return this.$store.getters["check_connection_period"];
    },
    // 2021/12/01 by noriaki.nakamura
    // 映像共有ボタンOFF時に映像共有を停止させる。
    is_showed() {
      return this.$store.getters["rtc/remote_show"];
    },
    // renew通知にて、接続先がグループ通話可能か？を示す。
    is_group_in_remote() {
      return this.user && this.user.per == "group";
    },
    is_group_in_local() {
      return this.mode == "group";
    },
    is_private_in_local() {
      return this.mode == "private";
    },
    talk_mode() {
      return this.is_group_in_remote ? "" : "(個別)";
    },
    titleStyleObj() {
      return {
        backgroundColor: this.is_offer_video_available ? "#39a2c0" : "darkgray",
        Opacity: this.is_offer_video_available ? 1.0 : 0.7,
      };
    },
    downButtonSrc() {
      return require("./assets/etc_arrow_01@1x.png");

      // return this.watching
      //   ? require("./assets/etc_arrow_01@1x.png")
      //   : require("./assets/etc_arrow_01@1x.png");
    },
    out_level() {
      return this.$store.getters["rtc/out_level"];
    },
    at_send() {
      return this.$store.getters["ptt/pttActive"];
    },
    ptt_type() {
      return this.$store.state.ptt_type;
    },
    stun_server() {
      return this.$store.getters["stun_server"];
    },
    video_rate() {
      return this.$store.getters["video_rate"];
    },
    video_encode() {
      return this.$store.getters["video_encode"];
    },
    myId() {
      return this.$store.getters["amt/myId"];
    },
    users() {
      return this.$store.getters["amt/users"];
    },
    local_video_stream() {
      // return this.$store.getters["rtc/local_video_stream"];
      const stream = this.$store.getters["rtc/local_video_stream"];
      const [track] = stream.getVideoTracks();
      if (track) {
        return stream;
      } else {
        return this.dummy_video_staream;
      }
    },
    local_audio_stream() {
      return this.$store.getters["rtc/local_audio_stream"];
    },
    dummy_video_staream() {
      return this.$store.getters["rtc/dummy_video_stream"];
    },
    socketio() {
      return this.$store.getters["socketio"];
    },
    style_remote_audio() {
      return {
        position: this.show_video ? "absolute" : "relative",
        width: this.show_video ? "3rem" : "100%",
        height: this.show_video ? "3rem" : "100%",
      };
    },
    func_toggle_cam() {
      return this.$store.getters["rtc/func_toggle_cam"];
    },
  },
  methods: {
    on_track(ev) {
      console.log(`ev.track`);
      console.dir(ev.track);
      const clear_settings_id = () => {
        clearInterval(this.remote_settings_id);
        this.remote_settings_id = null;
      };
      if (this.remote_settings_id) clear_settings_id();
      ev.track.onended = clear_settings_id;
      this.remote_settings_id = setInterval(() => {
        if (this.$refs.remote_video && this.$refs.remote_video.srcObject) {
          const [track] = this.$refs.remote_video.srcObject.getVideoTracks();
          this.remote_video_settings = track.getSettings();
          // console.log(this.remote_video_settings.aspectRatio);
          let { aspectRatio, width, height } = this.remote_video_settings;
          if (aspectRatio === 2 || width / height === 2) {
            this.onC360play = true;
          } else {
            this.onC360play = false;
          }
        }
      }, 1000);
    },
    oncanplay() {
      this.is_loading = false;
      console.log("★★★★★★ CAN PLAY ★★★★★★★");
    },
    onTitleClick(ev) {
      ev.preventDefault();
      this.$store.state.sound_on();
      this.start_stop_video();
    },

    start_get_audio_level(p2p, id) {
      if (this.clearId[id]) {
        clearInterval(this.clearId[id]);
      }
      this.clearId[id] = setInterval(async () => {
        const stats = {};
        await p2p.get_stats(stats);

        // 音声レベル
        if (stats.inbound_rtp && stats.inbound_rtp.audioLevel) {
          this.audio_level = stats.inbound_rtp.audioLevel;

          if (this.audio_level > 0.0003) {
            if (!this.audio_on_once) {
              this.$store.commit("ptt/set_recv_active", true);
              const txt = `${
                this.users[this.remote_id].user_name
              }より着信しました`;
              this.$store.state.toast_on(txt);
            }
            this.audio_on_once = true;
          } else {
            if (this.audio_on_once && this.audio_level < 0.0001) {
              this.$store.commit("ptt/set_recv_active", false);
              this.audio_on_once = false;
            }
          }
        } else {
          if (this.audio_on_once) {
            this.$store.commit("ptt/set_recv_active", false);
            this.audio_on_once = false;
          }
        }

        // 遅延時間
        if (
          stats.remote_outbound_rtp &&
          stats.remote_outbound_rtp.remoteTimestamp
        ) {
          const remoteTimestamp = stats.remote_outbound_rtp.remoteTimestamp;

          if (remoteTimestamp != this.last_remoteTimestamp) {
            const sa = Date.now() - remoteTimestamp;
            console.log(`タイムスタンプが更新されました。${sa}`);
            if (sa > 2000) {
              this.$store.commit("ptt/set_rtt_over", true);
            } else {
              this.$store.commit("ptt/set_rtt_over", false);
            }
          }
          this.last_remoteTimestamp = remoteTimestamp;
        }

        // パケットロス
        if (stats.inbound_rtp && stats.inbound_rtp.packetsLost) {
          if (this.packetsLost != stats.inbound_rtp.packetsLost) {
            console.log(`packetLoss=${stats.inbound_rtp.packetsLost}`);
          }
          this.packetsLost = stats.inbound_rtp.packetsLost;
        }
      }, 200);
    },

    audio_on_off() {
      // audio_on_off_flagはチャタ防止で、一度触れると、1秒操作を防止する。
      if (this.audio_on_off_flag) return;
      this.audio_on_off_flag = true;

      const audio_peers = [this.offer_audio_peer, this.answer_audio_peer];
      let audio_sender = null;
      for (let peer of audio_peers) {
        if (!peer.peer) continue;
        const tr = peer.peer.getTransceivers()[0];
        if (tr.sender && tr.sender.track && tr.sender.track.kind == "audio") {
          audio_sender = tr.sender;
        }
      }
      if (audio_sender.track.enabled) {
        audio_sender.track.enabled = false;
        this.img_mic = require("@/assets/mic_off.png");
      } else {
        audio_sender.track.enabled = true;
        this.img_mic = require("@/assets/mic.png");
      }
      setTimeout(() => {
        this.audio_on_off_flag = false;
      }, 1000);
    },
    fullscreen() {
      this.$refs.remote_video.requestFullscreen();
    },
    start_stop_video() {
      if (!this.is_offer_video_available) return;

      if (this.show_video) {
        this.offer_video_peer.stop();
        this.show_video = false;
        this.is_loading = false;
        this.onC360play = false;
        // this.$emit("call_end", this.remote_id);
      } else {
        this.offer_video_peer.start();
        this.show_video = true;
        this.is_loading = true;
      }
    },
    start(kind) {
      this.is_loading = true;
      this.offer_audio_peer.start();
      this.show_audio = true;
      if (kind == "video") {
        this.offer_video_peer.start();
        this.show_video = true;
      }
    },
    stop() {
      this.show_video = false;
      this.show_audio = false;
      this.is_loading = false;
      // this.offer_video_peer.stop();
      // this.offer_audio_peer.stop();
      this.$emit("call_end", this.remote_id);
    },
    audio_restart() {
      this.offer_audio_peer.sv_start();
    },
    close() {
      this.$refs.remote_video.srcObject = null;
      this.$refs.remote_audio.srcObject = null;
      Object.keys(this.clearId).forEach((id) => {
        clearInterval(this.clearId[id]);
      });
      this.offer_video_peer.close();
      this.answer_video_peer.close();
      this.offer_audio_peer.close();
      this.answer_audio_peer.close();
      this.$emit("call_end", this.remote_id);
    },
  },
  beforeDestroy() {
    this.$store.commit("ptt/set_recv_active", false);
    this.$refs.remote_video.srcObject = null;
    this.$refs.remote_audio.srcObject = null;
    Object.keys(this.clearId).forEach((id) => {
      clearInterval(this.clearId[id]);
    });
    this.offer_video_peer.close();
    this.answer_video_peer.close();
    this.offer_audio_peer.close();
    this.answer_audio_peer.close();
  },
};
</script>

