/* eslint-disable */
import axios from "axios";

let componentId = 1;

export default {
  namespaced: true,
  state: {
    myPosition: true, //自位置アイコンのON/OFF
    layerComponents: [], //onになっているコンポーネント一覧
    position: {
      lat: null,
      lng: null,
      altitude: null,
      accuracy: null,
      altitudeAccuracy: null,
      heading: null,
      speed: null,
    },
    inSubMenu: null,
    showed_exit_dialog: false,
    layer: [],
    registedLayers: [],
    isSearchComponent: false,
    isSearchComponentList: false,
    isSearchComponentSmall: false,
    searchDevice: [],
    searchTargetDevice: [],
    searchTrack: [],
    searchMemo: [],
    searchLayer: [],
    isSearchSelectDevice: false,
    isSearchSelectLayer: false,
  },
  getters: {
    map(state, getters, rootState, rootGetters) {
      return rootState.map;
    },
    socketio(state, getters, rootState, rootGetters) {
      return rootState.socketio;
    },
    users(state, getters, rootState, rootGetters) {
      return rootState.users;
    },
    myId(state, getters, rootState, rootGetters) {
      return rootState.myId;
    },
    myPosition(state, getters, rootState, rootGetters) {
      return state.position;
    },
    patrolState(state, getters, rootState, rootGetters) {
      // 0:休憩中 1:巡回中 2:緊急対応中
      return rootState.workStatus;
    },
    subMenu(state, getters, rootState, rootGetters) {
      return state.inSubMenu;
    },
    exit_dialog(state, getters, rootState, rootGetters) {
      return state.showed_exit_dialog;
    },
    layers(state, getters, rootState, rootGetters) {
      return state.layer;
    },
    registedLayers(state, getters, rootState, rootGetters) {
      return state.registedLayers;
    },
    isSearchComponent(state, getters, rootState, rootGetters) {
      return state.isSearchComponent;
    },
    isSearchComponentList(state, getters, rootState, rootGetters) {
      return state.isSearchComponentList;
    },
    isSearchComponentSmall(state, getters, rootState, rootGetters) {
      return state.isSearchComponentSmall;
    },
    searchDevice(state, getters, rootState, rootGetters) {
      return state.searchDevice;
    },
    searchLayer(state, getters, rootState, rootGetters) {
      return state.searchLayer;
    },
    searchTrack(state, getters, rootState, rootGetters) {
      return state.searchTrack;
    },
    searchMemo(state, getters, rootState, rootGetters) {
      return state.searchMemo;
    },
    isSearchSelectDevice(state, getters, rootState, rootGetters) {
      return state.isSearchSelectDevice;
    },
    isSearchSelectLayer(state, getters, rootState, rootGetters) {
      return state.isSearchSelectLayer;
    },
    searchTargetDevice(state, getters, rootState, rootGetters) {
      return state.searchTargetDevice;
    },
  },
  actions: {
    async fetchTrack(context, { userId, begin, end }) {
      let res = [];
      const url = "/api/tracks/";
      const data = { userId, begin, end };
      try {
        const ctx = await axios.post(url, data);
        res = ctx.data;
      } catch (err) {
        console.log("fetchTrack error : " + err);
      }
      return res;
    },
    async fetchMemo(context, { userId, begin, end, text }) {
      let res = [];
      const url = "/api/memos/";
      const data = { userId, begin, end, text };
      try {
        const ctx = await axios.post(url, data);
        res = ctx.data;
      } catch (err) {
        console.log("fetchTrack error : " + err);
      }
      return res;
    },
    async refreshCredential(context) {
      const res = await axios.post("/api/refreshCredential");
      return res.data;
    },
    hideMyPosition({ commit, state }) {
      state.myPosition = false;
    },
    showMyPosition({ commit, state }) {
      state.myPosition = true;
    },
    setLayerVisible({ commit, state }, { layerName, visible }) {
      state.layer[layerName].layer.setVisible(visible);
    },
    setLayerOpacity({ commit, state }, { layerName, opacity }) {
      state.layer[layerName].layer.setOpacity(opacity);
    },
    setLayerZIndex({ commit, state }, { layerName, zIndex }) {
      state.layer[layerName].layer.setZIndex(zIndex);
    },
    setSearchDevice({ commit, state }, { device }) {
      state.searchDevice = device;
    },
    resetSearchDevice({ commit, state }) {
      state.searchDevice = [];
    },
    setSearchTrack({ commit, state }, { userId, track }) {
      let inputData = {
        userId: userId,
        track: track,
      };
      state.searchTrack.push(inputData);
    },
    setSearchLayer({ commit, state }, { layer }) {
      state.searchLayer = layer;
    },
    resetSearchLayer({ commit, state }) {
      state.searchLayer = [];
    },
    resetSearchTrack({ commit, state }) {
      state.searchTrack = [];
    },
    setSearchMemo({ commit, state }, { userId, memo }) {
      state.searchMemo = memo;
    },
    resetSearchMemo({ commit, state }) {
      state.searchMemo = [];
    },
    showSearchComponent({ commit, state }) {
      state.isSearchComponent = true;
    },
    hideSearchComponent({ commit, state }) {
      state.isSearchComponent = false;
    },
    showSearchComponentList({ commit, state }) {
      state.isSearchComponentList = true;
    },
    hideSearchComponentList({ commit, state }) {
      state.isSearchComponentList = false;
    },
    showSearchComponentDevice({ commit, state }) {
      state.isSearchSelectDevice = true;
    },
    hideSearchComponentDevice({ commit, state }) {
      state.isSearchSelectDevice = false;
    },
    showSearchComponentSmall({ commit, state }) {
      state.isSearchComponentSmall = true;
    },
    hideSearchComponentSmall({ commit, state }) {
      state.isSearchComponentSmall = false;
    },
    showSearchComponentLayer({ commit, state }) {
      state.isSearchSelectLayer = true;
    },
    hideSearchComponentLayer({ commit, state }) {
      state.isSearchSelectLayer = false;
    },
    // comfirme_exit({ commit, state }) {
    //   state.showed_exit_dialog = true;
    // }
  },
  mutations: {
    //positionのgettersを追加   // userLocaion.js　171行目
    position(state, position) {
      state.position = {
        lat: position.lat,
        lng: position.lng,
        altitude: position.altitude,
        accuracy: position.accuracy,
        altitudeAccuracy: position.altitudeAccuracy,
        heading: position.heading,
        speed: position.speed,
      };
    },
    // コンポーネントがlayerComponentsに含まれていなければ追加
    // 追加するたびにユニークなIDを振る
    addComponent(state, component) {
      if (state.layerComponents.every((c) => c.vue !== component)) {
        state.layerComponents.push({ id: componentId, vue: component });
        componentId++;
      }
    },
    // 一旦、コンポーネント削除に確認ダイアログを表示/非表示にする。
    comfirme_exit(state, val) {
      state.showed_exit_dialog = val;
    },
    // コンポーネントをlayerComponentsから除外
    removeComponent(state, component) {
      state.layerComponents = state.layerComponents.filter(
        (c) => c.vue !== component
      );
    },
    // コンポーネントがlayerComponentsに含まれていなければ追加
    // 既にある場合は除外
    toggleComponent(state, component) {
      if (state.layerComponents.every((c) => c.vue !== component)) {
        state.layerComponents.push({ id: componentId, vue: component });
        componentId++;
      } else {
        state.layerComponents = state.layerComponents.filter(
          (c) => c.vue !== component
        );
      }
    },
    setSubMenu(state, name) {
      state.inSubMenu = name;
    },
    registLayer(state, val) {
      state.layer[val.name] = val;
    },
    registedLayers(state, val) {
      state.registedLayers = val;
    },
    setSearchTargetDevice(state, val) {
      state.searchTargetDevice = val;
    },
  },
};
