<template>
  <div id="mapLayerMenu">
    <div>
      <!-- メニューボタン・戻るボタン 兼用 -->
      <button
        v-on:click="backBtnClick"
        class="circleBtn"
        v-bind:title="backButtonTitle"
      >
        <img v-bind:src="backButtonSrc" />
      </button>
    </div>
    <!-- 軌跡  -->
    <!-- <div v-show="showMapMenu && !inSubMenu"> -->
    <!-- 追加ルレイヤ  -->
    <!-- <template v-for="layer in layerModules"> -->
    <template v-for="layer in menus">
      <!-- <div v-show="showMapMenu && !inSubMenu" :key="layer.name"> -->
      <!-- グループ0（検証グループ）で表示されるメニューを分ける。 -->
      <div
        v-show="showMapMenu && !inSubMenu && layer.addMenu"
        :key="layer.name"
      >
        <button
          @click="activate(layer)"
          class="circleBtn"
          v-bind:title="layer.displayname"
        >
          <img :src="layer.icon" />
        </button>
      </div>
    </template>
  </div>
</template>

<script>
/* eslint-disable */
import layerModules from "@/modules/layers";
import ModalDialog from "@/components/ModalDialog.vue";
import { drawEvent } from "@/modules/DrawLayer/index.js";

//                = 1 2 3
// showMapMenu  : X o x x
// inSubMenu    : X x o x
// showWriteMenu: X x x o

// @todo 用語の整理
export default {
  data() {
    return {
      showMapMenu: false,
      showWriteMenu: false,
      // active_share: false,
      // show_share_menu: false,
      // inSubMenu: false,
      // show_exit_dialog: false,
      // current_subMenu: "",
      // confirme_subMenus: ["ShareLayer"]
    };
  },
  props: ["drawLineColor", "drawLineSize", "writeShape"],
  mounted() {},
  watch: {
    inSubMenu: function (in_menu) {
      if (!in_menu) {
        this.showWriteMenu = false;
        this.$emit("toggleWrite", false);
      } else if (in_menu == "ShareLayer") {
        drawEvent.$emit("SETACTIVE", false);
        this.showWriteMenu = false;
        this.$emit("toggleWrite", false);
      } else {
        this.showMapMenu = false;
      }
    },
    group_id: function (new_id, old_id) {
      if (new_id !== 0 && old_id === 0) {
        this.backBtnClick();
      }
    },
  },
  methods: {
    backBtnClick() {
      this.$store.state.sound_on();

      if (!this.inSubMenu && !this.showWriteMenu) {
        this.showMapMenu = !this.showMapMenu;
      } else if (this.showWriteMenu && !this.showMapMenu) {
        this.$store.commit("amt/setSubMenu", null);
        this.showWriteMenu = false;
        this.$emit("toggleWrite", false);
      } else if (this.inSubMenu == "ShareLayer") {
        this.$store.commit("amt/comfirme_exit", true);
      } else {
        // } else if (this.inSubMenu != null && !this.showWriteMenu && !this.showMapMenu) {
        const currentModule = this.layerModules.find(
          (m) => m.name == this.inSubMenu
        );
        if (currentModule && currentModule.comfirme) {
          this.$store.commit("amt/comfirme_exit", true);
        } else {
          currentModule.layer.setActive(false);
        }
        this.$store.commit("amt/setSubMenu", null);
      }
    },
    writeBtnClick() {
      this.$store.state.sound_on();
      // 機能選択メニューを非表示にして代わりに手書き機能のメニューを表示
      this.showWriteMenu = true;
      this.showMapMenu = false;
      this.$store.commit("amt/setSubMenu", "WriteLayer");
      // this.inSubMenu = true;
      // this.$store.commit("amt/setSubMenu", true);
      // 手書きモード開始のイベントを発火
      this.$emit("toggleWrite", true);
    },
    backFromWriteBtnClick() {
      // 手書き機能のメニューを非表示にして機能選択メニューを表示
      this.showWriteMenu = !this.showWriteMenu;
      // 手書きモード開始のイベントを発火
      this.$emit("toggleWrite", false);
    },
    activate(item) {
      const layer = item.layer;
      layer.setActive(true);
      // this.inSubMenu = true;
      // this.$store.commit("amt/setSubMenu", true);
      // this.current_subMenu = item.name;
    },
  },
  computed: {
    inSubMenu() {
      return this.$store.getters["amt/subMenu"];
    },
    layerModules() {
      return layerModules;
    },
    menus() {
      return layerModules.filter((layer) => {
        return this.group_id === 0 ? layer.group0Menu : !layer.group0Menu;
      });
    },
    backButtonSrc() {
      return !this.showMapMenu && !this.inSubMenu && !this.showWriteMenu
        ? require("@/assets/NEW/menu@1x.png")
        : require("@/assets/menu_return@1x.png");
    },
    backButtonTitle() {
      return !this.showMapMenu && !this.inSubMenu && !this.showWriteMenu
        ? "メニュー"
        : "閉じる";
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
  },
};
</script>

