<template>
  <div
    v-bind:class="{ layer_box: !draggingNow, layer_box_select: draggingNow }"
  >
    <!-- デッドスペースの空白(ドラッグ可) --><!-- 2022-01-18ドラッグできないように 堀山 -->

    <div class="layer_space"></div>

    <!-- レイヤアイコン(ドラッグ可) --><!-- 2022-01-18ドラッグできないように 堀山 -->
    <div class="layer_icon">
      <img v-bind:src="icon" class="layer_icon_img" alt="" />
    </div>

    <!-- レイヤ名(ドラッグ可) --><!-- 2022-01-18ドラッグできないように 堀山 -->
    <div class="layer_title">
      {{ displayname }}
    </div>

    <!-- 表示/非表示トグルボタン -->
    <div class="layer_togglebutton" v-on:click="toggle_switch()">
      <template v-if="visible === false">
        <img src="./assets/select_no.png" alt="" />
      </template>
      <template v-else-if="visible === true">
        <img src="./assets/select_yes.png" alt="" />
      </template>
    </div>

    <!-- 透過率スライドバー -->
    <div class="layer_slidebar">
      <input
        class="layer_slider"
        type="range"
        min="0"
        max="1.0"
        step="0.01"
        v-model="layer_rotate"
        @change="onRangeChange"
        v-bind:title="layer_rotate"
      />
    </div>

    <!-- ドラッグ選択用ハンバーガーアイコン -->
    <div class="layer_humberger layer_draggable">
      <img src="./assets/menu10.png" alt="" />
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import moment from "moment";

export default {
  data() {
    return {
      displayname: this.layer.displayname,
      layerName: this.layer.name,
      visible: this.layer.visible,
      layer_rotate: this.layer.opacity,
      icon: this.layer.icon,
      onDragging: this.dragging,
    };
  },
  props: {
    // ManageLayer.vueのregistedLayersを参照
    layer: Object,
    // ドラッグ中の状態を参照
    dragging: String,
  },
  methods: {
    toggle_switch() {
      // store経由でlayerに変更を要求する
      let isVisible = this.visible;
      // 表示状態の格納
      this.layer.visible = !isVisible;
      // 状態のアップデート
      this.$store.dispatch("amt/setLayerVisible", {
        layerName: this.layer.name,
        visible: !isVisible,
      });
      // ブラウザ上の表示を変更
      this.visible = !isVisible;
    },
    onRangeChange() {
      // store経由でlayerに変更を要求する
      let opacity = this.layer_rotate;
      // 透過率の格納
      this.layer.opacity = opacity;
      // 透過状態アップデート
      this.$store.dispatch("amt/setLayerOpacity", {
        layerName: this.layer.name,
        opacity: opacity,
      });
    },
  },
  computed: {
    draggingNow() {
      return this.dragging == this.layerName;
    },
  },
};
</script>

<style>
/* styleは全てManageLayer.vueに記載 */
</style>
