    mapCenter() {
      let myCoord = null;
      try {
        const myData = this.$store.state.users[this.$store.state.myId];
        if (myData.lng != null && myData.lat != null) {
          myCoord = ol.proj.transform(
            [myData.lng, myData.lat],
            "EPSG:4326",
            "EPSG:3857"
          );
        }
      } catch (e) {
        myCoord = null;
      }
      if (myCoord == null) {
        myCoord = ol.proj.transform(
          this.mapCenterCoord,
          "EPSG:4326",
          "EPSG:3857"
        );
      }
      return myCoord;
    },
    toCenter() {
      this.$store.state.map.getView().setCenter(this.mapCenter());
      this.$store.commit("setCenterTrace", Date.now());
    },
    toCenterTrial() {
      const myData = this.$store.state.users[this.$store.state.myId];
      if (myData != null && myData.lng != null && myData.lat != null) {
        this.toCenter();
      } else {
        setTimeout(this.toCenterTrial, 500);
      }
    },
    showMap() {
      // メインレイヤー
      const url = this.$store.state.tiles_url;
      this.mainLayer = new ol.layer.Tile({
        source: new ol.source.OSM({ url }),
        // source: new ol.source.OSM(),
        opacity: 1,
        visible: true,
      });

      // viewを生成する
      this.mapView = new ol.View({
        projection: "EPSG:3857",
        center: this.mapCenter(),
        zoom: this.zoomVal,
      });

      // mapを生成する
      const map = new ol.Map({
        target: "mapField",
        layers: [this.mainLayer],
        view: this.mapView,
        controls: olControl.defaults({
          zoom: true,
          attribution: true,
          attributionOptions: {
            collapsible: true,
            left: "30px",
          },
          rotate: false,
        }),
        interactions: ol.interaction.defaults({
          altShiftDragRotate: false,
          pinchRotate: false,
        }),
      });
      this.$store.commit("setMap", map);

      this.toCenterTrial();
    },
