    // layers.jsに登録されたレイヤをマップに追加
    layerModules.forEach((m) => {
      m.layer.add();
      this.$store.commit("amt/registLayer", m);
      // layer.jsの初期値設定
      if (undefined !== m.visible) {
        // 表示状態
        m.layer.setVisible(m.visible);
      }
      if (undefined !== m.opacity) {
        // 透過率
        m.layer.setOpacity(m.opacity);
      }
      if (undefined !== m.zIndex) {
        // zIndex
        m.layer.setZIndex(m.zIndex);
      }
