      // クリックイベントのコールバックは登録順に実行されるので最後のコールバックとして登録
      const click_targets = this.$store.state.click_targets;
      // click_targetは配列なのでlength=1の場合、対象1件のみと判定
      if (1 == click_targets.length) {
