export default {
  namespaced: true,
  state: {
    local_stream: null,
    local_audio_stream: null, // MIC input
    remote_audio_stream: null, // Speacker output
    local_video_stream: null,
    remote_video_stream: null,
    dummy_video_stream: null,
    remote_show: false,
    local_show: false,
    personal_id: null,
    is_front_cam: null, // TRUE as VIDEO facingMode is FRONT
    func_toggle_cam: null,
    func_mic_on_off: null,
    out_level: null,
  },
  getters: {
    local_stream(state) {
      return state.local_stream;
    },
    local_audio_stream(state) {
      return state.local_audio_stream;
    },
    local_audio_track(state) {
      return state.local_audio_stream
        ? state.local_audio_stream.getAudioTracks()[0]
        : null;
    },
    remote_audio_stream(state) {
      return state.remote_audio_stream;
    },
    local_video_stream(state) {
      return state.local_video_stream;
    },
    local_video_track(state) {
      return state.local_video_stream
        ? state.local_video_stream.getVideoTracks()[0]
        : null;
    },
    remote_video_stream(state) {
      return state.remote_video_stream;
    },
    dummy_video_stream(state) {
      return state.dummy_video_stream;
    },
    remote_show(state) {
      return state.remote_show;
    },
    local_show(state) {
      return state.local_show;
    },
    personal_id(state) {
      return state.personal_id;
    },
    is_front_cam(state) {
      return state.is_front_cam;
    },
    func_toggle_cam(state) {
      return state.func_toggle_cam;
    },
    func_mic_on_off(state) {
      return state.func_mic_on_off;
    },
    out_level(state) {
      return state.out_level;
    },
  },
  mutations: {
    set_local_stream(state, data) {
      state.local_stream = data;
    },
    set_local_audio_stream(state, data) {
      state.local_audio_stream = data;
    },
    set_remote_audio_stream(state, data) {
      state.remote_audio_stream = data;
    },
    set_local_video_stream(state, data) {
      state.local_video_stream = data;
      if (state.local_video_stream) {
        const track = state.local_video_stream.getVideoTracks()[0];
        // const video_constraint = track.getConstraints();
        const video_constraint = track ? track.getSettings() : null;
        if (video_constraint && video_constraint.facingMode == "user") {
          state.is_front_cam = true;
        } else {
          state.is_front_cam = false;
        }
      } else {
        state.is_front_cam = null;
      }
    },
    set_dummy_video_stream(state, data) {
      state.dummy_video_stream = data;
    },
    set_remote_video_stream(state, data) {
      state.remote_video_stream = data;
    },
    set_remote_show(state, data) {
      state.remote_show = data;
    },
    set_local_show(state, data) {
      state.local_show = data;
    },
    set_personal_id(state, data) {
      state.personal_id = data;
    },
    set_func_toggole_cam(state, data) {
      state.func_toggle_cam = data;
    },
    set_func_mic_on_off(state, data) {
      state.func_mic_on_off = data;
    },
    set_out_level(state, data) {
      state.out_level = data;
    },
  },
};
