<template>
  <div>
    <div v-if="!is_me" class="name">{{ name }}</div>
    <div class="talk_box" :class="{'reverse': is_me, 'nack': nack }">
      <div :class="[ is_me ? 'say_Right' : 'say_Left' ]">
        <template v-if="href"><a @click="sound_on(href)">{{ msg }}</a></template>
        <template v-else><p>{{ msg }}</p></template>
      </div>
      <div class="c_time">{{ c_time }}</div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  props: [ "msg", "id", "name", "time", "nack", "href" ],
  computed: {
    myId() {
      return this.$store.state.myId
    },
    is_me() {
      return this.myId === this.id
    },
    users() {
      return this.$store.getters["amt/users"]
    },
    c_time() {
      const d = new Date(this.time);
      return this.nack ?  "送信中" : `${d.getHours()}:${d.getMinutes()}`
    },
    sound_on() {
      return this.$store.state.sound_on;
    },
  }
}
</script>

<style>
.talk_box {
  display: flex;
  align-items: flex-end;
}
.reverse {
  flex-direction: row-reverse;
}
.nack {
  opacity: 50%;
}
.c_time {
  font-size: 0.7rem;
  width: 40px;
}
.name {
  text-align: left;
  padding: 10px 10px 0px 10px;
  font-size: 0.7rem;
}
.say_Left {
  display: flex;
  text-align: left;
  max-width: 80%;
  min-width: 50px;
  width: fit-content;
  overflow-wrap: break-word;
  position: relative;
  margin: 0px 5px 0px 25px;
  padding: 10px 15px 8px 15px;
  border-radius: 12px;
  background: #d7ebfe;
}
.say_Left:after {
  content: "";
  display: inline-block;
  position: absolute;
  /* top: 50%; */
  left: -30px;
  /* margin-top: -15px; */
  border: 10px solid transparent;
  border-right: 22px solid #d7ebfe;
}
.say_Right {
  display: flex;
  text-align: left;
  max-width: 80%;
  min-width: 50px;
  width: fit-content;
  overflow-wrap: break-word;
  position: relative;
  margin: 5px 30px 5px 5px;
  padding: 10px 15px 8px 15px;
  /* padding: 17px 13px 15px 18px; */
  border-radius: 12px;
  background: #5AC323;
  color: white;
}
.say_Right::after {
  content: "";
  display: inline-block;
  position: absolute;
  left: 100%;
  border: 10px solid transparent;
  border-left: 22px solid #5AC323;
}
p {
  margin: 0 !important;
  padding: 0;
}
</style>