<template>
  <div width="500px">
    <div class="waiting_text">ログイン中です</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      challenge: "",
      privateKey: null,
      userId: null,
    };
  },
  async mounted() {
    await this.init();
  },
  methods: {
    async init() {
      console.log("Login: init start");
      try {
        // 秘密鍵の確認
        this.privateKey = await this.ldb.getItem("private_key");
        // IDの確認
        this.userId = await this.ldb.getItem("userId");

        console.log("privateKey = ");
        console.log(this.privateKey);
        console.log(`userId = ${this.userId}`);

        if (!this.privateKey || !this.userId) {
          await this.ldb.clear();
          // this.$router.push("/Regist/input");
          this.$router.push("/Regist");
        } else {
          await this.get_challenge();
          await this.post();
        }
      } catch (err) {
        console.log(err);
      }
      console.log("Login: init end");
    },
    async get_challenge() {
      // challengeの取得
      try {
        const res = await fetch("/api/req_login");
        if (res.ok) {
          const res_json = await res.json();
          this.challenge = res_json.challenge;
          console.log(`challenge = ${this.challenge}`);
          return res_json;
        }
      } catch (err) {
        console.log(err);
        return null;
      }
    },
    async post() {
      // 署名の作成
      const signature = await this.sign(this.privateKey, this.challenge);
      try {
        const res = await fetch("/api/req_auth", {
          method: "post",
          catch: "no-cache",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            id: this.userId,
            challenge: this.challenge,
            sign: signature,
          }),
        });
        if (res.ok) {
          const res_json = await res.json();
          console.log(res_json);
          if (
            res_json.message == "おめでとうございます。署名が検証出来ました。"
          ) {
            // this.$store.dispatch("auth_fetch", this.userId);
            this.$store.commit("setAuth", this.userId);

            console.log("router : ROOTに行きます。");
            if (res_json.path === "/review") {
              this.$store.commit("setReview", true);
            }
            this.$router.push("/");
          } else {
            console.log("router : Registに戻ります。");
            await this.ldb.clear();
            this.$router.push("/Regist?state=error");
          }
        }
      } catch (error) {
        console.log(error);
      }
    },
    ab2str(buf) {
      return String.fromCharCode.apply(null, new Uint8Array(buf));
    },
    async sign(key, data) {
      const sign_arraybuff = await crypto.subtle.sign(
        "RSASSA-PKCS1-v1_5",
        key,
        new TextEncoder().encode(data)
      );
      const sign_str = this.ab2str(sign_arraybuff);
      return window.btoa(sign_str);
    },
  },
};
</script>
<style scoped>
.waiting_text {
  position: absolute;
  color: white;
  font-size: 1.5rem;
  top: 45vh;
  width: 100%;
  text-align: center;
}
</style>
