<template>
  <div id="levelField">
    <div>
      <svg
        x="0"
        y="0"
        width="100%"
        height="100%"
        style="background-color: rgba(255, 255, 255, 0.05)"
        viewBox="0 0 100 3"
        preserveAspectRatio="none"
      >
        <defs>
          <linearGradient
            gradientUnits="userSpaceOnUse"
            id="gradation0"
            x1="0"
            y1="0"
            x2="100"
            y2="0"
            spreadMethod="pad"
          >
            <stop offset="0%" stop-color="#afc6e8" />
            <stop offset="100%" stop-color="#2e5bcc" />
          </linearGradient>
        </defs>
        <rect
          x="0"
          y="0"
          v-bind:width="in_level"
          height="3"
          fill="url(#gradation0)"
        />
      </svg>
      <span>MIC</span>
    </div>
    <div>
      <svg
        x="0"
        y="0"
        width="100%"
        height="100%"
        style="background-color: rgba(255, 255, 255, 0.13)"
        viewBox="0 0 100 3"
        preserveAspectRatio="none"
      >
        <linearGradient
          gradientUnits="userSpaceOnUse"
          id="gradation1"
          x1="0"
          y1="0"
          x2="100"
          y2="0"
          spreadMethod="pad"
        >
          <stop offset="0%" stop-color="#B7C7E9" />
          <stop offset="100%" stop-color="#22b2cc" />
        </linearGradient>
        <rect
          x="0"
          y="0"
          v-bind:width="out_level"
          height="3"
          fill="url(#gradation1)"
        />
      </svg>
      <span>SP</span>
    </div>
  </div>
</template>

<script>
const AudioContext = window.AudioContext || window.webkitAudioContext;
export default {
  data() {
    return {
      in_level: 0,
      // out_level: 0,
      audio_ctx: null,
      clearId: {},
    };
  },
  computed: {
    local_audio_stream() {
      return this.$store.getters["rtc/local_audio_stream"];
    },
    remote_audio_stream() {
      return this.$store.getters["rtc/remote_audio_stream"];
    },
    out_level() {
      return this.$store.getters["rtc/out_level"];
    },
  },
  watch: {
    // 送信音声ストリームの変化を検出して、再初期化を行う。
    local_audio_stream() {
      if (this.local_audio_stream) {
        this.in_init(this.local_audio_stream);
        this.clearId["get_in_level"] = setInterval(() => {
          this.get_in_level();
        }, 200);
      } else {
        clearInterval(this.clearId["get_in_level"]);
      }
    },
    // 送信音声ストリームの変化を検出して、再初期化を行う。
    remote_audio_stream() {
      if (this.remote_audio_stream) {
        this.out_init(this.remote_audio_stream);
        this.clearId["get_out_level"] = setInterval(() => {
          this.get_out_level();
        }, 200);
      } else {
        this.$store.commit("rtc/set_out_level", 0);
        clearInterval(this.clearId["get_out_level"]);
      }
    },
  },
  methods: {
    in_init(stream) {
      // const AudioContext = window.AudioContext || window.webkitAudioContext;
      if (stream.active == false) return;

      this.audio_ctx = new AudioContext();
      const input = this.audio_ctx.createMediaStreamSource(stream);
      this.in_analyser = this.audio_ctx.createAnalyser();
      input.connect(this.in_analyser);
    },
    out_init(stream) {
      if (stream.active == false) return;

      const input = this.audio_ctx.createMediaStreamSource(stream);
      this.out_analyser = this.audio_ctx.createAnalyser();
      input.connect(this.out_analyser);
    },
    get_in_level() {
      const times = new Uint8Array(this.in_analyser.fftSize);
      this.in_analyser.getByteTimeDomainData(times);
      // どうも、正側の最大値のみを拾って、0～1間で正規化している様子。
      this.in_level =
        ((times.reduce((acc, val) => (val > acc ? val : acc), 128) - 128) /
          127) *
        100;
    },
    get_out_level() {
      const times = new Uint8Array(this.out_analyser.fftSize);
      this.out_analyser.getByteTimeDomainData(times);
      // どうも、正側の最大値のみを拾って、0～1間で正規化している様子。
      const out_level =
        ((times.reduce((acc, val) => (val > acc ? val : acc), 128) - 128) /
          127) *
        100;
      this.$store.commit("rtc/set_out_level", out_level);
    },
  },
  beforeDestroy() {
    Object.keys(this.clearId).forEach((key) => {
      clearInterval(this.clearId[key]);
    });
  },
};
</script>

<style>
div#levelField {
  position: absolute;
  left: 0;
  top: 45vh;
  width: 86%;
  margin-left: 7%;
  margin-right: 7%;
  height: 1.2rem;
  font-size: 0.5rem;
}
div#levelField div {
  position: relative;
  width: 100%;
  height: 0.6rem;
}
div#levelField span {
  position: absolute;
  top: 0;
  left: 0;
  color: white;
}
</style>
