<template>
  <div :style="styleObj" ref="videoUnit">
    <div
      ref="videoContainer"
      class="videoContainer"
      :style="videoStyleObj"
      @mousedown="pre_mdown"
      @mousemove="mmove"
      @mouseup="mup"
      @mouseleave="mup"
      @touchstart="pre_mdown"
      @touchmove="mmove"
      @touchend="mup"
    >
      <slot></slot>
    </div>
    <div
      ref="resize_grip"
      class="resize_grip"
      :style="gripStyleObj"
      @mousedown.prevent="rdown"
      @mousemove.prevent="rmove"
      @mouseup.prevent="rup"
      @touchstart.prevent="rdown"
      @touchmove.prevent="rmove"
      @touchend.prevent="rup"
    ></div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  data() {
    return {
      move_flag: false,
      move_once: false,
      resize_flag: false,
      top: 0,
      left: 0,
      init_pos: { top: 0, left: 0 },
      last_pos: {},
      watching: false, // 映像表示を行っているか、いないか。
    };
  },
  props: ["is_showed"],
  mounted() {
    document.body.addEventListener("mousemove", this.rmove, false);
    document.body.addEventListener("mouseup", this.rup, false);
    document.body.addEventListener("touchend", this.rup, false);
  },
  methods: {
    pre_mdown(ev) {
      ev.stopPropagation();
      if ("getContext" in ev.target) return;

      this.$refs.videoUnit.addEventListener("mouseup", this.pre_mup, false);
      this.$refs.videoUnit.addEventListener("touchend", this.pre_mup, false);
      this.clearId = setTimeout(() => {
        this.mdown(ev);
        this.$refs.videoUnit.removeEventListener(
          "mouseup",
          this.pre_mup,
          false
        );
        this.$refs.videoUnit.removeEventListener(
          "touchend",
          this.pre_mup,
          false
        );
      }, 200);
    },
    pre_mup(ev) {
      clearTimeout(this.clearId);
      this.$refs.videoUnit.removeEventListener("mouseup", this.pre_mup, false);
      this.$refs.videoUnit.removeEventListener("touchend", this.pre_mup, false);
    },
    mdown(ev) {
      const _ev = ev.touches ? ev.touches[0] : ev;
      this.$store.state.sound_on();

      if (!this.move_once) {
        this.init_width = this.$refs.videoUnit.clientHeight;
      }
      this.move_flag = true;
      this.move_once = true;
      this.top = this.$refs.videoUnit.offsetTop;
      this.left = this.$refs.videoUnit.offsetLeft;
      this.width = this.$refs.videoUnit.clientWidth;
      this.last_pos = { x: _ev.clientX, y: _ev.clientY };

      this.$emit("zIndex_up", this);
    },
    mmove(ev) {
      if (this.move_flag) {
        const _ev = ev.touches ? ev.touches[0] : ev;
        this.top = this.top + (_ev.clientY - this.last_pos.y);
        this.left = this.left + (_ev.clientX - this.last_pos.x);
        this.$refs.videoUnit.style.width = this.width + "px";
        this.last_pos = { x: _ev.clientX, y: _ev.clientY };
      }
    },
    mup(ev) {
      this.move_flag = false;
    },
    rdown(ev) {
      const _ev = ev.touches ? ev.touches[0] : ev;
      this.resize_flag = true;
      this.width = this.$refs.videoUnit.clientWidth;
      this.last_pos = { x: _ev.clientX, y: _ev.clientY };
    },
    rmove(ev) {
      if (this.resize_flag) {
        const _ev = ev.touches ? ev.touches[0] : ev;
        this.width = this.width + (_ev.clientX - this.last_pos.x);
        this.$refs.videoUnit.style.width = this.width + "px";
        this.last_pos = { x: _ev.clientX, y: _ev.clientY };
      }
    },
    rup(ev) {
      this.resize_flag = false;
    },
  },
  watch: {
    is_showed() {
      if (!this.is_showed) {
        this.move_once = false;
        this.top = this.init_pos.top;
        this.left = this.init_pos.left;
        this.width = "100%";
      }
    },
  },
  computed: {
    styleObj() {
      return {
        position: this.move_once ? "absolute" : "relative",
        borderRadius: this.move_once ? "10px" : "0px",
        top: this.top + "px",
        left: this.left + "px",
        width: this.width == "100%" ? "100%" : this.width + "px",
        Opacity: this.move_flag ? 0.5 : 1,
        border: this.move_flag ? "4px solid #f00" : "0px",
        minWidth: "150px",
        // backgroundColor: "#37a3c0",
        zIndex: this.focus_flag ? 1000 : 100,
        backgroundColor: this.focus_flag ? "#ffa2c0" : "#38a2c0",
        overflow: "hidden",
      };
    },
    videoStyleObj() {
      return {
        width: "100%",
      };
    },
    gripStyleObj() {
      return {
        borderBottomColor: this.resize_flag ? "#c03838" : "#37a3c0",
      };
    },
  },
  beforeDestroy() {
    document.body.removeEventListener("mousemove", this.rmove, false);
    document.body.removeEventListener("mouseup", this.rup, false);
    document.body.removeEventListener("touchend", this.rup, false);
  },
};
</script>

<style>
.videoContainer {
  /* width: 100%; */
  min-width: 150px;
  position: relative;
}
.videoContainer video {
  display: block;
  height: auto;
  width: 100%;
  /* object-fit: cover; */
}

.resize_grip {
  width: 0;
  height: 0;
  bottom: 0px;
  right: 0px;
  border-left: 30px solid transparent;
  border-bottom: 30px solid #37a3c0;
  position: absolute;
  text-align: center;
  z-index: 1000;
}
.gripped {
  border-bottom-color: #c03838;
}
</style>
