<template>
  <div ref="amtHeader" class="cHeader">
    <div id="headerLeft" class="cHeaderLeft">
      <button class="hiddenBtn" v-on:click="onBackLogin" title="ログアウト">
        <img v-bind:src="leftButtonSrc" class="headerPng" />
      </button>
    </div>
    <div id="headerCenter" class="cHeaderCenter">
      <div style="display: flex; align-items: center">
        <div @click="my_video_show" style="width: fit-content">
          <img
            ref="icon_arrow"
            src="@/assets/etc_arrow_01@1x.png"
            class="rotate90"
            title="カメラ映像"
          />
        </div>
        <div class="nosel text_over" @click="my_memo_classify">
          {{ headerName }}
          <div class="classify_disp_name text_over" v-if="isLandscape">
            {{ classify_disp_name }}
          </div>
        </div>
      </div>
      <GroupSelect v-if="!isLandscape"></GroupSelect>
    </div>
    <div id="headerRight" class="cHeaderRight">
      <button class="hiddenBtn" title="端末映像共有">
        <img
          src="@/assets/map_img@1x.png"
          class="headerPng"
          v-on:click="onVideoButton"
        />
      </button>
      <div
        class="users_length"
        v-on:click="onVideoButton"
        v-show="users_length > 0"
      >
        {{ users_length }}
      </div>
    </div>
    <workList v-if="workListShowing"></workList>

    <modalDialog v-if="show_msg_dialog">
      <span slot="header">メモの分類</span>
      <span slot="body">
        作成するメモの分類を変更します
        <!-- <classifyList>
          <b-form-select
            v-model="classify_name"
            :options="classifyListObj"
          ></b-form-select>
        </classifyList> -->
        <div>
          <b-form-select
            v-model="classify_name"
            :options="classifyListObj"
          ></b-form-select>
        </div>
      </span>
      <div slot="footer">
        <button class="btn btn-primary" @click="close_msg_dialog()">
          戻る
        </button>
        <button class="btn btn-primary" @click="ok_classify_memo()">
          変更
        </button>
      </div>
    </modalDialog>
  </div>
</template>

<script>
/* eslint-disable */
import WorkList from "./WorkList.vue";
import GroupSelect from "../components/GroupSelect";
import ModalDialog from "@/components/ModalDialog.vue";
export default {
  data() {
    return {
      // headerName: this.$store.state.myCompanyName,
      headerName: this.$store.state.myName,
      workListShowing: false,
      show_msg_dialog: false,
      classify_name: "",
      classify_userAgent: "",
    };
  },
  // props: ["my_video_showed"],
  components: {
    workList: WorkList,
    GroupSelect: GroupSelect,
    modalDialog: ModalDialog,
  },
  watch: {
    // my_video_showed() {
    //   if (this.my_video_showed) {
    //     this.$refs.icon_arrow.classList.remove("rotate90");
    //     this.$store.state.p2p.start_localVideoElm();
    //   } else {
    //     this.$refs.icon_arrow.classList.add("rotate90");
    //     this.$store.state.p2p.stop_localVideoElm();
    //   }
    // },
    rtt_alert() {
      if (this.rtt_alert) {
        this.$refs.amtHeader.classList.add("blinking");
      } else {
        this.$refs.amtHeader.classList.remove("blinking");
      }
    },
  },
  methods: {
    my_video_show() {
      this.$store.state.sound_on();
      // this.$emit("my_video_show");
      // const toggle = this.$store.getters["rtc/func_toggle_cam"];
      // toggle();
      if (this.is_local_video_showed) {
        this.$refs.icon_arrow.classList.add("rotate90");
        this.$store.commit("rtc/set_local_show", false);
      } else {
        this.$refs.icon_arrow.classList.remove("rotate90");
        this.$store.commit("rtc/set_local_show", true);
      }
    },
    async my_memo_classify() {
      this.$store.state.sound_on();
      // 初期値を持たせる
      this.classify_name = this.$store.state.memo_classify;
      await this.$store.dispatch("memo/fetchClassify");
      this.show_msg_dialog = true;
    },
    ok_classify_memo() {
      this.$store.state.sound_on();
      this.$store.commit("setMemoClassify", this.classify_name);
      this.show_msg_dialog = false;
    },
    close_msg_dialog() {
      this.$store.state.sound_on();
      this.show_msg_dialog = false;
    },
    onBackLogin() {
      this.$store.state.sound_on();
      if (this.$store.state.mapWindowLock) return;
      // 確認ダイアログ表示
      this.$store.commit("confirmOn", {
        body: "ログアウトします。よろしいですか？",
        // Yes => ログアウトする
        funcYes: async () => {
          // location.href = "/logout";
          if (this.$store.state.renew_timerId) {
            console.log(
              `-------CLEAR TIMER (RENEW ID:${this.$store.state.renew_timerId})-------`
            );
            clearInterval(this.$store.state.renew_timerId);
            this.$store.state.socketio.disconnect();
          }
          await this.ldb.clear();
          this.$store.commit("confirmOff");
          this.$store.commit("setReview", false);
          // this.$router.push("/Regist");
          this.$router.go({
            path: this.$router.currentRoute.path,
            force: true,
          });
        },
        // No => ダイアログを閉じる
        funcNo: () => {
          this.$store.commit("confirmOff");
        },
      });
    },
    onVideoButton() {
      this.$store.state.sound_on();
      if (this.$store.state.mapWindowLock) return;
      // this.$store.state.mapChangeEvent.$emit("togglePeepDisp");
      if (this.$store.getters["rtc/remote_show"]) {
        this.$store.commit("rtc/set_remote_show", false);
      } else {
        this.$store.commit("rtc/set_remote_show", true);
      }
    },
    onWorkStatusButton() {
      const on = !this.workListShowing;
      this.workListShowing = on;
      const el = document.getElementById("workStatusButton");
      if (on) {
        el.style["z-index"] = 11;
      } else {
        el.style["z-index"] = "";
      }
      this.$store.commit("setMask", on);
    },
  },
  computed: {
    isLandscape() {
      return this.$store.state.screen_landscape;
    },
    leftButtonSrc() {
      return require("../assets/logout10.png");
    },
    flagSrc() {
      switch (this.$store.state.workStatus) {
        case 2:
          return require("../assets/flagRed10.png");
        case 3:
          return require("../assets/flagYellow10.png");
        default:
          return require("../assets/flag10.png");
      }
    },
    users() {
      return this.$store.getters["amt/users"];
    },
    users_length() {
      return this.$store.getters["users_length"];
    },
    // 2021.04.07 user_typeによる判定を追加
    // users_length() {
    //   const same_group_members = Object.keys(this.users).filter(id => {
    //     return (
    //       this.users[id].group_id == this.$store.state.activeId.id &&
    //       this.users[id].user_type == "amt"
    //     );
    //   });
    //   return Object.keys(same_group_members).length - 1;
    // },
    classifyListObj() {
      // オブジェクトの形に変換
      let options = [{ value: "", text: "" }]; // 空(全選択)を行えるように
      let obj = {};
      this.$store.state.memo.classify_list.forEach((data, i) => {
        if (data) {
          obj = { value: data, text: data };
          options.push(obj);
        }
      });
      return options;
    },
    rtt_alert() {
      return this.$store.getters["ptt/rtt_alert"];
    },
    classify_disp_name() {
      if (this.$store.state.memo_classify) {
        return "分類 : " + this.$store.state.memo_classify;
      } else {
        // ローディング時に分類が未選択だとundefinedになる為
        return "分類 : ";
      }
    },
    is_local_video_showed() {
      return this.$store.getters["rtc/local_show"];
    },
  },
  mounted() {
    this.$store.state.mapChangeEvent.$on("closeWorkingListAndLock", () => {
      this.workListShowing = false;
      //this.$parent.isModal = false;
      //this.$store.commit("setMask", false);
      this.$store.commit("setMapWindowLock", true);
    });
    // userAgentから端末を判断する。Androidかその他で判別
    var ua = navigator.userAgent;
    if (ua.indexOf("Android") > 0) {
      this.classify_userAgent = "Android";
    } else {
      this.classify_userAgent = "Win64";
    }
  },
  beforeDestroy() {
    this.$store.state.mapChangeEvent.$off("closeWorkingListAndLock");
  },
};
</script>

<style>
