  flex-basis: 70%;
  display: flex;
  align-items: baseline;
  align-items: center;
  font-size: 1.5rem;
  color: white;
}
div.cHeaderCenter div {
  width: 100%;
  text-align: center;
}
div.cHeaderRight {
  flex-basis: 15%;
  display: flex;
  align-items: center;
}
img.headerPng {
  width: 1.8rem;
  height: 1.8rem;
}
div.cHeaderLeft button {
  margin-left: 2rem;
  padding: 0px;
}
div#headerRight button:first-child {
  margin-left: auto;
  margin-right: 0.5rem;
  padding: 0px;
}
div#headerRight button:nth-child(2) {
  margin-left: 0;
  margin-right: 1rem;
}
div#headerRight button:nth-child(3) {
  margin-left: 0;
  margin-right: 1rem;
}
button.hiddenBtn {
  width: 2.5rem;
  height: 2.5rem;
  background: rgba(42, 121, 142, 0);
  border: none;
  border-radius: 3rem;
  -moz-border-radius: 3rem;
  -webkit-border-radius: 3rem;
  color: white;
