<template>
  <!-- メモの詳細画面 -->
  <div
    id="ViewLayersDevice"
    v-show="view_layers_device"
    class="ViewLayersLightbox ViewLayersDevice"
  >
    <div class="ViewLayersLightbox_grid1">
      <div class="click_content_head">
        <p>端末情報</p>
        <button class="hiddenBtn" title="閉じる">
          <img
            src="@/assets/close05.png"
            class="btnImg"
            @click="close_button()"
          />
        </button>
      </div>
    </div>
    <div class="ViewLayersLightbox_grid2 ViewLayersDevice">
      <!-- 端末アイコン -->
      <div class="ViewLayersDevice_left_content">
        <div class="ViewLayersDevice_left_content_img">
          <img v-bind:src="device.img" />
        </div>
        <!-- テキスト -->
        <div>
          {{ device.info }}
        </div>
      </div>

      <div class="ViewLayersDevice_right_content">
        <div class="ViewLayersDevice_left_button" title="音声通話発信">
          <img v-bind:src="tel_icon" @click="tel_button()" />
        </div>
        <div class="ViewLayersDevice_right_button" title="ビデオ通話発信">
          <img v-bind:src="video_icon" @click="video_button()" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    close_button() {
      this.$store.commit("setViewLayersDevice", false);
      this.$store.commit("resetViewLayersDeviceInfo");

      const click_targets = this.$store.state.click_targets;
      if (1 == click_targets.length) {
        // 対象1件のみだった場合は一覧画面を飛ばして地図に戻る
        this.$store.commit("resetClickTargets");
      } else {
        // 2件以上の場合は一覧画面を表示
        this.$store.commit("displayClickList");
      }
    },
    tel_button() {
      // 通話ボタンを押した時の挙動
      this.$store.state.sound_on();
      // 個別通話（映像）のボタンを押下した際の処理
      this.$parent.$refs.rtc_private.call_to_remote(
        this.device.personalDestCan,
        "audio"
      );

      this.$store.commit("setViewLayersDevice", false);
      // 取得したデータのリセット
      this.$store.commit("resetViewLayersDeviceInfo");
      this.$store.commit("resetClickTargets");
    },
    video_button() {
      // ビデオ通話ボタンを押した時の挙動
      this.$store.state.sound_on();
      // 個別通話（映像）のボタンを押下した際の処理
      this.$parent.$refs.rtc_private.call_to_remote(
        this.device.personalDestCan,
        "video"
      );

      this.$store.commit("setViewLayersDevice", false);
      // 取得したデータのリセット
      this.$store.commit("resetViewLayersDeviceInfo");
      this.$store.commit("resetClickTargets");
    },
  },
  computed: {
    view_layers_device() {
      return this.$store.state.view_layers_device;
    },
    device() {
      return this.$store.state.view_layers_device_info;
    },
    tel_icon() {
      return "/images/menu_tel.png";
    },
    video_icon() {
      return "/images/menu_video.png";
    },
  },
};
</script>

