import $store from "@/store";
import weatherComponent from "./weatherComponent.vue";
// import weatherComponent from "./weatherLayer.vue";

import Vue from "vue";
export const weatherEvent = new Vue();

export function weatherLayer() {
  const noop = () => {};

  function add() {
    $store.commit("amt/addComponent", weatherComponent);
    setTimeout(() => {
      weatherEvent.$emit("INIT", null);
    }, 1000);
  }

  function setOpacity(val) {
    weatherEvent.$emit("SETOPACITY", val);
  }

  function setVisible(val) {
    weatherEvent.$emit("SETVISIBLE", val);
  }

  function setZIndex(val) {
    weatherEvent.$emit("SETZINDEX", val);
  }

  function setActive(active) {
    if (active) {
      // $store.commit("amt/setSubMenu", "WeatherLayer");
      $store.commit("amt/addComponent", weatherComponent);
      $store.dispatch("amt/showWeatherComponent");
    } else {
      // $store.commit("amt/setSubMenu", null);
      $store.commit("amt/removeComponent", weatherComponent);
      $store.dispatch("amt/hideWeatherComponent");
    }
  }

  // function setActive(val) {
  //   weatherEvent.$emit("SETACTIVE", val);
  //   $store.commit("amt/addComponent", weatherComponent);
  //   setTimeout(() => {
  //     weatherEvent.$emit("INIT", null);
  //   }, 1000)
  // }

  return {
    add, // マップ初期化時に呼び出し
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity,
    setVisible,
    setZIndex,
    setActive,
    // setActive: noop // メニュー操作でトグル
  };
}
