<template>
  <div id="manageLayer_container">
    <!-- タイトル部分 -->
    <div id="manageLayer_title">
      <H3>レイヤ</H3>
      <div
        id="manageLayer_closebutton"
        @click="manageLayer_closebutton()"
        title="閉じる"
      >
        <img src="./assets/close@1x.png" alt="" />
      </div>
    </div>

    <!-- ドラッグで移動できるように -->
    <draggable
      tag="ul"
      handle=".layer_draggable"
      animation="100"
      @start="dragstart"
      @update="dragenter"
      @end="dragend"
    >
      <!-- レイヤ毎のボックス -->
      <li v-for="layer in registedLayers" :key="layer.name">
        <!-- リスト部分はコンポーネントを分割した -->
        <manageLayerList :layer="layer" :dragging="dragging" />
      </li>
    </draggable>
  </div>
</template>

<script>
import { manageEvent } from "./index.js";
import ManageLayerList from "./ManageLayerList";
import draggable from "vuedraggable";

export default {
  data() {
    return {
      Layers: [],
      draggingLayer: undefined,
      dragging: null,
    };
  },
  mounted() {
    let layers = this.$store.getters["amt/layers"];
    // v-forで表示するために型を配列へ
    for (const layer in layers) {
      // layers.jsでレイヤメニューに表示する設定の物だけを抽出
      if (layers[layer].isDisplayLayerMenue) {
        this.Layers.push(layers[layer]);
      }
    }
    this.$store.commit("amt/registedLayers", this.Layers);
  },
  components: {
    manageLayerList: ManageLayerList,
    draggable: draggable,
  },
  methods: {
    manageLayer_closebutton() {
      manageEvent.$emit("MANAGE_BACK");
    },
    dragstart(e) {
      // ドラッグするレイヤを覚えとく
      this.draggingLayer = this.getLayerList()[e.oldDraggableIndex];
      // ドラッグ中は半透明にする
      e.target.style.opacity = 0.5;
      this.dragging = this.draggingLayer.name;
    },
    dragend(e) {
      // 離したら透明度を戻す
      e.target.style.opacity = 1;
      this.dragging = null;
    },
    dragenter(e) {
      const layerList = this.getLayerList();
      const layer = layerList[e.newDraggableIndex];
      // zIndexを入れ替え
      const tempIndex = layer.zIndex;
      layer.zIndex = this.draggingLayer.zIndex;
      this.draggingLayer.zIndex = tempIndex;
      // 表示状態の格納
      this.$store.commit("amt/registedLayers", layerList);

      // zIndexをアップデート
      layerList.forEach((registedLayer) => {
        this.$store.dispatch("amt/setLayerZIndex", {
          layerName: registedLayer.name,
          zIndex: registedLayer.zIndex,
        });
      });
    },
    getLayerList() {
      // 他のmethodから参照したいので最新のregstedLayersを取り出してソート(registedLayersのコピー)
      return this.$store.getters["amt/registedLayers"].slice().sort((a, b) => {
        // zIndex大=リスト先頭にするため+-を逆にする(第2引数が大きい時を+にする)
        return a.zIndex < b.zIndex ? 1 : a.zIndex > b.zIndex ? -1 : 0;
      });
    },
  },
  computed: {
    registedLayers() {
      return this.$store.getters["amt/registedLayers"].slice().sort((a, b) => {
        // zIndex大=リスト先頭にするため+-を逆にする(第2引数が大きい時を+にする)
        return a.zIndex < b.zIndex ? 1 : a.zIndex > b.zIndex ? -1 : 0;
      });
    },
  },
};
</script>

