<template>
  <div id="searchSelectDevice">
    <div class="overflow">
      <p class="deviceTitle float">端末選択</p>
      <p class="deviceAllselect float">
        <input type="checkbox" v-model="selectAll" />
        全選択
      </p>
    </div>
    <div class="searchScrollBox">
      <ul class="searchUl">
        <template v-for="device in devices">
          <li :key="device.id">
            <label>
              <input
                class="heckboxInput"
                type="checkbox"
                v-model="checkNames"
                v-bind:value="device"
              />
              {{ device.name }}
            </label>
          </li>
        </template>
      </ul>
    </div>

    <div class="searchButton">
      <button @click="clickSelect()" class="select">選択</button>
    </div>
    <div class="searchButton">
      <button @click="clickCancel()" class="cancel">キャンセル</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkNames: [],
    };
  },
  methods: {
    allSelect() {},
    clickSelect() {
      this.$store.dispatch("amt/setSearchDevice", {
        device: this.checkNames,
      });
      // 閉じる
      this.$store.dispatch("amt/hideSearchComponentDevice");
      // 検索画面を表示
      this.$store.dispatch("amt/showSearchComponent");
    },
    clickCancel() {
      // 閉じる
      this.$store.dispatch("amt/hideSearchComponentDevice");
      // 検索画面を表示
      this.$store.dispatch("amt/showSearchComponent");
    },
  },
  computed: {
    devices: function () {
      return this.$store.getters["amt/searchTargetDevice"];
    },
    selectAll: {
      get: function () {
        //authorsのチェックボックスのすべてにチェックが入ったかを判定
        if (this.checkNames.length == this.devices.length) {
          return true; //authorsにすべてチェックを入れるとselectAllはtrueとなりチェックが入る
        } else {
          return false;
        }
      },
      set: function (value) {
        //全選択のチェックボックスを操作した場合
        let checkArray = [];
        //チェックが入った場合（trueでの判定）
        if (value) {
          this.devices.forEach(function (device) {
            checkArray.push({ id: device.id, name: device.name });
          });
        }
        this.checkNames = checkArray;
      },
    },
  },
};
</script>

