/* eslint-disable */
import $store from "@/store";
import SearchComponent from "./SearchLayer.vue";

import Vue from "vue";
export const searchEvent = new Vue();

/****** 軌跡を表示 ******/
import { Map, View, Overlay, Feature } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";
/****** 軌跡を表示 ******/

import { memoEvent } from "../MemoLayer/index.js";

export function SearchLayer() {
  /****** 軌跡を表示 ******/

  const ol = {};
  ol.Map = Map;
  ol.View = View;
  ol.Overlay = Overlay;
  ol.Feature = Feature;
  ol.layer = olLayer;
  ol.control = olControl;
  ol.interaction = olInteraction;
  ol.proj = olProj;
  ol.source = olSource;
  ol.style = olStyle;
  ol.geom = olGeom;

  let vecLayer;
  let map;
  let vecLayer_memo;
  let currentMemos;
  let clicked_memo = [];
  let isActive = false;

  // removeを行う時に使用
  let vecLayerTrackInfo = [];
  let vecLayerMemoInfo = [];

  /****** 軌跡を表示 ******/

  const noop = () => {};

  function add() {
    map = $store.getters["amt/map"];

    /****** 軌跡を表示 ******/
    // socketio変更
    searchEvent.$on("track", (msg) => {
      if (isActive == true) {
        vecLayer = new ol.layer.Vector({
          source: new ol.source.Vector({ features: [] }),
        });
        map.addLayer(vecLayer);
        vecLayer.setVisible(true);
        vecLayerTrackInfo.push(vecLayer);

        const layer = $store.getters["amt/layers"];
        const track_layer = layer["TrackLayer"];
        // zIndexと透過率は軌跡レイヤの設定に従う(表示は非表示でも強制表示)
        vecLayer.setZIndex(track_layer.zIndex);
        vecLayer.setOpacity(track_layer.opacity);

        on_track(JSON.parse(msg));
      }
    });

    // VectorLayerの軌跡検索結果を削除
    searchEvent.$on("reset_track", () => {
      if (vecLayer) {
        const source = vecLayer.getSource();
        for (const feature of source.getFeatures()) {
          source.removeFeature(feature);
        }
        for (const vecLayerTrack of vecLayerTrackInfo) {
          map.removeLayer(vecLayerTrack);
        }
        vecLayer = undefined;
        vecLayerTrackInfo = [];
      }
    });
    /****** 軌跡を表示 ******/

    /****** メモを表示 ******/
    map.on("click", _onMapClick);

    vecLayer_memo = new ol.layer.Vector({
      source: new ol.source.Vector({ features: [] }),
    });
    map.addLayer(vecLayer_memo);
    vecLayer_memo.setVisible(true);
    vecLayerMemoInfo.push(vecLayer_memo);
    // socketio変更
    searchEvent.$on("memo", (msg) => {
      if (isActive == true) {
        const layer = $store.getters["amt/layers"];
        const memo_layer = layer["MemoLayer"];
        // zIndexと透過率はメモレイヤの設定に従う(表示は非表示でも強制表示)
        vecLayer_memo.setZIndex(memo_layer.zIndex);
        vecLayer_memo.setOpacity(memo_layer.opacity);
        vecLayer_memo.setVisible(true);
        currentMemos = msg;
        _updateIcons(msg);
      }
    });

    // VectorLayerの軌跡検索結果を削除
    searchEvent.$on("reset_memo", () => {
      const source = vecLayer_memo.getSource();
      for (const feature of source.getFeatures()) {
        source.removeFeature(feature);
      }
    });
    /****** メモを表示 ******/

    searchEvent.$on("SEARCH_END", () => setActive(false));
  }

  function setActive(active) {
    isActive = active;

    if (isActive) {
      $store.commit("amt/addComponent", SearchComponent);
      $store.commit("amt/setSubMenu", "SearchLayer");
      $store.dispatch("amt/showSearchComponent");
    } else {
      searchEvent.$emit("reset_track");
      searchEvent.$emit("reset_memo");
      $store.commit("amt/removeComponent", SearchComponent);
      $store.commit("amt/setSubMenu", null);
      $store.dispatch("amt/hideSearchComponent");
    }
  }

  /***********************/
  /****** 軌跡を表示 ******/
  //TRACK描画要求受信
  // 注意：OpenLayersでの緯度経度処理は[long,lat]でエアマルチ上とは逆
  // data = {
  //    user_id1: { coords1: [[lat, long], [lat, long], ...] },
  //    user_id2: { coords2: [[lat, long], [lat, long], ...] },
  //    ....
  // }
  // 一度、フォーマット変換してから_on_track_drawで描画
  const on_track = (data) => {
    Object.keys(data).forEach((user_id) => {
      if (data[user_id] && data[user_id].coords) {
        // log(`TRACK:data[${user_id}]=${JSON.stringify(data[user_id])}`);
        data[user_id].coords = data[user_id].coords.map((pnt) => {
          var latitude = Number(pnt[1].substr(0, 6));
          var longitude = Number(pnt[0].substr(0, 6));
          return ol.proj.transform(
            [longitude, latitude],
            "EPSG:4326",
            "EPSG:3857"
          );
        });
        _on_track_draw(data[user_id]);
      }
    });
  };
  // _on_track_drawは、「手書き描画」とほぼ同じ構成
  // この場合のdataは一つの描画オブジェクト
  // data = {coords}
  const _on_track_draw = (data) => {
    // データなしor座標なしの場合は抜ける
    if (!data || !data.coords || data.coords.length == 0) {
      return;
    }
    // 検索から来た時はグループに関係なく表示
    // グループ情報を確認して自分の選択中グループでない場合は描画しない
    // 自分と送り主が両者nullの場合も同様
    // if (data.group == null || data.group != activeId.id) {
    //   return;
    // }
    // 描画レイヤからfeatureリストを取得してidが同じなら削除
    const vecLayerSource = vecLayer.getSource();
    vecLayerSource.getFeatures().forEach((feature) => {
      if (feature.get("id") == data.id) {
        vecLayerSource.removeFeature(feature);
      }
    });
    let geometry = new ol.geom.LineString(data.coords);
    const feature = new ol.Feature({
      geometry: geometry,
      id: data.id,
    });
    const style = new ol.style.Style({
      stroke: new ol.style.Stroke({
        width: data.width,
        color: data.color,
      }),
    });
    feature.setStyle(style);
    // 描画
    vecLayerSource.addFeature(feature);
  };
  /****** 軌跡を表示 ******/
  /***********************/

  /***********************/
  /****** メモを表示 ******/
  function _updateIcons(memos) {
    const iconFeatures = memos
      .filter((memo) => memo.lng != null && memo.lat != null)
      .map((memo) => {
        const coordinate = ol.proj.transform(
          [parseFloat(memo.lng), parseFloat(memo.lat)],
          "EPSG:4326",
          "EPSG:3857"
        );
        const iconFeature = new ol.Feature({
          geometry: new ol.geom.Point(coordinate),
          name: "memo_search",
          id: memo.id,
          uid: memo.user_id,
          gid: memo.gid,
          userName: memo.name,
          lng: memo.lng,
          lat: memo.lat,
          date: memo.date,
          kind: memo.kind,
          info: memo.info,
        });
        iconFeature.setStyle(
          new ol.style.Style({
            image: new ol.style.Icon({
              anchor: [0.5, 1],
              opacity: 1.0,
              scale: 0.25,
              src:
                memo.status == "既読"
                  ? memo.kind == "memo"
                    ? "/images/memo_pin.png"
                    : memo.kind == "photo"
                    ? "/images/photo_pin.png"
                    : "/images/video_pin.png"
                  : memo.kind == "memo"
                  ? "/images/memo_red_pin.png"
                  : memo.kind == "photo"
                  ? "/images/photo_red_pin.png"
                  : "/images/video_red_pin.png",
            }),
          })
        );
        return iconFeature;
      });
    const source = vecLayer_memo.getSource();
    for (const feature of source.getFeatures()) {
      source.removeFeature(feature);
    }
    source.addFeatures(iconFeatures);
  }

  function _onMapClick(e) {
    if (!isActive) {
      // 非アクティブ時は何もしない
      return;
    }
    map.forEachFeatureAtPixel(e.pixel, (feature, layer) => {
      _onIconClick(e, feature);
    });
  }

  ///////////////////
  // メモ表示
  ///////////////////

  let memo;
  function _onIconClick(e, feature) {
    if (feature.get("name") == "memo_search") {
      $store.state.sound_on();
      const id = feature.get("id");
      const kind = feature.get("kind");
      // playId = id;
      // playKind = kind;
      memo = currentMemos.find((m) => m.id == id);
      if (memo) {
        memo.kind = kind;
      } else {
        return;
      }
      memo.coordinate = e.coordinate;

      // クリックされた物一覧に追加
      let memo_info = {};
      // ID
      memo_info.id = memo.id;
      // クリックされた時の挙動
      memo_info.click = function (memo_id) {
        // 詳細画面を表示
        memoEvent.$emit("MEMO_VIEW2", clicked_memo[memo_id]);
      };
      // ↑のクリック時動作にわたすパラメータ
      memo_info.param = memo.id;
      // 表示する物を指定
      if (memo.kind == "video") {
        memo_info.type = "img";
        memo_info.img = "/memo/" + memo_info.id + "_thumb.jpg";
      } else if (memo.kind == "photo") {
        memo_info.type = "img";
        memo_info.img = "/memo/" + memo.id;
      } else {
        memo_info.type = "text";
        memo_info.text = memo.info;
      }
      // 左下のアイコン
      let memo_image =
        memo.status == "既読"
          ? memo.kind == "memo"
            ? "/images/memo_icon.png"
            : memo.kind == "photo"
            ? "/images/photo_icon.png"
            : "/images/video_icon.png"
          : memo.kind == "memo"
          ? "/images/memo_red_icon.png"
          : memo.kind == "photo"
          ? "/images/photo_red_icon.png"
          : "/images/video_red_icon.png";
      memo_info.icon = memo_image;
      // 説明文
      memo_info.info = memo.date;

      // storeのオブジェクトに追加
      // $store.state.click_targets.push(memo_info);
      $store.commit("addClickTargets", memo_info);
      $store.commit("displayClickList");

      // クリックされた時の関数で使用
      clicked_memo[memo.id] = memo;
    }
  }
  /****** メモを表示 ******/
  /***********************/

  return {
    add, // マップ初期化時に呼び出し
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity: noop, // 現在のバージョンでは呼び出されない
    setVisible: noop, // 現在のバージョンでは呼び出されない
    setActive, // メニュー操作でトグル
  };
}
