<template>
  <div id="rightButtonDiv">
    <!-- <button v-on:click="$emit('toCenter')" class="circleBtn"> -->
    <button
      @click="toggle_center_mode"
      ref="location_btn"
      class="circleBtn"
      :class="btnClass"
      title="自アイコン追従"
    >
      <img src="@/assets/location10.png" alt="Center Button" />
    </button>
  </div>
</template>

<script>
export default {
  computed: {
    btnClass() {
      return this.$store.state.center_trace > 0 ? "circleBtnSelected" : null;
    },
  },
  // mounted() {
  //   if (this.$store.state.center_trace > 0) {
  //     this.$refs.location_btn.classList.add("circleBtnSelected");
  //   }
  // },
  methods: {
    toggle_center_mode() {
      this.$store.state.sound_on();
      if (this.$store.state.center_trace == 0) {
        this.$store.commit("setCenterTrace", Date.now());
        // this.$refs.location_btn.classList.add("circleBtnSelected");
      } else {
        this.$store.commit("setCenterTrace", 0);
        // this.$refs.location_btn.classList.remove("circleBtnSelected");
      }
    },
  },
};
</script>

<style>
div#rightButtonDiv {
  /* position: absolute; */
  position: fixed;
  right: 10px;
  bottom: 20px;
  z-index: 10;
}
</style>
