<template>
  <div id="view_click_elm" v-show="map_click_visible">
    <div class="click_content_box">
      <div class="click_content_head">
        <p>マップアイコン一覧</p>
        <button class="hiddenBtn" title="閉じる">
          <!-- 非表示化するボタン -->
          <img
            src="@/assets/close05.png"
            class="btnImg"
            @click="close_button()"
          />
        </button>
      </div>
      <div class="click_content_list">
        <!-- クリックされた物一覧 -->
        <template v-for="click_targets in this.click_targets">
          <div
            v-bind:key="click_targets.id"
            @click="click_targets.click(click_targets.id)"
            class="repeat_content"
          >
            <div class="position_relative">
              <!-- 左下のアイコン -->
              <img
                v-show="click_targets.icon"
                class="status_icon"
                v-bind:src="click_targets.icon"
              />

              <!-- サムネイルの画像や本文 -->
              <div class="main_content">
                <div v-if="click_targets.type == 'img'">
                  <!-- メイン画像 -->
                  <img class="main_img" :src="click_targets.img" />
                </div>
                <div v-else-if="click_targets.type == 'text'">
                  <!-- メインテキスト -->
                  <textarea
                    class="main_text"
                    v-model="click_targets.text"
                    readonly
                  />
                </div>
              </div>
            </div>
            <!-- 注釈テキスト -->
            <div class="info_text">
              {{ click_targets.info }}
            </div>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import $store from "@/store";
import { memoEvent } from "@/modules/MemoLayer/index.js";

export default {
  data() {
    return {
      isVisible: false,
    };
  },
  // mounted() {
  //   console.log("読み込みが行われました。");
  // },
  methods: {
    close_button() {
      // メモのデータをサーバへ送信（反映）
      // console.log(this.$store.state.memo_update);
      for (let key in this.$store.state.memo_update) {
        // console.log(key);
        // console.log(JSON.stringify(this.$store.state.memo_update[key]));
        if (0 === Object.keys(this.$store.state.memo_update[key]).length) {
          // 削除の場合
          memoEvent.$emit("DEL_MEMO", key);
        } else {
          memoEvent.$emit("UPDATE_MEMO", this.$store.state.memo_update[key]);
        }
      }
      // 地図に戻る時にクリア
      this.$store.commit("resetMemoUpdate");

      // 取得したデータのリセット
      this.$store.commit("resetClickTargets");
      this.$store.commit("hiddenClickList");
    },
  },
  computed: {
    map_click_visible() {
      return this.$store.state.map_click_visible;
    },
    click_targets() {
      return this.$store.state.click_targets;
    },
  },
};
</script>

