<template>
  <div>
    <!-- Menueボタン -->
    <drawMenue
      v-on:toggleWrite="toggleWrite"
      v-on:toggleErase="toggleErase"
      v-bind:drawLineColor="drawLineColor"
      v-bind:drawLineSize="drawLineWidth"
      v-bind:writeShape="drawShape"
      v-on:inputColor="drawLineColor = $event.hex"
      v-on:inputSize="drawLineWidth = $event"
      v-on:inputShape="updateDrawShape($event)"
      v-show="inSubMenu"
    >
    </drawMenue>
    <map-popup :pos="pos" ref="map_popup"></map-popup>
  </div>
</template>

<script>
/* eslint-disable */
import { Map, View, Feature } from "ol";

import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";
import * as olFormat from "ol/format";
import Draw, { createBox, createRegularPolygon } from "ol/interaction/Draw.js";

import DrawMenue from "./DrawMenue.vue";
import { drawEvent } from "./index.js";
import { evEmitter } from "../ReviewLayer";
import MapPopup from "@/components/MapPopup.vue";

import $ from "jquery";

var ol = {};
ol.Map = Map;
ol.View = View;
ol.layer = olLayer;
ol.control = olControl;
ol.interaction = olInteraction;
ol.proj = olProj;
ol.source = olSource;
ol.style = olStyle;
ol.geom = olGeom;
ol.format = olFormat;
ol.Feature = Feature;

export default {
  data: function () {
    return {
      inSubMenu: false,
      drawStartFlag: false,
      multiTouch: false,
      drawX: 0,
      drawY: 0,
      drawLineWidth: 5,
      drawLineColor: "#000000",
      lineId: "",
      timerId: null,
      tiimerCnt: 0,

      drawLayer: null,
      eraseLayer: null,
      freeDraw: null,
      freeErase: null,

      writeMode: false,
      eraseMode: false,
      multitouch: true,

      drawShape: "free",

      pos: null,
    };
  },
  components: {
    drawMenue: DrawMenue,
    mapPopup: MapPopup,
  },
  computed: {
    myId: function () {
      return this.$store.getters["amt/myId"];
    },
    myPosition: function () {
      return this.$store.getters["amt/myPosition"];
    },
    patrolState: function () {
      return this.$store.getters["amt/patrolState"];
    },
    users() {
      return this.$store.getters["amt/users"];
    },
    user() {
      return this.myId && this.users[this.myId];
    },
    user_name() {
      return this.myId && this.users[this.myId].user_name;
    },
    socketio() {
      return this.$store.getters["socketio"];
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
  },
  watch: {
    group_id(val) {
      this.socketio.emit("initdraw");
    },
  },
  methods: {
    //描画用レイヤー作成
    createDrawLayer: function () {
      const drawLayerSource = new ol.source.Vector({
        format: new ol.format.GeoJSON(),
        // wrapX: false,
        //url: 'file://' + __dirname + '/data/drawdata.json',
      });
      return new ol.layer.Vector({
        source: drawLayerSource,
        style: new ol.style.Style({
          stroke: new ol.style.Stroke({ color: "#000000", width: 3 }),
        }),
      });
    },
    //消去用レイヤー作成
    createEraseLayer: function () {
      const eraseLayerSource = new ol.source.Vector({
        format: new ol.format.GeoJSON(),
        //url: 'file://' + __dirname + '/data/drawdata.json',
      });
      return new ol.layer.Vector({
        source: eraseLayerSource,
        style: new ol.style.Style({
          stroke: new ol.style.Stroke({ color: "rgba(0,0,0,0)", width: 0 }),
        }),
      });
    },
    //消去オブジェクト作成
    createFreeErase: function () {
      return new ol.interaction.Draw({
        source: this.eraseLayer.getSource(),
        type: "LineString",
        freehand: true,
      });
    },
    // 描画オブジェクト作成
    createDrawShape: function () {
      // まずは書いてみる
      // 丸と四角(三角をどうするか)
      let options = {};
      switch (this.drawShape) {
        case "free":
          options = {
            source: this.drawLayer.getSource(),
            type: "LineString",
            freehand: true,
          };
          break;
        case "line":
          options = {
            source: this.drawLayer.getSource(),
            type: "LineString",
            freehand: false,
          };
          break;
        case "triangle":
          options = {
            source: this.drawLayer.getSource(),
            type: "Polygon",
            freehand: false,
            maxPoints: 3,
            minPoints: 3,
          };
          break;
        case "square":
          options = {
            source: this.drawLayer.getSource(),
            type: "Circle",
            freehand: true,
            geometryFunction: createBox(),
          };
          break;
        case "circle":
          options = {
            source: this.drawLayer.getSource(),
            type: "Circle",
            freehand: true,
          };
          break;
      }
      return new ol.interaction.Draw(options);
    },
    //描画開始
    // フリーハンド描画の場合は一定時間毎に描画中の内容を共有
    onDrawFreeStart: function (e) {
      this.multiTouch = false;
      this.drawStartFlag = true;
      var style_modify = new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: this.drawLineWidth,
          color: this.drawLineColor,
        }),
      });
      var currentFeature = e.feature;
      currentFeature.setStyle(style_modify);
      this.lineId = this.getLineUniqueStr();
      currentFeature.set("id", this.lineId);
      //this.log("draw start");
      if (this.timerId != null) {
        clearInterval(this.timerId);
        this.timerId = null;
      }
      this.timerCnt = 100;
      this.timerId = setInterval(() => {
        this.sendDraw(currentFeature, this.lineId);
        if (--this.timerCnt <= 0) {
          clearInterval(this.timerId);
          this.timerId = null;
        }
      }, 200);
    },
    // 描画開始
    // フリーハンドで線を引く場合を除いて、描画完了時点で送信
    onDrawStart: function (e) {
      this.multiTouch = false;
      this.drawStartFlag = true;
      var style_modify = new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: this.drawLineWidth,
          color: this.drawLineColor,
        }),
      });
      var currentFeature = e.feature;
      currentFeature.setStyle(style_modify);
      this.lineId = this.getLineUniqueStr();
      currentFeature.set("id", this.lineId);
      //this.log("draw start");
    },
    //描画終了
    onDrawEnd: function (e) {
      if (this.timerId != null) {
        clearInterval(this.timerId);
        this.timerId = null;
      }
      console.log(e);
      e.feature.set("user_name", this.user_name);
      e.feature.set("timestamp", Date.now());

      this.sendDraw(e.feature, this.lineId);
      //this.log("draw end");
    },
    //消去開始
    onEraseStart: function (e) {
      // @task スタイルを幅1で透明な線に変更(見えない線をひく)
      const style_modify = new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: 1,
          color: "rgba(255,0,0,0)",
        }),
      });
      const currentFeature = e.feature;
      currentFeature.setStyle(style_modify);
    },
    //消去終了
    onEraseEnd: function () {
      // @task 消去レイヤーに描いた透明な線を削除
      const source = this.eraseLayer.getSource();
      source.getFeatures().forEach(function (feature) {
        source.removeFeature(feature);
      });
    },
    //線分のユニーク名を取得
    getLineUniqueStr: function (myStrong) {
      var strong = 10;
      if (myStrong) strong = myStrong;
      return (
        new Date().getTime().toString(16) +
        Math.floor(strong * Math.random()).toString(16)
      );
    },
    //描画を通知
    sendDraw: function (feature, id) {
      var data = {};
      // 描画用の情報
      data.width = this.drawLineWidth;
      data.color = this.drawLineColor;
      data.type = feature.getGeometry().getType();
      data.id = id;
      data.coords = null;
      data.radius = null;
      data.user_name = feature.get("user_name");
      data.timestamp = feature.get("timestamp");
      // 現在の選択中グループ情報
      const activeId = this.$store.state.activeId;
      if (activeId.kind == "group") {
        data.group = activeId.id;
      } else {
        data.group = null;
      }
      // this.log(`type:${data.type}, selected:this.drawShape`);
      // 描画する図形によって取得すべき情報が異なるので分岐する
      switch (data.type) {
        case "LineString":
        case "Polygon":
          // 線または多角形の描画情報
          // 対象: free/line/triangle/square
          // 座標を取得
          data.coords = feature.getGeometry().getCoordinates();
          break;
        case "Circle":
          // 円の描画
          // 対象: circle
          // 中心座標と半径を取得
          data.radius = feature.getGeometry().getRadius();
          data.coords = feature.getGeometry().getCenter();
          break;
      }
      var jsonData = JSON.stringify(data);
      this.$store.state.socketio.emit("draw", jsonData);
    },
    //消去ボタン（開始）
    eraseBtnStart: function () {
      // $("#eraseBtn").css({"background-color": this.$store.state.buttonRed});
      const map = this.$store.state.map;
      map.removeInteraction(this.freeDraw);
      map.addInteraction(this.freeErase);
      this.eraseMode = true;
    },
    //消去ボタン（終了）
    eraseBtnEnd: function () {
      // $("#eraseBtn").css({"background-color": this.$store.state.buttonBlue});
      this.eraseMode = false;
      const map = this.$store.state.map;
      map.removeInteraction(this.freeErase);
      map.addInteraction(this.freeDraw);
    },
    //消去実行
    eraseNearest: function (myCoord) {
      // 閾値
      const max = 250 / 2 ** (this.$store.state.map.getView().getZoom() - 14);
      let minFeature;
      let minDist = -1;
      const drawLayerSource = this.drawLayer.getSource();

      drawLayerSource.getFeatures().forEach(function (feature) {
        // 最短距離の場所にある座標を取得
        const geometry = feature.get("geometry");
        const closest = geometry.getClosestPoint(myCoord);

        // 各座標間の距離
        const xDist = Math.abs(closest[0] - myCoord[0]);
        const yDist = Math.abs(closest[1] - myCoord[1]);
        if (xDist + yDist <= max) {
          // 自座標と一番近い点のLineStringを作成し、距離を取得する
          const ls = new ol.geom.LineString([closest, myCoord]);
          const dist = ls.getLength();
          if (minDist < 0 || dist < minDist) {
            // 一番近くの点の情報に更新
            minDist = dist;
            minFeature = feature;
          }
        }
      });
      // 最小距離が0以上(=該当するものがあった判定)なら描画レイヤーからその描画単位で削除する
      if (minDist >= 0) {
        //alert(minFeature.get("id"));
        drawLayerSource.removeFeature(minFeature);
        minFeature.changed();
        this.$store.state.socketio.emit("erase", minFeature.get("id"));
        //this.log("erased");
      }
    },

    //描画指令受信
    onReceiveDraw: function (jsonData) {
      // データなしor座標なしの場合は抜ける
      const data =
        typeof jsonData === "string" ? JSON.parse(jsonData) : jsonData;
      if (!data || !data.coords || data.coords.length == 0) {
        return;
      }
      // グループ情報を確認して自分の選択中グループでない場合は描画しない
      // 自分と送り主が両者nullの場合も同様
      if (
        data.group == null ||
        (data.group != this.$store.state.activeId.id &&
          this.$store.state.activeId.id !== 0)
      ) {
        return;
      }
      // 描画レイヤからfeatureリストを取得してidが同じなら削除
      const drawLayerSource = this.drawLayer.getSource();
      drawLayerSource.getFeatures().forEach((feature) => {
        if (feature.get("id") == data.id) {
          drawLayerSource.removeFeature(feature);
        }
      });

      // 受信したデータから図形を再現
      // 図形の種別によって分岐
      let geometry;
      switch (data.type) {
        case "LineString":
          // 対象: free/line
          geometry = new ol.geom.LineString(data.coords);
          break;
        case "Polygon":
          // 対象: triangle/square
          geometry = new ol.geom.Polygon(data.coords);
          break;
        case "Circle":
          // 対象: circle
          geometry = new ol.geom.Circle(data.coords, data.radius);
          break;
      }
      const feature = new ol.Feature({
        geometry: geometry,
        id: data.id,
        user_name: data.user_name,
        timestamp: data.timestamp,
      });
      const style = new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: data.width,
          color: data.color,
        }),
      });
      feature.setStyle(style);
      // 描画
      drawLayerSource.addFeature(feature);
    },

    //全描画指令受信
    onReceiveAllDraw(jsonData) {
      // まずは、全削除
      const drawLayerSource = this.drawLayer.getSource();
      drawLayerSource.getFeatures().forEach(function (feature) {
        drawLayerSource.removeFeature(feature);
      });
      // そして、全描画
      const dataSet =
        typeof jsonData === "object" ? jsonData : JSON.parse(jsonData);
      dataSet.forEach((data) => {
        this.onReceiveDraw(data);
      });
    },

    //消去指令受信
    onReceiveErase: function (id) {
      const drawLayerSource = this.drawLayer.getSource();
      drawLayerSource.getFeatures().forEach(function (feature) {
        if (feature.get("id") == id) {
          drawLayerSource.removeFeature(feature);
        }
      });
    },

    //タッチ開始
    onWindowTouchStart: function (e) {
      if (this.writeMode) {
        if (e.touches.length > 1) {
          this.multiTouch = true;
        } else {
          //multiTouch = false;
        }
      }
    },

    //地図ドラッグ（描画/消去実行）
    onMapDrag: function (e) {
      if (this.eraseMode) {
        this.eraseNearest(e.coordinate);
        //return false;
      } else if (this.writeMode) {
        // if (multiTouch) {
        //     return false;
        // }
        //const x = e.pixel[0];
        //const y = e.pixel[1];
        // @task 描画開始直後なら、その点を保持
        if (this.drawStartFlag) {
          this.drawX = e.pixel[0];
          this.drawY = e.pixel[1];
          this.drawStartFlag = false;
        } else {
          // @task 描画中なら、今の座標と前回の点の差をとる
          const xDiff = e.pixel[0] - this.drawX;
          const yDiff = e.pixel[1] - this.drawY;
          // @task マルチタッチが有効？and2点距離が50を超えていたらfalseを返す
          if (
            this.multiTouch &&
            Math.sqrt(xDiff * xDiff + yDiff * yDiff) > 50
          ) {
            return false;
          } else {
            this.drawX = e.pixel[0];
            this.drawY = e.pixel[1];
          }
        }
      }
    },

    initDraw: function () {
      this.drawLayer = this.createDrawLayer();
      this.eraseLayer = this.createEraseLayer();
      const map = this.$store.state.map;
      map.addLayer(this.drawLayer);
      map.addLayer(this.eraseLayer);
      this.freeDraw = this.createDrawShape();
      this.freeErase = this.createFreeErase();

      //描画開始
      this.freeDraw.on("drawstart", (e) => {
        this.onDrawFreeStart(e);
      });

      //描画終了
      this.freeDraw.on("drawend", (e) => {
        this.onDrawEnd(e);
      });

      //消去開始
      this.freeErase.on("drawstart", (e) => {
        this.onEraseStart(e);
      });

      //消去終了
      this.freeDraw.on("drawend", () => {
        this.onEraseEnd();
      });

      //描画指令受信
      this.$store.state.socketio.on("draw", (jsonData) => {
        this.onReceiveDraw(jsonData);
      });

      //全描画指令受信
      this.$store.state.socketio.on("alldraw", (jsonData) => {
        this.onReceiveAllDraw(jsonData);
      });

      // //TRACK描画指令受信
      // // 注意：OpenLayersでの緯度経度処理は[long,lat]でエアマルチ上とは逆
      // this.$store.state.socketio.on("track", jsonData => {
      //   const data = JSON.parse(jsonData);
      //   console.log(`TRACK:data=${jsonData}`)
      //   Object.keys(data).forEach(user_id => {
      //     if (data[user_id] && data[user_id].coords) {
      //       console.log(`TRACK:data[${user_id}]=${JSON.stringify(data[user_id])}`);
      //       console.log(`TRACK:data[${user_id}].coords=${JSON.stringify(data[user_id].coords)}`);
      //       data[user_id].coords = data[user_id].coords.map(pnt => {
      //         return ol.proj.transform([pnt[1], pnt[0]], "EPSG:4326","EPSG:3857");
      //       });
      //       console.log(`変換後:TRACK:data[${user_id}]=${JSON.stringify(data[user_id])}`);
      //       this.onReceiveDraw(data[user_id]);
      //     }
      //   });
      // });

      //消去指令受信
      this.$store.state.socketio.on("erase", (id) => {
        this.onReceiveErase(id);
      });

      //タッチ開始
      $(window).on("touchstart", (e) => {
        this.onWindowTouchStart(e);
      });

      //地図ドラッグ（描画/消去実行）
      map.on("pointerdrag", (e) => {
        this.onMapDrag(e);
      });

      // サーバで持っている描画データを要求 alldrawが返ってくる
      setTimeout(() => {
        this.$store.state.socketio.emit("initdraw");
      }, 1000);

      // jsonの手書き色を反映
      this.drawLineColor = this.$store.state.draw_color;

      // ---------------------------------REVIEW DRAW-------------------------------------------------------
      evEmitter.on("draw", (ev) => {
        console.log(ev);
        this.onReceiveDraw(ev);
      });
      evEmitter.on("alldraw", (ev) => {
        console.log(ev);
        this.onReceiveAllDraw(ev);
      });
      evEmitter.on("erase", (ev) => {
        console.log(ev);
        this.onReceiveErase(ev);
      });
    },
    updateDrawShape(shape) {
      // 初期化はフリーハンドなので図形選択切替時に
      // 描画オブジェクトを入れ替える
      // @todo 描画状態かどうか何らかの判定が必要かも
      console.log(shape);
      // this.log(shape);
      this.$store.state.map.removeInteraction(this.freeDraw);
      // 描画オブジェクトを作り直し
      this.drawShape = shape;
      this.freeDraw = this.createDrawShape();
      if (this.drawShape == "free") {
        this.freeDraw.on("drawstart", (e) => {
          this.onDrawFreeStart(e);
        });
      } else {
        this.freeDraw.on("drawstart", (e) => {
          this.onDrawStart(e);
        });
      }
      //描画終了
      this.freeDraw.on("drawend", (e) => {
        this.onDrawEnd(e);
      });
      this.$store.state.map.addInteraction(this.freeDraw);
    },
    toggleWrite(isEnable) {
      this.$store.state.mapChangeEvent.$emit("changeAtDraw", isEnable);
    },
    toggleErase(isEnable) {
      // 今の所このメソッドでは振り分けているだけ
      if (isEnable) {
        this.eraseBtnStart();
      } else {
        this.eraseBtnEnd();
      }
    },
  },
  mounted() {
    this.initDraw();

    // @todo ここの処理は古いイベントハンドラを使っているため、Vue向けに置き換えする
    this.$store.state.mapChangeEvent.$on("changeAtDraw", (val) => {
      // this.$store.commit("setAtDraw", val);
      if (this.eraseMode) {
        this.$store.state.map.removeInteraction(this.freeErase);
      }
      this.eraseMode = false;
      if (val && !this.writeMode) {
        this.$store.state.map.addInteraction(this.freeDraw);
      } else if (!val) {
        this.$store.state.map.removeInteraction(this.freeDraw);
      }
      this.writeMode = val;
    });

    drawEvent.$on("SETACTIVE", (active) => {
      if (active) {
        // 手書きモードの開始
        this.toggleWrite(true);
        this.inSubMenu = true;
      } else {
        // 手書きモードの終了
        this.toggleWrite(false);
        this.inSubMenu = false;
      }
    });

    drawEvent.$on("CHANGE_COLOR", (drawLineColor) => {
      this.drawLineColor = drawLineColor;
    });

    drawEvent.$on("SETOPACITY", (val) => {
      this.drawLayer.setOpacity(val);
      this.eraseLayer.setOpacity(val);
    });

    drawEvent.$on("SETVISIBLE", (val) => {
      this.drawLayer.setVisible(val);
      this.eraseLayer.setVisible(val);
    });

    drawEvent.$on("SETZINDEX", (val) => {
      this.drawLayer.setZIndex(val);
      this.eraseLayer.setZIndex(val);
    });

    this.$store.state.map.on("click", (ev) => {
      const pos = this.$refs.map_popup.getPos();
      if (pos) {
        this.$refs.map_popup.hide();
      }
      // this.pos = ev.coordinate;
      if (this.last_feature && this.original_style) {
        this.last_feature.setStyle(this.original_style);
      }
      this.$refs.map_popup.on_click(ev, this.drawLayer);
    });
  },
  beforeDestroy() {
    this.toggleWrite(false);
    this.inSubMenu = false;
  },
};
</script>
