// import pdfjsLib from "pdfjs-dist";
import pdfjsLib from "pdfjs-dist/webpack";
// pdfjsLib.GlobalWorkerOptions.workerSrc = require('pdfjs-dist/build/pdf.worker.min.js');

export default function (elm, url) {
  this.canvas = elm || document.createElement("canvas");
  this.ctx = this.canvas.getContext("2d");
  this.page_num = 1;
  this.scale = 1;
  this.started = false;
  // const pdfjsLib = require("pdfjs-dist/webpack");

  this.readFileAsync = async (file_obj) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        resolve(reader.result);
      };
      reader.onerror = reject;
      reader.readAsArrayBuffer(file_obj);
    });
  };

  this.callback = {};
  this.on = (key, func_name) => {
    this.callback[key] = func_name;
  };
  this.off = (key) => {
    delete this.callback[key];
  };
  this.all_off = () => {
    Object.keys(this.callback).forEach((key) => {
      this.off(key);
    });
  };

  this.renderPDF = async () => {
    try {
      const page = await this.pdfDoc.getPage(this.page_num);
      const viewport = page.getViewport({ scale: this.scale });

      this.canvas.height = viewport.height;
      this.canvas.width = viewport.width;

      const renderTask = await page.render({
        canvasContext: this.ctx,
        viewport: viewport,
      }).promise;
      // レンダリング完了後に実行される
      console.log("Page rendered");
      if (!this.started) {
        this.callback["start"]();
        this.started = true;
      }
      this.callback["resize"](this.canvas.width, this.canvas.height);
      this.callback["page"](this.page_num);
      await page.cleanup();
    } catch (err) {
      console.log(`ERROR:${err}`);
    }
  };

  this.readFileAsync_url = async (url) => {
    const res = await fetch(url);
    const blob = await res.blob();
    const fileData = await this.readFileAsync(blob);
    this.pdfDoc = await pdfjsLib.getDocument({ data: fileData });
    this.renderPDF();
  };

  if (url) {
    this.readFileAsync_url(url);
  } else {
    this.input = document.createElement("input");
    this.input.type = "file";
    this.input.accept = "application/pdf";
    this.input.style = "display: none";
    this.input.onchange = async () => {
      const fileData = await this.readFileAsync(this.input.files[0]);
      this.pdfDoc = await pdfjsLib.getDocument({ data: fileData }).promise;
      this.renderPDF();
    };
    this.open = () => this.input.click();
  }
  this.close = () => {
    this.pdfDoc.cleanup();
    this.pdfDoc.destroy();
    this.pdfDoc = null;
    this.all_off();
  };
  this.next = () => {
    this.page_num =
      this.page_num < this.pdfDoc.numPages ? this.page_num + 1 : this.page_num;
    this.renderPDF();
  };
  this.prev = () => {
    this.page_num = this.page_num > 1 ? this.page_num - 1 : this.page_num;
    this.renderPDF();
  };
  this.page = (page_num, val) => {
    if (val) this.set_scale(val);
    if (0 < page_num && this.pdfDoc.numPages >= page_num) {
      this.page_num = page_num;
      this.renderPDF();
    }
  };
  this.set_scale = (val) => {
    this.scale = val;
  };
  this.canvas.onclick = this.next;
}
