import { P2P, getDesktopStream, getStream } from "./p2p.js";
import $store from "@/store";
import io from "socket.io-client";

//--------------------------------------------------
// LOG
// <<使い方>>
// LOG(
//   TITLE,       /* タイトル */
//   message,     /* 表示したいメッセージ */
//   style        /* CSSと同じスタイル（オプション） */
// )
// 配列NO_DISPに、このTITLEを含めると、表示しなくなる。
//--------------------------------------------------
const NO_DISP = [];
const LOG = function (title, s, style) {
  // const NO_DISP = ["OFFER", "ANSWER", "ICE"]
  if (NO_DISP.includes(title)) return;

  if (style) {
    console.log(`${title}: %c${s}`, style, "");
  } else {
    console.log(`${title}: ${s}`);
  }
};

export function P2G(socketio) {
  this.closed = false;
  this.socketio = socketio ? socketio : io.connect();
  // this.socketio = io.connect();
  const my_id = $store.state.myId;
  const group_id = $store.state.activeId.id;
  const stun_server = $store.state.stun_server;
  let userHash = {};
  let last_user_ids = null;
  this.remotes = {};
  this.local_stream;

  //--------------------------------------------------
  // Regist
  //--------------------------------------------------
  // this.socketio.emit("regist", JSON.stringify({ id: my_id, group_id: "g1" }));

  //--------------------------------------------------
  // Regist
  //--------------------------------------------------
  // setInterval(() => {
  //   this.socketio.emit("renew", JSON.stringify({ id: my_id, group_id: "g1" }));
  // }, 1500);

  //--------------------------------------------------
  // Callback登録
  //--------------------------------------------------
  const callbacks = {};
  this.on = (event_name, callback_func) => {
    callbacks[event_name] = callback_func;
  };

  this.on_renew = (msg) => {
    // if (!local_stream) return;
    if (this.closed) {
      return;
    }

    let new_users = []; // 追加したリモート先
    let del_users = []; // 削除するリモート先
    userHash = JSON.parse(msg);
    const user_ids = Object.keys(userHash).filter(
      (id) => id !== "system" && userHash[id].group_id == group_id
    );
    if (user_ids.includes(my_id)) user_ids.splice(user_ids.indexOf(my_id), 1);
    last_user_ids = Object.keys(this.remotes);
    // console.log(`last_user_ids=${last_user_ids}`);

    // 前回のRenew通知と比較して増えた分が、new_users、
    // 減った分がdel_usersとして抽出する。
    if (last_user_ids.length > 0) {
      new_users = user_ids.filter((user) => {
        return !last_user_ids.includes(user);
      });
      del_users = last_user_ids.filter((user) => {
        return !user_ids.includes(user);
      });
    } else {
      new_users = user_ids;
    }

    new_users.forEach((user) => {
      if (my_id == user) return;
      const peer = new P2P({
        my_id: my_id,
        remote_id: user,
        group_id: group_id,
        socket: this.socketio,
        direction: "sendonly",
        type: "video",
        EVENT_KEY: "share",
        stun_server: stun_server,
      });

      // 各接続先から着信イベントで発火する。
      peer.on("call_in", (stream) => {
        callbacks.call_in(stream);
      });
      // 各接続先から着信したtrackのmuteイベントで発火する。
      peer.on("call_out", () => {
        callbacks.call_out();
      });
      this.remotes[user] = { peer: peer };
    });
    del_users.forEach((user) => {
      delete this.remotes[user];
    });

    last_user_ids = user_ids;
  };

  this.socketio.on("renew", this.on_renew);

  // this.getStream = () => {
  //   getDesktopStream(null).then(stream => {
  //     this.local_stream = stream;
  //   });
  // };

  this.call_in = async (elm) => {
    if (elm.getContext) {
      this.local_stream = elm.captureStream();
    } else {
      this.local_stream = await getDesktopStream(elm);
    }
    Object.keys(this.remotes).forEach((id) => {
      if (my_id == id) return;
      this.remotes[id].peer.setStream(this.local_stream);
      this.remotes[id].peer.call_in("sendonly");
      // this.remotes[id].peer.call_in("sendonly", this.local_stream);
    });
    return this.local_stream;
  };

  this.call_out = () => {
    Object.keys(this.remotes).forEach((id) => {
      if (my_id == id) return;
      this.remotes[id].peer.call_out();
    });
  };

  this.close = () => {
    this.closed = true;
    Object.keys(this.remotes).forEach((id) => {
      if (my_id == id) return;
      // this.remotes[id].peer.call_out();
      // this.socketio = null;
      // this.socketio.off("renew");
      this.socketio.off("renew", this.on_renew);
      this.remotes[id].peer.close();
      delete this.remotes[id].peer;
      delete this.remotes[id];
      this.last_user_ids = null;
    });
  };
}

const _uuidv4 = () => {
  return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (c) {
    var r = (Math.random() * 16) | 0,
      v = c == "x" ? r : (r & 0x3) | 0x8;
    return v.toString(16);
  });
};
