<template>
  <div id="timeDisp" class="nosel">{{ timeStr }}</div>
</template>
<script>
export default {
  data() {
    return {
      timeSec: 0,
      timer_id: null,
    };
  },
  watch: {
    at_send() {
      if (this.at_send) {
        this.startTimer();
      } else {
        this.stopTimer();
      }
    },
    at_recv() {
      if (this.at_recv) {
        this.startTimer();
      } else {
        this.stopTimer();
      }
    },
  },
  computed: {
    at_send() {
      return this.$store.getters["ptt/pttActive"];
    },
    at_recv() {
      return this.$store.getters["ptt/pttRecv"];
    },
    timeStr() {
      let s = this.timeSec;
      const s1 = s % 10;
      s = (s - s1) / 10;
      const s2 = s % 6;
      s = (s - s2) / 6;
      const m1 = s % 10;
      s = (s - m1) / 10;
      const m2 = s % 6;
      return "" + m2 + m1 + ":" + s2 + s1;
    },
  },
  methods: {
    startTimer() {
      if (this.timer_id != null) {
        clearInterval(this.timer_id);
        this.timer_id = null;
      }
      this.timeSec = 0;
      this.timer_id = setInterval(() => {
        this.timeSec++;
        // this.$store.commit("setTimeStr", this.timeStr);
      }, 1000);
    },
    stopTimer() {
      if (this.timer_id != null) {
        clearInterval(this.timer_id);
        this.timer_id = null;
      }
      this.timeSec = 0;
      // this.$store.commit("setTimeStr", this.timeStr);
    },
  },
  beforeDestroy() {
    clearInterval(this.timer_id);
  },
};
</script>
<style scoped>
div#timeDisp {
  position: absolute;
  top: 28vh;
  width: 100%;
  height: 7rh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}
</style>
