<template>
  <div id="phoneButtonDiv">
    <button
      v-on:touchstart="phoneTouchStart"
      v-on:touchend="phoneTouchEnd"
      v-on:mousedown="phoneTouchStart"
      v-on:mouseup="phoneTouchEnd"
      v-on:mouseout="phoneTouchEnd"
      id="phoneBtn"
      ref="phoneBtn"
      class="circleBigBtn"
      :style="phoneButtonSrc"
      title="PTT"
    >
      <img
        :src="mic"
        :style="mic_class"
        class="unselect"
        oncontextmenu="return false;"
      />
      <audio ref="audio_press" :src="audio_url"></audio>
    </button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      clearId: {},
      audio_level: 0, // 0 - 1.0 // 0.001以下で無着信と判断する。
      last_audio_level: 0,
      audio_url: require("../assets/press.wav"),
      audio_press: null,
      mic: require("../assets/mic.png"),
    };
  },
  mounted() {
    this.audio_press = this.$refs.audio_press;
    this.btn = document.getElementById("phoneBtn");
  },
  methods: {
    phoneTouchStart() {
      this.$store.dispatch("ptt/touchStart");
      this.audio_press.play();
    },
    phoneTouchEnd() {
      this.$store.dispatch("ptt/touchEnd");
    },
  },
  computed: {
    phoneButtonSrc() {
      return {
        backgroundColor:
          this.pttRecv || this.out_level > 0
            ? "#4cc444"
            : this.pttActive
            ? "#c44444"
            : this.pttEnabled
            ? "#4c44c4"
            : "#c4c4c4",
      };
    },
    pttRecv() {
      return this.$store.getters["ptt/pttRecv"];
    },
    pttActive() {
      return this.$store.getters["ptt/pttActive"];
    },
    pttEnabled() {
      return this.$store.getters["ptt/pttEnabled"];
    },
    mic_class() {
      if (this.pttActive) {
        return {
          position: "absolute",
          top: "calc(13%)",
          left: "calc(13%)",
          width: "80%",
        };
      } else {
        return {
          position: "absolute",
          top: "calc(10%)",
          left: "calc(10%)",
          width: "80%",
        };
      }
    },
    remote_audio_stream() {
      return this.$store.getters["rtc/remote_audio_stream"];
    },
    out_level() {
      return this.$store.getters["rtc/out_level"];
    },
  },
  beforeDestroy() {
    Object.keys(this.clearId).forEach((key) => {
      clearInterval(this.clearId[key]);
    });
  },
};
</script>

<style>
div#phoneButtonDiv {
  position: absolute;
  /* position: fixed; */
  bottom: 3vh;
  left: calc(50% - 52px);
  z-index: 100;
}
button.circleBigBtn {
  width: auto;
  padding: 0;
  margin: 0;
  /* background: none; */
  border: 0;
  font-size: 0;
  line-height: 0;
  overflow: visible;
  cursor: pointer;
  outline: none !important;
  user-select: none;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  /* background-color: gray; */
}
.greenBtn {
  background-color: green;
}
.redBtn {
  background-color: red;
}
.blueBtn {
  background-color: blue;
}
.grayBtn {
  background-color: gray;
}
.unselect {
  user-select: none;
  -webkit-user-drag: none;
  -webkit-touch-callout: none;
}
.mic {
  position: absolute;
  top: calc(10%);
  left: calc(10%);
  width: 80%;
}
</style>
