<template>
  <div>
    <div class="videoContainer_private" ref="videoContainer2">
      <video-unit v-if="is_showed" @zIndex_up="zIndex_down">
        <remote-video
          ref="remoteVideo"
          :user="user"
          :remote_id="remote_id"
          :mode="mode"
          @call_end="call_end"
        >
        </remote-video>
      </video-unit>
    </div>
    <!-- 個別通話用モーダルダイアログ -->
    <modal-dialog v-if="is_callee_dialog_visible">
      <span slot="header"></span>
      <span slot="body">
        {{ user_name }}から個別通話 {{ media_type }}が着信しました。応答しますか
      </span>
      <div slot="footer">
        <div style="display: flex; justify-content: space-around">
          <div
            class="circle"
            style="background-color: red"
            @click="call_end"
            title="拒否"
          >
            <img :src="hook_down" class="circle_img" />
          </div>
          <div
            class="circle"
            style="background-color: green"
            @click="call_accept"
            title="応答"
          >
            <img :src="hook_up" class="circle_img" />
          </div>
        </div>
      </div>
    </modal-dialog>
  </div>
</template>

<script>
/* eslint-disable */
import VideoUnit from "./VideoUnit";
import ModalDialog from "@/components/ModalDialog.vue";
import RemoteVideo from "./RemoteVideo.vue";
import { memoEvent } from "../MemoLayer/index.js";

export default {
  components: {
    videoUnit: VideoUnit,
    modalDialog: ModalDialog,
    remoteVideo: RemoteVideo,
  },
  data() {
    return {
      user: null,
      user_name: "",
      remote_id: null,
      mode: "private",
      is_showed: false,
      is_callee_dialog_visible: false,
      is_caller_dialog_visible: false,
      media_type: null,
      caller_name: "",
      hook_up: require("@/assets/phone10.png"),
      hook_down: require("@/assets/hookdown10.png"),
    };
  },
  mounted() {
    // publishメッセージ受信時の処理分岐
    this.socketio.on("publish", this.on_call_from_remote);
  },
  methods: {
    zIndex_down(vm) {
      const child_elms = this.$refs.videoContainer2.children;
      for (let i = 0; i < child_elms.length; i++) {
        child_elms[i].style.zIndex = 2;
      }
      // vm.zIndex = 10;
      vm.$refs.videoUnit.style.zIndex = 10;
    },
    // start(remote_id) {
    //   this.user = this.users[remote_id];
    //   this.remote_id = remote_id;
    //   this.$refs.video_unit.start();
    // },
    // ----------------------------------------------------------------------------------
    // 個別通話の発呼
    call_to_remote(remote_id, media_type) {
      // this.$store.commit("rtc/set_personal_id", remote_id);
      this.$store.commit("setPer", "person");
      this.user = this.users[remote_id];
      this.user_name = this.user.user_name;
      this.remote_id = remote_id;
      const send_msg = {
        type: media_type == "video" ? "call_video" : "call_audio",
        dest: remote_id,
        src: this.myId,
      };
      this.socketio.emit("publish", send_msg);
      this.$store.state.toast_on("個別通話を呼出し中です。", "success");
      this.is_showed = true;
    },
    // 個別通話の着信
    on_call_from_remote(msg) {
      if (msg.type == "call_video") {
        this.$store.state.sound_on();
        this.user = this.users[msg.src];
        this.user_name = this.user.user_name;
        this.remote_id = msg.src;
        this.is_callee_dialog_visible = true;
        this.media_type = "video";
      } else if (msg.type == "call_audio") {
        this.$store.state.sound_on();
        this.user = this.users[msg.src];
        this.user_name = this.user.user_name;
        this.remote_id = msg.src;
        this.is_callee_dialog_visible = true;
        this.media_type = "audio";
      } else if (msg.type == "call_end") {
        this.on_call_end(msg.src);
      }
    },
    call_accept() {
      // メモ画面クローズ
      memoEvent.$emit("ALL_CLOSE_MEMO");

      // 個別通話発信画面を閉じる
      this.$store.commit("setViewLayersDevice", false);
      this.$store.commit("resetViewLayersDeviceInfo");

      this.$store.state.sound_on();
      this.is_callee_dialog_visible = false;
      this.$store.commit("setPer", "person");
      // this.$refs.video_unit.start(this.media_type);
      this.is_showed = true;
      setTimeout(() => {
        this.$refs.remoteVideo.start(this.media_type);
      }, 200);
    },
    call_end(id) {
      this.$store.state.sound_on();
      const send_msg = {
        type: "call_end",
        dest: this.remote_id,
        src: this.myId,
      };
      this.socketio.emit("publish", send_msg);
      this.on_call_end(id);
    },
    on_call_end(id) {
      this.is_callee_dialog_visible = false;
      this.$store.state.toast_on("個別通話を終了します。");
      this.media_type = null;
      this.$store.commit("setPer", "group");
      // this.$refs.video_unit.on_stop();
      // this.$refs.remoteVideo.stop();
      this.user_name = "";
      this.remote_id = null;
      this.is_showed = false;
    },
  },
  computed: {
    socketio() {
      return this.$store.getters["socketio"];
    },
    users() {
      return this.$store.getters["amt/users"];
    },
    myId() {
      return this.$store.getters["amt/myId"];
    },
    myPosition() {
      return this.$store.getters["amt/myPosition"];
    },
    // personal_remote_id() {
    //   return this.$store.getters["rtc/personal_id"];
    // }
  },
  beforeDestroy() {
    this.is_showed = false;
    this.socketio.off("publish", this.on_call_from_remote);
  },
};
</script>

