import $store from "@/store";
import ShareComponent from "./ShareComponent.vue";
// import { P2G } from "./p2g";

import Vue from "vue";
export const shareEvent = new Vue();

export function ShareLayer() {
  const noop = () => {};

  let infos = {};
  let isActive = false;

  function add() {
    // 他の端末から共有が始めれた場合。
    $store.state.socketio.on("share", (msg) => {
      // share通知中の送信元が、共有オーナーだった場合には、共有自体を終了する。
      const data = JSON.parse(msg);

      // グループが違っていたら処理しないで抜ける。
      if (data.group_id != $store.state.activeId.id) return;

      // 既にshare_ownerの登録がある場合、セッション中と判断して、STARTは受けない

      // share通知（type=START）を受けると、送信元を共有オーナとして保存して、共有機能をロードする。
      if (data.type == "START" && !get_info("share_owner")) {
        infos["share_owner"] = data.src;
        $store.commit("amt/addComponent", ShareComponent);

        // 共有オーナからのshare通知（type=END）を受けると、共有機能を削除する。
      } else if (data.type == "END" && data.src == get_info("share_owner")) {
        $store.commit("amt/removeComponent", ShareComponent);
        infos = {};
      }
    });
  }

  function get_info(item) {
    return infos ? infos[item] : null;
  }

  function setActive(active) {
    const users = $store.getters["amt/users"];
    const myId = $store.getters["amt/myId"];
    const my_group_id =
      $store.state.activeId.kind == "group" ? $store.state.activeId.id : null;

    isActive = active;

    Object.keys(users).forEach((id) => {
      const data = {
        dest: id,
        src: myId,
        group_id: my_group_id,
        type: active ? "START" : "END",
      };
      if (id != myId) {
        $store.state.socketio.emit("share", JSON.stringify(data));
      }
    });
    if (active) {
      infos["share_owner"] = myId;
      infos["group_id"] = my_group_id;
      $store.commit("amt/setSubMenu", "ShareLayer");
      $store.commit("amt/addComponent", ShareComponent);
    } else {
      $store.commit("amt/setSubMenu", null);
      $store.commit("amt/removeComponent", ShareComponent);
      infos = {};
    }
  }

  function getActive() {
    return isActive;
  }

  return {
    add, // マップ初期化時に呼び出し
    get_info,
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity: noop, // 現在のバージョンでは呼び出されない
    setVisible: noop, // 現在のバージョンでは呼び出されない
    setActive, // メニュー操作でトグル
    getActive,
  };
}
