<template>
  <div class="base" ref="chatLayer" v-if="isVisible">
    <div class="input_box">
      <input type="text" ref="sendMsg" class="sendMsg" placeholder="メッセージ" @keypress.enter="send" />
      <div class="btn_send" @click="send">
        <img class="btn_send_img" :src="btn_send_img" />
      </div>
    </div>
    <div class="balloon_box" ref="balloon_box">
      <balloon
        v-for="(chatMessage, index) in chatMessages"
        :key="index"
        :msg="chatMessage.msg"
        :id="chatMessage.id"
        :name="chatMessage.name"
        :time="chatMessage.time"
        :nack="chatMessage.nack"
        :href="chatMessage.href"
      ></balloon>
    </div>
  </div>
</template>

<script>
import { chatEvent } from "./index.js";
import Balloon from './BalloonComp.vue';
const api_url = "/api/chat"

export default {
  components: {
    balloon: Balloon
  },
  data() {
    return {
      chatLayer: null,
      isVisible: false,
      isActive: false,
      chatMessages: [],
      btn_send_img: require("../../assets/paper_airplane.png")
    };
  },
  watch: {
    group_id() {
      this.init();
    }
  },
  computed: {
    users() {
      return this.$store.getters["amt/users"];
    },
    myId() {
      return this.$store.state.myId;
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
    socketio() {
      return this.$store.getters["socketio"];
    },
  },
  methods: {
    async init() {
      console.log("HELLO this is Chat Module")
      const method = "POST";
      const body = JSON.stringify({
        id: this.myId,
        group_id: this.group_id
      });
      const headers = {
        'Accept': 'application/json',
      };
      try {
        const res = await fetch(api_url, {method, headers, body});
        const talks = await res.json();
        console.log(talks);
        this.chatMessages = talks;
      } catch (err) {
        console.log(err);
      }
    },
    send() {
      // 空っぽは送信しない。
      if (this.$refs.sendMsg.value === "") return;

      const myMsg = {
        id: this.myId,
        name: this.users[this.myId].user_name,
        group_id: this.group_id,
        time: Date.now(),
        msg: this.$refs.sendMsg.value,
        nack: true
      }
      this.socketio.emit("chat_msg", myMsg);
      // myMsg.nack = true;
      this.chatMessages.push(myMsg);
      this.$refs.sendMsg.value = "";
      this.$nextTick(() => {
        this.$refs.balloon_box.scrollTop = this.$refs.balloon_box.scrollHeight;
      })
    },
    recv(msg) {
      // 自分のメッセージだったら
      if (msg.id === this.myId) {
        // 初めからnackがfalseの場合、音声認識処理なので、直ぐに登録する。
        if (!msg.nack) {
          this.chatMessages.push(msg);
        } else {
          const  index = this.chatMessages.findLastIndex(chatMsg => {
            return chatMsg.id === msg.id && chatMsg.time === msg.time
          });
          if (!isNaN(index) && this.chatMessages[index]) {
            this.chatMessages[index].nack = false;
          }
        }
      } else {
        // 他人のメッセージの場合、直ぐに登録
        msg.nack = false;
        this.chatMessages.push(msg);
      }
      this.$nextTick(() => {
        this.$refs.balloon_box.scrollTop = this.$refs.balloon_box.scrollHeight;
      })
    },
  },
  mounted() {
    console.log(
      `chatLayerが読み込まれました。${new Date().toLocaleTimeString()}`
    );
    chatEvent.$on("SETOPACITY", (val) => {
      this.$refs.chatLayer.style.opacity = val;
    });
    chatEvent.$on("SETVISIBLE", (val) => {
      this.isVisible = val;
    });
    chatEvent.$on("SETZINDEX", (val) => {
      this.$refs.chatLayer.style.zIndex = val;
    });
    chatEvent.$on("SETACTIVE", (val) => {
      this.isActive = val;
      if (val) {
        if (!this.isVisible) {
          this.isVisible = true;
        }
      }
    });
    this.socketio.on("chat_msg", this.recv);
    this.init();
  },
  beforeDestroy() {
    this.socketio.off("chat_msg", this.recv);
  },
};
</script>

<style scoped>
/* --------------Remote Box-------------------- */
/* 縦向きの場合 */
@media screen and (orientation: portrait) {
  .base {
    z-index: 4;
    position: absolute;
    top: 20px;
    left: 0px;
    width: 100%;
    height: 70vh;
    background-color: whitesmoke;
    max-height: 60vh;
  }
}
/* 横向きの場合 */
@media screen and (orientation: landscape) {
  .base {
    z-index: 4;
    position: absolute;
    top: 20px;
    left: 0px;
    width: 100%;
    height: 70vh;
    background-color: whitesmoke;
    max-height: 60vh;
  }
}

.input_box {
  /* position: relative; */
  display: flex;
  justify-content: center;
  width: 100%;
  height: 7vh;
  min-height: 50px;
  padding: 5px 0px 5px 0px;
  background-color: #e0e0e0;
}
.balloon_box {
  /* position: relative; */
  width: 100%;
  height: 85%;
  overflow-y: scroll;
  overflow-x: hidden;
}
input[type="text"]{
  border-radius: 10px;
  margin: 5px;
  border: 0.5px;
  line-height: 1.5rem;
  font-size: 1.2rem;
  flex-basis: 60%;
}
.btn_send {
  background-color: #5AC323;
  border-radius: 10px;
  border: 0px;
  margin: 5px;
  flex-basis: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn_send_img {
  margin: auto;
  height: 80%;
}
.balloon_box::-webkit-scrollbar {
  width: 9px;
}
.balloon_box::-webkit-scrollbar-track {
  background-color: #e4e4e4;
  border-radius: 100px;
}
.balloon_box::-webkit-scrollbar-thumb {
  background-color: #9b9a98;
  border-radius: 100px;
}

</style>
