<template>
  <div id="levelMeter">
    <div>
      <svg
        x="0"
        y="0"
        width="100%"
        height="100%"
        style="background-color: rgba(255, 255, 255, 0.05)"
        viewBox="0 0 100 3"
        preserveAspectRatio="none"
      >
        <linearGradient
          gradientUnits="userSpaceOnUse"
          id="gradation2"
          x1="0"
          y1="0"
          x2="100"
          y2="0"
          spreadMethod="pad"
        >
          <stop offset="0%" stop-color="#afc6e8" />
          <stop offset="100%" stop-color="#2e5bcc" />
          <!-- <stop offset="0%" stop-color="#B7C7E9" />
          <stop offset="100%" stop-color="#22b2cc" /> -->
        </linearGradient>
        <rect
          x="0"
          y="0"
          v-bind:width="draw_level"
          height="3"
          fill="url(#gradation2)"
        />
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      draw_level: 0,
      clearId: {},
    };
  },
  props: ["audio_level"],
  mounted() {
    this.clearId["level"] = setInterval(() => {
      this.draw_level = this.audio_level * 100;
    }, 200);
  },
  beforeDestroy() {
    Object.keys(this.clearId).forEach((key) => {
      clearInterval(this.clearId[key]);
    });
  },
};
</script>

