import $store from "@/store";
import RTCGroup from "./RTCGroup.vue";
import RTCPrivate from "./RTCPrivate.vue";

import Vue from "vue";
export const rtcEvent = new Vue();

export function RTCLayer() {
  const noop = () => {};

  function add() {
    // setActive(true);
  }

  function setActive(active) {
    if (active) {
      // $store.commit("amt/setSubMenu", "RTCLayer");
      $store.commit("amt/addComponent", RTCGroup);
      $store.commit("amt/addComponent", RTCPrivate);
    } else {
      // $store.commit("amt/setSubMenu", null);
      $store.commit("amt/removeComponent", RTCGroup);
      $store.commit("amt/removeComponent", RTCPrivate);
    }
  }

  return {
    add, // マップ初期化時に呼び出し
    get_info: noop,
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity: noop, // 現在のバージョンでは呼び出されない
    setVisible: noop, // 現在のバージョンでは呼び出されない
    setActive, // メニュー操作でトグル
  };
}
