<template>
  <div>
    <div id="searchComponentSmall" v-if="isSearchComponentSmall">
      <button v-on:click="showSearchLayer" class="circleBtn" title="拡大">
        <img src="./assets/square.png" />
      </button>
      <button v-on:click="closeSearchLayer" class="circleBtn" title="閉じる">
        <img src="./assets/close.png" />
      </button>
    </div>
  </div>
</template>

<script>
import { searchEvent } from "./index.js";

export default {
  methods: {
    closeSearchLayer() {
      // ☓ボタンが押されたら閉じる
      this.$store.dispatch("amt/hideSearchComponentSmall");
      searchEvent.$emit("close");
    },
    showSearchLayer() {
      // 最大化ボタンが押されたら検索画面を表示する
      this.$store.dispatch("amt/showSearchComponent");
      this.$store.dispatch("amt/hideSearchComponentSmall");
    },
  },
  computed: {
    isSearchComponentSmall: function () {
      return this.$store.getters["amt/isSearchComponentSmall"];
    },
  },
};
</script>

<style>
#searchComponentSmall {
  width: 4rem;
  height: 8rem;
  background-color: #37a3c0;
  border: none;
  border-radius: 5rem;
  -moz-border-radius: 5rem;
  -webkit-border-radius: 5rem;
  color: white;
  outline: none !important;
  transition: background-color, color 0.7s ease-in;
  user-select: none;

  position: fixed;
  right: 15px;
  bottom: 100px;
  z-index: 10;
}
