/* eslint-disable */
export default {
  namespaced: true,
  state: {
    touchPTT: false,
    nativePTT: false,
    keyPTT: false,
    peers: -1,
    audioDests: [], // 送信先 IDのリスト
    audioSrc: "", // 送信元の名称（文字列）--> 送信元のIDに変更
    recv_active: false,
    count: 0,
    connectionState: null,
    trackState: false,
    ptt_keycode: 0,
    rtt_alert: false,
    devices_len: 0,
    local_audio_stream: null, // MIC input
    remote_audio_stream: null, // Speacker output
  },
  getters: {
    pttState(state) {
      return state.touchPTT || state.nativePTT || state.keyPTT;
    },
    pttEnabled(state, gtters, rootState, rootGetters) {
      // return state.peers > 0;
      // return true;
      // return state.audioDests.length > 0;
      return rootGetters.users_length > 0;
    },
    pttActive(state, getters) {
      return getters.pttState && getters.pttEnabled;
    },
    pttRecv(state, getters) {
      return state.recv_active;
    },
    peerState(state, getters) {
      return state.connectionState;
    },
    peerTrackState(state, getters) {
      return state.trackState;
    },
    callStatus(state, getters, rootState, rootGetters) {
      // if (state.peers === -1) {
      if (rootGetters.users_length === -1) {
        return "圏外";
      } else {
        return "通話可能";
      }
    },
    rtt_alert(state) {
      return state.rtt_alert;
    },
    devices_len(state) {
      return state.devices_len;
    },
    local_audio_stream(state) {
      return state.local_audio_stream;
    },
    remote_audio_stream(state) {
      return state.remote_audio_stream;
    },
  },
  mutations: {
    touchPTT(state, data) {
      state.touchPTT = data;
    },
    nativePTT(state, data) {
      state.nativePTT = data;
    },
    keyPTT(state, data) {
      state.keyPTT = data;
    },
    peers(state, data) {
      state.peers = data;
    },
    set_audioDests(state, data) {
      state.audioDests = data;
    },
    set_ontrack_src(state, data) {
      state.audioSrc = data;
    },
    set_recv_active(state, data) {
      state.recv_active = data;
    },
    set_connectionState(state, data) {
      state.connectionState = data;
    },
    set_trackState(state, data) {
      state.trackState = data;
    },
    set_rtt_over(state, data) {
      state.rtt_alert = data;
    },
    set_devices_len(state, data) {
      state.devices_len = data;
    },
  },
  actions: {
    // PTTイベントハンドラを登録する
    initPTT({ commit, state }) {
      // Androidネイティブアプリからのプレス状態通知
      window.onPressStatusChanged = (data) => {
        commit("nativePTT", data);
      };

      // スペースキーDOWN/UPイベント
      // テキスト編集中のキー入力と区別するため、textareaとinputタグで発生した
      // イベントは無視する
      // キーリピートでもDOWNイベント発生するが、イベントのプロパティで区別可能
      document.addEventListener(
        "keydown",
        (e) => {
          const textInput = e.target.closest('textarea, input[type="text"]');
          if (!textInput && e.keyCode === state.ptt_keycode && !e.repeat) {
            document.activeElement.blur();
            commit("keyPTT", true);
          }
        },
        false
      );
      document.addEventListener(
        "keyup",
        (e) => {
          const textInput = e.target.closest('textarea, input[type="text"]');
          if (!textInput && e.keyCode === state.ptt_keycode && !e.repeat) {
            commit("keyPTT", false);
          }
        },
        false
      );

      var hidden, visibilityChange;
      if (typeof document.hidden !== "undefined") {
        hidden = "hidden";
        visibilityChange = "visibilitychange";
      } else if (typeof document.mozHidden !== "undefined") {
        hidden = "mozHidden";
        visibilityChange = "mozvisibilitychange";
      } else if (typeof document.msHidden !== "undefined") {
        hidden = "msHidden";
        visibilityChange = "msvisibilitychange";
      } else if (typeof document.webkitHidden !== "undefined") {
        hidden = "webkitHidden";
        visibilityChange = "webkitvisibilitychange";
      }
      // ページ非表示と表示時に呼ばれる
      document.addEventListener(
        visibilityChange,
        function () {
          if (document.hidden) {
            commit("touchPTT", false);
            commit("keyPTT", false);
          }
        },
        false
      );
    },
    // Web画面のPTTをタッチ
    touchStart({ commit }) {
      commit("touchPTT", true);
    },
    // Web画面のPTTをリリース
    touchEnd({ commit }) {
      commit("touchPTT", false);
    },
  },
};
