import { sp_start, sp_stop } from "./sp_recognition";
import { memoEvent } from "./index.js";

const memo_common = {
  computed: {
    is_not_ios() {
      const ua = navigator.userAgent.toLowerCase();
      if (
        ua.indexOf("iphone") > -1 ||
        ua.indexOf("ipad") > -1 ||
        (ua.indexOf("macintosh") && document.ontouchend)
      ) {
        return false;
      } else {
        return true;
      }
    },
    local_audio_stream() {
      return this.$store.getters["rtc/local_audio_stream"];
    },
    local_video_stream() {
      return this.$store.getters["rtc/local_video_stream"];
    },
    mic_btn_color() {
      return { backgroundColor: this.mic_btn_flag ? "red" : "#37a3c0" };
    },
    mic_on_off() {
      return this.$store.getters["rtc/func_mic_on_off"];
    },
    is_front() {
      return this.$store.getters["rtc/is_front_cam"];
    },
  },
  methods: {
    is_left() {
      const rect = document.querySelector(".slides").getBoundingClientRect();
      const video_rect = document
        .getElementById("memo_video")
        .getBoundingClientRect();
      const center = (rect.right + rect.left) / 2;
      if (center < video_rect.right) {
        this.left_view = true;
      } else {
        this.left_view = false;
      }
    },
    on_click_mic() {
      if (this.mic_btn_flag) {
        sp_stop({
          next: async () => {
            await this.mic_on_off("start");
            this.mic_btn_flag = false;
            // this.init();
          },
        });
      } else {
        sp_start({
          speech_text: this.text_memo,
          before: async () => {
            await this.mic_on_off("stop");
            this.mic_btn_flag = true;
          },
          next: (text) => {
            this.text_memo = text;
          },
          end: () => {
            sp_stop({
              next: async () => {
                await this.mic_on_off("start");
                this.mic_btn_flag = false;
                document.getElementById("memo_text").blur();
                // this.init();
              },
            });
          },
          error: () => {
            console.log("sp_recognition ERROR");
          },
        });
      }
    },
    async close_button() {
      if (this.mic_btn_flag) {
        this.dialog_msg = "音声認識中ですが、終了させますか";
        this.is_dialog_visible = true;
      } else if (this.recorder && this.recorder.state == "recording") {
        this.dialog_msg = "録画中ですが、終了させますか";
        this.is_dialog_visible = true;
      } else {
        memoEvent.$emit("BACK_MEMO");
        this.init_clear();
      }
    },
    err_exit(message) {
      memoEvent.$emit("BACK_MEMO");
      this.init_clear();
      this.$store.commit("memo/set_error_dialog", {
        is_visible: true,
        msg: message,
      });
    },
    async init() {
      this.$nextTick(() => {
        if (this.$refs.video) {
          this.$refs.video.srcObject = this.local_video_stream;
        }
      });
      setTimeout(() => {
        const txt_elm = document.querySelector(".text_fade");
        if (txt_elm) {
          txt_elm.classList.add("fade");
        }
      }, 1000);
    },
    gen_file_name() {
      const _d = new Date().toLocaleString().split(/\/|:|\s/gi);
      const cur_date = `${_d[0]}年${_d[1]}月${_d[2]}日${_d[3]}時${_d[4]}分${_d[5]}秒`;
      if (this.last_date == cur_date) {
        this.count_date++;
      } else {
        this.count_date = 0;
      }
      this.last_date = cur_date;
      return `${this.$store.state.myName}_${cur_date}_${this.count_date}`;
    },
    post_file(file_name, b64) {
      let formData = new FormData();
      formData.append("data", b64, encodeURIComponent(file_name));
      console.log(...formData.entries());

      fetch(this.url, {
        method: "post",
        mode: "no-cors",
        body: formData,
      })
        .then((response) => {
          if (response.status == 200) {
            console.log(response);
          } else {
            this.err_exit(
              "ファイルの転送に失敗しました。再度地点メモの登録をお願いします。"
            );
          }
        })
        .catch((error) => {
          this.err_exit(`${error}:ファイルの転送に失敗しました`);
        });
    },
    toBlob(base64, myType) {
      // Base64からバイナリへ変換
      var bin = atob(base64.replace(/^.*,/, ""));
      var buffer = new Uint8Array(bin.length);
      for (var i = 0; i < bin.length; i++) {
        buffer[i] = bin.charCodeAt(i);
      }
      // Blobを作成
      var blob = new Blob([buffer.buffer], { type: myType });
      return blob;
    },
    drawCanvas(img) {
      this.$parent.show_photo = true;
      this.$parent.show_video = false;
      const canvas = document.createElement("canvas");
      //-----------------------------------------------------------
      // サイズ縮小
      // 縦横で長い方を2048とする。
      // そもそも、画像の長径が2048以下の場合、サイズ圧縮しない
      //-----------------------------------------------------------
      const IMG_LENGTH = 2048;
      if (img.width > img.height) {
        if (img.width < IMG_LENGTH) {
          canvas.width = img.width;
          canvas.height = img.height;
        } else {
          canvas.width = IMG_LENGTH;
          canvas.height = img.height * (IMG_LENGTH / img.width);
        }
      } else {
        if (img.height < IMG_LENGTH) {
          canvas.width = img.width;
          canvas.height = img.height;
        } else {
          canvas.height = IMG_LENGTH;
          canvas.width = img.width * (IMG_LENGTH / img.height);
        }
      }
      canvas.getContext("2d").clearRect(0, 0, canvas.width, canvas.height);
      canvas
        .getContext("2d")
        .drawImage(
          img,
          0,
          0,
          img.width,
          img.height,
          0,
          0,
          canvas.width,
          canvas.height
        );

      let b64 = canvas.toDataURL("image/jpeg", 0.75); // 画質0.75
      b64 = this.toBlob(b64, this.myType);
      return b64;
    },
    // async imageCaptureTakePhoto(stream) {
    async imageCaptureTakePhoto(stream, canvas, video) {
      // FirefoxでImageCaptureが使えない問題の対処
      // if ("ImageCapture" in window) {
      //   // Chromeは使える
      //   const track = stream.getVideoTracks()[0];
      //   this.imageCapture = new ImageCapture(track);
      //   return this.imageCapture.takePhoto();
      // } else {
      if ("ImageCapture" in window) {
        const track = stream.getVideoTracks()[0];
        const imageCapture = new ImageCapture(track);
        const imageBitmap = await imageCapture.grabFrame();
        // const canvas = document.createElement("canvas");
        const context = canvas.getContext("2d");

        canvas.width = imageBitmap.width;
        canvas.height = imageBitmap.height;
        return new Promise((resolve, reject) => {
          try{
            context.drawImage(imageBitmap, 0, 0, imageBitmap.width, imageBitmap.height);
            canvas.toBlob(resolve, "image/jpeg");
          } catch (err) {
            reject(err);
          }
        })
      } else {
        // Firefoxは使えない
        // const video = document.createElement("video");
        // const canvas = document.createElement("canvas");
        const context = canvas.getContext("2d");
        video.srcObject = stream;
        // video.playsInline = true;
        // video.muted = true;
        return new Promise((resolve, reject) => {
          video.addEventListener("loadeddata", async () => {
            const { videoWidth, videoHeight } = video;
            canvas.width = videoWidth;
            canvas.height = videoHeight;
            try {
              await video.play();
              context.drawImage(video, 0, 0, videoWidth, videoHeight);
              canvas.toBlob(resolve, "image/jpeg");
            } catch (error) {
              reject(error);
            }
          });
        });
      }
    },
  },
  async beforeDestroy() {
    // 音声認識が動いていたら停止させる。
    if (this.mic_btn_flag) {
      this.mic_btn_flag = false;
      sp_stop();
      await this.mic_on_off("start");
    }
    if (this.recorder && this.recorder.state == "recording") {
      this.recorder.stop();
    }
    this.init_clear();
  },
};

export { memo_common };
