import $store from "@/store";
import DrawComponent from "./DrawComponent.vue";

import Vue from "vue";
export const drawEvent = new Vue();

export function DrawLayer() {
  const noop = () => {};

  let isActive = false;

  function add() {
    // this.initDraw();
    $store.commit("amt/addComponent", DrawComponent);
  }

  function setActive(active) {
    isActive = active;

    if (active) {
      drawEvent.$emit("SETACTIVE", active);
      $store.commit("amt/setSubMenu", "DrawLayer");

      // 手書きモードに入る時は地図上のレイヤも有効化
      drawEvent.$emit("SETVISIBLE", active);
      // $store.commit("amt/addComponent", DrawComponent);
    } else {
      drawEvent.$emit("SETACTIVE", active);
      $store.commit("amt/setSubMenu", null);
      // $store.commit("amt/removeComponent", DrawComponent);
    }
  }

  function getActive() {
    return isActive;
  }

  function setOpacity(val) {
    drawEvent.$emit("SETOPACITY", val);
  }

  function setVisible(val) {
    drawEvent.$emit("SETVISIBLE", val);
  }

  function setZIndex(val) {
    drawEvent.$emit("SETZINDEX", val);
  }

  return {
    add, // マップ初期化時に呼び出し
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity,
    setVisible,
    setZIndex,
    setActive, // メニュー操作でトグル
    getActive,
  };
}
