<template>
  <div>
    <div class="waku1">
      <video
        v-show="is_local_video_showed"
        ref="localVideo"
        muted
        autoplay
        playsinline
        @click="toggle"
        class="my_video_box"
        :class="{ front_class: is_front }"
      ></video>
      <button
        v-show="is_showed_rec_btn"
        class="btn-rec"
        :class="{ btnOnRec: rec_flag }"
        @click="on_rec"
      >
        {{ rec_flag ? "NOW ON REC" : "REC" }}
      </button>
      <div class="local_settings">
        {{ settings_height }}@{{ settings_frameRate }}fps
      </div>
    </div>
    <canvas ref="dummyVideo" class="dummyVideo"></canvas>
    <!-- メッセージ用モーダルダイアログ -->
    <modalDialog v-show="is_dialog_visible">
      <span slot="header">カメラを選択してください </span>
      <span slot="body">
        <ul class="list-group">
          <li
            class="list-group-item"
            :class="
              video_device.deviceId == cur_constraints.video.deviceId
                ? 'active'
                : ''
            "
            v-for="(video_device, index) in video_devices"
            :key="video_device.devideId"
            @click="select_device(index)"
          >
            {{ video_device.label }}
          </li>
        </ul>
      </span>
      <div slot="footer">
        <!-- <button class="btn btn-primary" @click="close_msg_dialog">はい</button> -->
      </div>
    </modalDialog>
  </div>
</template>

<!--
StreamIo.vue
(1)ローカル側のメディアデバイスからストリームを取得する。
(2)取得後、ストリームを$store.state.rtcへ登録し、外部コンポーネントから利用する。
(3)取得後、映像のみ、video要素に投影する。
   このvideo要素は、非表示時、投影を停止する。
   クリックで、カメラの前面/背面の切替を行う。
-->
<script>
/* eslint-disable */
import ModalDialog from "@/components/ModalDialog.vue";
import { trackEvent } from "../modules/TrackLayer";

const URL_UPLOAD = "/api/callrec";
const URL_FILE_NAME = "/api/on_video";
const TIMESLICE = 1000;

export default {
  components: {
    ModalDialog,
  },
  data() {
    return {
      facingModeId: null,
      userConstraints: {
        video: {
          facingMode: "user",
          width: this.video_width,
          frameRate: this.video_frame,
        },
        audio: true,
      },
      envConstraints: {
        video: {
          facingMode: "environment",
          width: this.video_width,
          frameRate: this.video_frame,
        },
        audio: true,
      },
      theta_constraints: {
        audio: true,
        video: {
          width: 3840,
          frameRate: 30,
        },
      },
      cur_constraints: {
        audio: true,
        video: {
          width: this.video_width,
          frameRate: this.video_frame,
        },
      },
      audio_stream: null,
      video_stream: null,
      video_devices: [],
      dummy_video_stream: null,
      is_dialog_visible: false,
      recorder: null,
      rec_flag: false,
      timestamp: "",
      last_timestamp: "",
      last_last_timestamp: "",
      local_settings: null,
      clear_id: {},
    };
  },
  beforeDestroy() {
    Object.keys(this.clear_id).forEach((id) => {
      clearInterval(this.clear_id[id]);
    });
  },
  async mounted() {
    await this.get_stream(this.userConstraints);
    this.whiteNoise();
    this.$store.commit("rtc/set_func_toggole_cam", this.toggle);
    this.$store.commit("rtc/set_func_mic_on_off", this.mic);

    trackEvent.$on("ON_TIME", ({ timestamp, last_timestamp }) => {
      this.timestamp = timestamp;
      this.last_timestamp = last_timestamp;
    });
    trackEvent.$on("ON_VIDEO", () => {
      console.log(`録画開始：${this.myId}_${this.last_timestamp}.webm`);
      if (this.recorder) {
        if (this.recorder.state === "recording") this.recorder.stop();
        if (this.rec_flag) this.recorder.start(TIMESLICE);
      }
    });
    this.clear_id["video_settings"] = setInterval(() => {
      if (this.$refs.localVideo && this.$refs.localVideo.srcObject) {
        const [track] = this.$refs.localVideo.srcObject.getVideoTracks();
        this.local_settings = track.getSettings();
      }
    }, 1000);
  },
  computed: {
    settings_height() {
      return this.local_settings ? this.local_settings.height : 0;
    },
    settings_frameRate() {
      return this.local_settings
        ? Math.floor(this.local_settings.frameRate)
        : 0;
    },
    is_front() {
      return this.cur_constraints.video.deviceId === this.facingModeId;
    },
    is_local_video_showed() {
      return this.$store.getters["rtc/local_show"];
    },
    video_devices_len() {
      return this.$store.getters["ptt/devices_len"];
    },
    video_width() {
      return this.$store.getters["video_width"];
    },
    video_frame() {
      return this.$store.getters["video_frame"];
    },
    socketio() {
      return this.$store.getters["socketio"];
    },
    myId() {
      return this.$store.getters["amt/myId"];
    },
    on_chronology() {
      return this.$store.getters["on_chronology"];
    },
    is_showed_rec_btn() {
      return this.is_local_video_showed || this.on_chronology ? true : false;
    },
  },
  watch: {
    is_local_video_showed() {
      if (this.is_local_video_showed) {
        this.$refs.localVideo.srcObject = this.video_stream;
      } else {
        this.$refs.localVideo.srcObject = null;
      }
    },
  },
  methods: {
    whiteNoise() {
      const canvas = this.$refs.dummyVideo;
      const ctx = canvas.getContext("2d");
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      let p = ctx.getImageData(0, 0, canvas.width, canvas.height);
      requestAnimationFrame(function draw() {
        for (var i = 0; i < p.data.length; i++) {
          p.data[i++] = p.data[i++] = p.data[i++] = Math.random() * 255;
        }
        ctx.putImageData(p, 0, 0);
        requestAnimationFrame(draw);
      });
      this.$store.commit(
        "rtc/set_dummy_video_stream",
        canvas.captureStream(20)
      );
    },
    async re_get_stream() {
      if (!this.video_stream.active) {
        await this.stop_stream();
        await this.get_stream(this.userConstraints);
      }
    },
    async get_stream(construction) {
      console.log("★★★★★★★★★  GET_STREAM  ★★★★★★★★★★★★★");
      console.dir(construction);

      try {
        if (construction.video) {
          const video_construction = {
            video: construction.video,
            audio: false,
          };
          this.video_stream = await navigator.mediaDevices.getUserMedia(
            video_construction
          );
          this.$refs.localVideo.srcObject = this.video_stream;
          const [video_track] = this.video_stream.getVideoTracks();
          video_track.contentHint = "detail";
          const video_settings = video_track ? video_track.getSettings() : null;

          // 現状のカメラ設定を保存します。
          // this.cur_constraints.video = video_settings
          //   ? {
          //       width: video_settings.width,
          //       frameRate: video_settings.frameRate,
          //       deviceId: video_settings.deviceId,
          //     }
          //   : false;
          // 自カメラ映像の左右反転をするのが、user。それ以外はenvironmentとする
          // 渡されるパラメータでfacingModeがuserの場合には、そのdeviceIdとの紐づけをする
          if (construction.video.facingMode === "user") {
            this.facingModeId = video_settings.deviceId;
          }
          // this.$store.commit("rtc/set_local_video_stream", this.video_stream);
          this.$store.commit(
            "rtc/set_local_video_stream",
            new MediaStream([video_track])
          );
        }
      } catch (err) {
        this.$store.commit("rtc/set_local_video_stream", new MediaStream([]));
        console.dir(construction);
        console.log(err);
      }

      try {
        if (construction.audio) {
          const audio_construction = {
            video: false,
            audio: construction.audio,
          };
          this.audio_stream = await navigator.mediaDevices.getUserMedia(
            audio_construction
          );
          const [audio_track] = this.audio_stream.getAudioTracks();
          const audio_settings = audio_track ? audio_track.getSettings() : null;
          this.cur_constraints.audio = audio_settings ? true : false;
          this.$store.commit("rtc/set_local_audio_stream", this.audio_stream);
        }
      } catch (err) {
        this.$store.commit("rtc/set_local_audio_stream", new MediaStream([]));
        console.dir(construction);
        console.log(err);
      }
    },
    stop_stream(kind) {
      return new Promise((resolev, reject) => {
        // const stream = this.$refs.localVideo.srcObject;
        const stream = kind == "audio" ? this.audio_stream : this.video_stream;
        stream.getTracks().forEach((track) => {
          track.stop();
        });
        if (kind != "audio") {
          this.$refs.localVideo.srcObject = null;
        }
        resolev();
      });
    },
    // カメラの前面/背面を切り替え
    async toggle_cam(face_mode) {
      if (!face_mode) {
        const device_list = await navigator.mediaDevices.enumerateDevices();
        this.video_devices = device_list.filter((device) => {
          return device.kind == "videoinput";
        });
        console.log(`カメラの台数＝${this.video_devices.length}`);
        console.dir(this.video_devices);

        // まず、現在の選択デバイスを取得する。
        const cur_deviceId = this.video_stream
          .getTracks()[0]
          .getSettings().deviceId;
        delete this.cur_constraints.video.facingMode;

        // カメラが1台だった場合、何もしない。
        if (this.video_devices.length == 2) {
          // カメラが2台だった場合、現在のカメラじゃない方をnext_deviceとして拾う。
          const [next_device] = this.video_devices.filter((video_device) => {
            return video_device.deviceId !== cur_deviceId;
          });
          this.cur_constraints.video.deviceId = next_device.deviceId;
        } else if (this.video_devices.length > 2) {
          // カメラが3台以上の場合、ダイアログを開いて選択させる。
          this.is_dialog_visible = true;
        }
      } else {
        // 指定があれば、指定を入力する。
        this.cur_constraints.video.facingMode = face_mode;
      }
      await this.stop_stream();
      // return await this.get_stream(this.cur_constraints);
      await this.get_stream(this.cur_constraints);
    },
    close_msg_dialog() {
      this.is_dialog_visible = false;
    },
    async select_device(index) {
      await this.stop_stream();
      if (this.video_devices[index].label === "RICOH THETA UVC") {
        this.theta_constraints.video.deviceId =
          this.video_devices[index].deviceId;
        await this.get_stream(this.theta_constraints);
      } else {
        this.cur_constraints.video.deviceId =
          this.video_devices[index].deviceId;
        await this.get_stream(this.cur_constraints);
      }
      // this.$refs.localVideo.srcObject = await this.get_stream(
      //   this.cur_constraints
      // );
      this.is_dialog_visible = false;
    },
    async toggle(ev) {
      this.$store.state.sound_on();
      // this.$refs.localVideo.srcObject = await this.toggle_cam();
      await this.toggle_cam();
    },
    async mic(mode) {
      if (mode == "stop") {
        await this.stop_stream("audio");
        this.cur_constraints.audio = false;
      } else {
        this.cur_constraints.audio = true;
      }
      await this.get_stream(this.cur_constraints);
      // return await this.get_stream(this.cur_constraints);
    },
    async on_rec() {
      // 録画開始（rec_flag=trueで開始。falseで停止
      this.rec_flag = !this.rec_flag;
      console.log(`ON_REC: ${this.rec_flag}`);

      if (this.rec_flag) {
        // 録画初期化（MediaRecorderの準備）
        this.init_rec(this.video_stream);
        this.recorder.start(TIMESLICE);
      } else {
        if (this.recorder && this.recorder.state === "recording") {
          this.recorder.stop();
        }
      }
      // 録画開始状況を皆にお知らせ
      this.$store.commit("set_rec_flag", this.rec_flag);
    },
    init_rec(stream) {
      if (!stream.active) return;

      let chunks = [];
      const myType = "video/webm";
      this.recorder = new MediaRecorder(stream);
      this.recorder.ondataavailable = (ev) => {
        chunks.push(ev.data);
      };
      this.recorder.onstart = async () => {
        const file_name = `${this.myId}_${this.timestamp}.webm`;
        const res = await fetch(URL_FILE_NAME, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            id: this.myId,
            on_video: true,
          }),
        });
      };
      this.recorder.onstop = async () => {
        let blob = new Blob(chunks, { type: myType });
        const _timestamp =
          this.last_timestamp === this.last_last_timestamp
            ? this.timestamp
            : this.last_timestamp;
        const file_name = `${this.myId}_${_timestamp}.webm`;
        if (blob.size > 8192) {
          try {
            const res = await this.post_file(file_name, blob);
          } catch (err) {
            console.log(err);
          }
        }
        if (!this.rec_flag) {
          const res = await fetch(URL_FILE_NAME, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              id: this.myId,
              on_video: false,
            }),
          });
        }
        chunks = [];
        this.last_last_timestamp = this.last_timestamp;
      };
      this.recorder.onerror = (ev) => {
        console.log(ev.error.name);
        this.$bvToast.toast(`録画にエラーが発生しました:${ev.error.name}`, {
          title: "AirMultiTalk",
          toaster: "b-toaster-top-center",
          variant: "danger",
          autoHideDelay: 3000,
          appendToast: false,
        });
      }
    },
    async post_file(file_name, b64) {
      let formData = new FormData();
      formData.append("data", b64, encodeURIComponent(file_name));
      console.log(...formData.entries());

      return fetch(URL_UPLOAD, {
        method: "post",
        mode: "no-cors",
        body: formData,
      });
    },
  },
};
</script>

<style>
