  /* スマホ */
  .my_video_box {
    width: 5rem;
  }
}
@media screen and (min-width: 480px) {
  /* スマホ */
  .my_video_box {
    width: 10rem;
  }
}
.local_settings {
  position: absolute;
  top: 0px;
  left: 0px;
  font-size: 1rem;
  color: white;
  background-color: black;
}
.my_video_box {
  position: relative;
}
/* .my_video_box {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
} */
.my_video {
  width: 100%;
  z-index: 3;
}
