  position: absolute;
  display: none;
  /* z-index: 0; */
  width: 320px;
  height: 240px;
}
.front_class {
  transform: scaleX(-1);
}
.back_class {
  transform: scaleX(1);
}
.waku1 {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 20;
}
.btn-rec {
  z-index: 20;
  /* left:  */
  bottom: 0px;
  position: relative;
  display: inline-block;
  padding: 7px 20px;
  border-radius: 25px;
  border: 0px;
  text-decoration: none;
  color: #fff;
  background-image: linear-gradient(45deg, #07b9ff 0%, #39a2c0 100%);
  transition: 0.4s;
}
/* .btn-rec:hover {
  background-image: linear-gradient(45deg, #FFC107 0%, #f76a35 100%);
} */
.btnOnRec {
  background-image: linear-gradient(45deg, #ffc107 0%, #ff8b5f 100%);
}
</style>
