<template>
  <div>
    <div
      ref="groupNameSel"
      v-on:click="toggleGroupSel"
      class="groupNameSel nosel text_over"
    >
      {{ selGroupName }}
    </div>
    <groupList
      v-if="groupListShowing"
      v-on:groupSelected="groupSelected"
    ></groupList>
  </div>
</template>

<script>
import GroupList from "../components/GroupList.vue";
export default {
  data() {
    return {
      // groupListShowing: false
    };
  },
  components: {
    groupList: GroupList,
  },
  methods: {
    logDebug(msg) {
      this.$store.state.logger.debug(msg);
    },
    log(msg) {
      this.$store.state.logger.info(msg);
    },
    logError(msg) {
      this.$store.state.logger.error(msg);
    },
    setGroupShow(on) {
      this.$store.commit("setGroupListShow", on);
      // this.groupListShowing = on;
      this.$store.commit("setMask", on);
      // const el = this.$refs.groupNameSel;
      if (on) {
        this.$refs.groupNameSel.style["z-index"] = 21;
      } else {
        this.$refs.groupNameSel.style["z-index"] = "";
      }
    },
    toggleGroupSel() {
      if (this.pttRecv) {
        this.at_pttRecv();
        return;
      }
      // 共有レイヤがonの時はグループの選択ができないように修正 @todo 共有レイヤが無い時の事を考慮
      if (this.$store.getters["amt/subMenu"] == "ShareLayer") {
        return;
      }
      this.setGroupShow(!this.$store.state.groupListShow);
    },
    groupSelected() {
      this.setGroupShow(false);
      // this.$emit("goupSelected");
    },
    at_pttRecv() {
      this.$bvToast.toast("現在着信です。", {
        title: "AirMultiTalk",
        toaster: "b-toaster-top-center",
        variant: "danger",
        autoHideDelay: 3000,
        appendToast: false,
      });
    },
  },
  computed: {
    pttRecv() {
      return this.$store.getters["ptt/pttRecv"];
    },
    selGroupName() {
      const activeId = this.$store.state.activeId;
      if (
        this.$store.state.phoneQuering != null &&
        this.phoneQueryCnt % 2 == 0
      ) {
        return this.$store.state.users[this.$store.state.phoneQuering]
          .user_name;
      } else if (activeId.kind == "group" && activeId.id != null) {
        return this.$store.state.groups.find((g) => g.group_id == activeId.id)
          .group_name;
      } else if (activeId.kind == "person" && activeId.id != null) {
        if (this.$store.state.users[activeId.id] != null) {
          return this.$store.state.users[activeId.id].user_name;
        } else {
          this.$store.commit("backActiveId");
          return "";
        }
      } else {
        return "選択なし";
      }
    },
    groupListShowing() {
      return this.$store.state.groupListShow;
    },
  },
};
</script>

<style>
.groupNameSel {
  /* position: absolute; */
  left: 0;
  /* top: 8vh; */
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
  height: 7rh;
  color: white;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
