<template>
  <div sytle="position: relative">
    <modalDialog v-if="show_daialog">
      <span slot="header"
        ><span style="color: red">{{ share_owner }}</span
        >より共有の開始が行われました。</span
      >
      <span slot="body">共有を開始しますか？</span>
      <div slot="footer">
        <button class="btn btn-primary" v-on:click="share_start_yes">
          開始
        </button>
        <button class="btn btn-primary" v-on:click="share_start_no">
          キャンセル
        </button>
      </div>
    </modalDialog>
    <!-- コンポーネント削除確認ダイアログ -->
    <modalDialog v-if="$store.getters['amt/exit_dialog']">
      <span slot="header">共有機能</span>
      <span slot="body">共有を停止しますか？</span>
      <div slot="footer">
        <button class="btn btn-primary" @click="exit_dialog_no()">
          キャンセル
        </button>
        <button class="btn btn-primary" @click="exit_dialog_yes()">停止</button>
      </div>
    </modalDialog>

    <div
      id="share_menu"
      v-show="!inSubMenu && menu_enable"
      class="share_submenu"
    >
      <div class="writeItem">
        <div>
          <button
            v-on:click="active_share_desktop"
            v-bind:class="isActive ? 'circleBtnSelected' : null"
            class="circleBtn subMenu"
            title="デスクトップ共有"
          >
            <img src="./assets/share_desktop@1x.png" />
          </button>
        </div>
      </div>
      <div class="writeItem">
        <div>
          <button
            class="circleBtn subMenu"
            v-on:click="active_share_pdf"
            title="PDF共有"
          >
            <img src="./assets/share_pdf@1x.png" />
          </button>
        </div>
      </div>
    </div>

    <div class="pdf_page_menu">
      <div id="pdf_page_menu" v-if="page_menu" style="display: flex">
        <div class="writeItem">
          <div>
            <button
              v-on:click="pdf.prev"
              v-bind:class="isActive ? 'circleBtnSelected' : null"
              class="circleBtn page_menu_left"
              title="前のページ"
            >
              <img src="./assets/share_back@1x.png" />
            </button>
          </div>
        </div>
        <div class="writeItem">
          <div>
            <button
              v-on:click="pdf.next"
              v-bind:class="isActive ? 'circleBtnSelected' : null"
              class="circleBtn page_menu_right"
              title="次のページ"
            >
              <img src="./assets/share_next@1x.png" />
            </button>
          </div>
        </div>
      </div>
      <div id="pdf_page_menu" v-if="scale_menu" style="display: flex">
        <!-- 縮小 -->
        <div>
          <div>
            <button v-on:click="scale_dec" class="circleBtn" title="縮小">
              <img src="./assets/share_zoom_out@1x.png" />
            </button>
          </div>
        </div>
        <!-- 拡大 -->
        <div>
          <div>
            <button v-on:click="scale_inc" class="circleBtn" title="拡大">
              <img src="./assets/share_zoom_in@1x.png" />
            </button>
          </div>
        </div>
      </div>
    </div>

    <canvasDraw
      ref="canvas_draw"
      v-show="inSubMenu"
      :w="w"
      :h="h"
      :page="page"
      @move="move"
    ></canvasDraw>
    <canvas ref="pdf_canvas" class="pdf_canvas" :style="canvas_style"></canvas>
    <video
      ref="desktop_video"
      v-show="show_video"
      class="desktop_video"
      :style="canvas_style"
      playsinline
    ></video>
  </div>
</template>

<script>
/* eslint-disable */
// import $store from "@/store";
import { shareEvent } from "./index.js";
import openPDF from "./openPDF";
import layerModules from "@/modules/layers";
import CanvasDraw from "./canvasDraw";
import ModalDialog from "@/components/ModalDialog.vue";
import { P2G } from "./p2g";

export default {
  data() {
    return {
      inSubMenu: false,
      page_menu: false,
      scale_menu: false,
      isActive: false,
      show_daialog: false,
      show_video: false,
      current_stream_src: null,
      pdf: null,
      pdf_ctx: null,
      p2g: null,
      playing: false,
      canvas_style: {
        top: "0px",
        left: "0px",
      },
      img: null,
      w: 300,
      h: 150,
      org_w: 300,
      org_h: 150,
      page: 1,
      scale: 1,
      scale_period: 0.1,
    };
  },
  components: {
    canvasDraw: CanvasDraw,
    modalDialog: ModalDialog,
  },
  mounted() {
    // layer.js中の自分
    this.share = layerModules.find((m) => m.name == "ShareLayer");
    // mapMenuの「メニューボタン」を「戻るボタン」にする。
    // this.$parent.$refs.mapMenu.inSubMenu = true;
    this.$store.commit("amt/setSubMenu", "ShareLayer");
    // 共有機能がonになった時はグループの変更ができないように
    this.$store.commit("setGroupListShow", false); // グループの選択リストを閉じる
    this.$store.commit("setMask", false); // マップにかぶさっているマスクを外す
    this.$parent.$refs.mapMenu.showMapMenu = true;
    // 共有開始のダイアログ表示
    this.show_daialog = true;

    this.p2g = new P2G(this.socketio);
    // this.p2g = new P2G();
    this.p2g.on("call_in", (stream) => {
      if (!this.$refs.desktop_video) return;
      this.inSubMenu = true;
      this.scale_menu = true;
      this.$refs.desktop_video.srcObject = stream;
      this.$refs.desktop_video.play().then(() => {
        this.playing = true;
        this.show_video = true;
      });
    });
    // 切断イベント着信による発火
    this.p2g.on("call_out", () => {});

    this.pdf_ctx = this.$refs.pdf_canvas.getContext("2d");
    this.socketio.on("share_draw", this.on_call_in_canvas);

    this.$refs.desktop_video.onresize = () => {
      this.w = this.org_w = this.$refs.desktop_video.clientWidth;
      this.h = this.org_h = this.$refs.desktop_video.clientHeight;
      console.log(
        `desktop_video: ON_RESIZE w=${this.$refs.desktop_video.clientWidth} h=${this.$refs.desktop_video.clientHeight}`
      );
      this.$nextTick(() => {
        this.$refs.canvas_draw.all_draw();
      });
    };
  },
  methods: {
    // Canvas socket
    call_in_canvas() {
      const data = this.$refs.pdf_canvas.toDataURL("image/webp");
      const pdf_data = {
        src: this.myId,
        group_id: this.group_id,
        type: "pdf",
        w: this.w,
        h: this.h,
        data: data,
      };
      this.socketio.emit("share_draw", JSON.stringify(pdf_data));
    },
    on_call_in_canvas(msg) {
      const data = JSON.parse(msg);
      if (data.type == "pdf") {
        this.w = this.org_w = this.$refs.pdf_canvas.width = data.w;
        this.h = this.org_h = this.$refs.pdf_canvas.height = data.h;
        this.inSubMenu = true;
        this.scale_menu = true;
        this.img = new Image(this.w, this.h);
        this.img.src = data.data;
        this.img.onload = () => {
          this.pdf_ctx.drawImage(this.img, 0, 0, this.w, this.h);
        };
      }
    },
    // WebRTC
    async call_in(elm) {
      const stream = await this.p2g.call_in(elm);
      this.show_video = true;
      // 自分の送信ストリーム（chrome自体の共有）が切れたら、自分の映像表示も落とす。
      stream.getVideoTracks().forEach((track) => {
        track.onended = () => {
          this.share.layer.setActive(false);
          this.$store.commit("amt/setSubMenu", null);
        };
      });
      await this.$refs.desktop_video.play();
    },
    call_out(elm) {
      // 映像の停止
      if (elm && elm.srcObject)
        elm.srcObject.getVideoTracks().forEach((t) => t.stop());
    },
    share_start_no() {
      this.show_daialog = false;
      this.share.layer.setActive(false);
    },
    share_start_yes() {
      this.show_daialog = false;
    },
    active_share_desktop() {
      this.inSubMenu = true;
      this.scale_menu = true;
      this.current_stream_src = this.$refs.desktop_video;
      this.call_in(this.current_stream_src);
    },
    active_share_pdf() {
      this.inSubMenu = true;
      this.page_menu = true;
      this.scale_menu = true;
      this.pdf = new openPDF(this.$refs.pdf_canvas);

      this.pdf.on("resize", (w, h) => {
        this.w = w;
        this.h = h;
        this.$nextTick().then(() => {
          this.$refs.canvas_draw.all_draw();
        });
      });
      this.pdf.on("start", () => {});
      this.pdf.on("page", (page) => {
        // ページ更新時に一度だけ、pdf_canvasから転写
        this.page = page;
        this.org_w = this.$refs.pdf_canvas.width;
        this.org_h = this.$refs.pdf_canvas.height;
        // 手書きcanvas表示用ページも更新し、再描画する
        this.$refs.canvas_draw.page = page;
        this.$refs.canvas_draw.all_draw();
        this.$refs.canvas_draw.send_point({ page: page });
        this.call_in_canvas();
      });
      this.pdf.open();
      // this.current_stream_src = this.$refs.remote_canvas;
      // this.call_in(this.current_stream_src);
    },
    // ------------------------------------------------------------------
    // scale
    //   pdfレンダリング      -> this.w, this.h
    //                       -> pdf_canvasの内部canvasのwidth, height
    //   video着信(onresize)  -> this.w, this.h
    //   this.w, this.hの変更 -> canvasDrawの内部canvasのwidth, height
    //                        -> video.width, .height
    //                       -> pdf_canvasの内部canvasのwidth, height
    // ------------------------------------------------------------------
    scale_inc() {
      if (this.scale < 2.0) {
        this.scale = this.scale + this.scale_period;
        this.w = this.org_w * this.scale;
        this.h = this.org_h * this.scale;
        console.log(`SCALE=${this.scale}`);
        console.log(`w=${this.w} h=${this.w}`);
        if (this.page_menu) this.pdf.page(this.page, this.scale);
        if (this.scale_menu) {
          this.$refs.desktop_video.width = this.$refs.pdf_canvas.width = this.w;
          this.$refs.desktop_video.height = this.$refs.pdf_canvas.height =
            this.h;
        }
        this.$nextTick(() => {
          this.$refs.canvas_draw.all_draw();
          if (!this.page_menu && this.img) {
            this.pdf_ctx.drawImage(this.img, 0, 0, this.w, this.h);
          }
        });
      }
    },
    scale_dec() {
      if (this.scale > 0) {
        this.scale = this.scale - this.scale_period;
        this.w = this.org_w * this.scale;
        this.h = this.org_h * this.scale;
        console.log(`SCALE=${this.scale}`);
        console.log(`w=${this.w} h=${this.w}`);
        if (this.page_menu) this.pdf.page(this.page, this.scale);
        if (this.scale_menu) {
          this.$refs.desktop_video.width = this.$refs.pdf_canvas.width = this.w;
          this.$refs.desktop_video.height = this.$refs.pdf_canvas.height =
            this.h;
        }
        this.$nextTick(() => {
          this.$refs.canvas_draw.all_draw();
          if (!this.page_menu && this.img) {
            this.pdf_ctx.drawImage(this.img, 0, 0, this.w, this.h);
          }
        });
      }
    },
    move(pos_style) {
      this.canvas_style = pos_style;
    },
    // Comfirme Exit
    exit_dialog_yes() {
      this.exit_subMenu();
      this.$store.commit("amt/comfirme_exit", false);
      this.$store.commit("amt/setSubMenu", null);
    },
    exit_dialog_no() {
      this.$store.commit("amt/setSubMenu", "ShareLayer");
      this.$store.commit("amt/comfirme_exit", false);
    },
    exit_subMenu() {
      // サブメニューを抜ける
      // this.inSubMenu = false;
      // this.$store.commit("amt/setSubMenu", false);
      this.share.layer.setActive(false);
      // layerModules.forEach(m => m.layer.setActive(false));
    },
  },
  computed: {
    menu_enable() {
      return ["win32"].includes(navigator.platform.toLowerCase());
    },
    users() {
      return this.$store.getters["amt/users"];
    },
    myId() {
      return this.$store.getters["amt/myId"];
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
    myPosition() {
      return this.$store.getters["amt/myPosition"];
    },
    patrolState() {
      return this.$store.getters["amt/patrolState"];
    },
    share_owner() {
      const id = this.share.layer.get_info("share_owner")
        ? this.share.layer.get_info("share_owner")
        : this.myId;
      return this.users[id].user_name;
    },
    socketio() {
      return this.$store.getters["socketio"];
    },
  },
  beforeDestroy() {
    clearInterval(this.clearId);
    if (this.pdf) {
      this.pdf.close();
      this.pdf = null;
    }
    this.page_menu = false;
    this.scale_menu = false;
    this.inSubMenu = false;
    this.call_out(this.current_stream_src);
    this.p2g.close();
    this.socketio.off("share_draw", this.on_call_in_canvas);
    // this.$parent.$refs.mapMenu.inSubMenu = false;
    // this.$store.commit("setSbuMenu", false);
    this.$store.commit("amt/setSubMenu", null);
    this.$parent.$refs.mapMenu.showMapMenu = true;
    // this.local_ctx = null;
    // this.remote_ctx = null;
    // 2020-11-30 共有停止確認ダイアログを表示したタイミングで共有主が終了した時の動作を修正
    this.$store.commit("amt/comfirme_exit", false);
  },
};
</script>

<style>
@media screen and (min-width: 0px) and (max-width: 479px) {
  /* 縦向き */
  .share_submenu {
    left: 0vw;
  }
}
@media screen and (min-width: 480px) {
  /* 横向き */
  .share_submenu {
    left: 15vw;
  }
}
.share_submenu {
  display: flex;
  flex-direction: column-reverse;
  position: fixed;
  z-index: 100;
  padding-left: 10px;
  padding-bottom: 15px;
  bottom: 5rem;
}

.share_submenu::-webkit-scrollbar {
  display: none;
}
.circleBtn {
  margin-right: 5px;
  margin-top: 5px;
}
.pdf_page_menu {
  position: absolute;
  z-index: 100;
  top: 0px;
  left: 0px;
  display: flex;
  opacity: 0.4; /* 変更 0.2 -> 0.4 2020/09/01 中村 */
}
.pdf_page_menu:hover {
  opacity: 1;
}
.page_menu_left {
  margin-left: 5px;
}
.pdf_canvas {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 97;
}
.dummy_canvas {
  position: absolute;
  top: 0px;
  left: 0px;
  display: none;
}
.desktop_video {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 3;
}
</style>
