/* eslint-disable */
import axios from "axios";

export default {
  namespaced: true,
  state: {
    classify_list: [],
    classify_name: "",
    is_visible_dialog: false,
    error_dialog: {
      is_visible: false,
      msg: null,
    },
    msg_dialog: "",
    viewlayer_status: false,
    memo_makes_view: false,

    clicked_memo: {}, //地図でクリックされたメモのデータを格納する
  },
  getters: {
    clicked_memo(state) {
      return state.clicked_memo;
    },
    classify_list(state, getters, rootState, rootGetters) {
      return state.classify_list;
    },
    classify_name(state, getters, rootState, rootGetters) {
      return state.classify_name;
    },
    error_msg(state, getters, rootState, rootGetters) {
      return state.error_dialog.msg;
    },
    is_visible_error_dialog(state, getters, rootState, rootGetters) {
      return state.error_dialog.is_visible;
    },
    is_memo_makes_view(state, getters, rootState, rootGetters) {
      return state.memo_makes_view;
    },
  },
  actions: {
    async fetchClassify(context) {
      let res = [];
      const url = "/api/classify/";
      try {
        const ctx = await axios.get(url);
        res = ctx.data;
      } catch (err) {
        console.log("fetchClassify error : " + err);
      }
      context.commit("set_classify_list", res);
    },
  },
  mutations: {
    set_clicked_memo(state, val) {
      state.clicked_memo = val;
    },
    set_classify_list(state, val) {
      state.classify_list = val;
    },
    set_classify_name(state, val) {
      state.classify_name = val;
    },
    show_dialog(state, val) {
      state.is_visible_dialog = val;
    },
    set_error_dialog(state, val) {
      state.error_dialog = val;
    },
    setClickedMemo(state, val) {
      state.clicked_memo[val.id] = val;
    },
    setMemoMakesView(state, val) {
      state.memo_makes_view = val;
    },
  },
};
