<template>
  <div></div>
</template>
<script>
import { Map, View, Feature, Overlay } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";
import * as olEasing from "ol/easing";
import * as olObservable from "ol/Observable";
import * as olFormat from "ol/format";

import { hazardMapEvent } from "./index.js";

var ol = {};
ol.Map = Map;
ol.View = View;
ol.layer = olLayer;
ol.control = olControl;
ol.interaction = olInteraction;
ol.proj = olProj;
ol.source = olSource;
ol.style = olStyle;
ol.geom = olGeom;
ol.Feature = Feature;
ol.Overlay = Overlay;
ol.easing = olEasing;
ol.Observable = olObservable;
ol.format = olFormat;

export default {
  data: function () {
    return {
      hazardMapLayer: null,
      layers: [],
      // urls: [
      //   "https://disaportaldata.gsi.go.jp/raster/01_flood_l2_shinsuishin_kuni_data/{z}/{x}/{y}.png",
      //   "https://disaportaldata.gsi.go.jp/raster/05_dosekiryukeikaikuiki/{z}/{x}/{y}.png",
      // ]
    };
  },
  computed: {
    map() {
      return this.$store.state.map;
    },
    map_urls() {
      return this.$store.getters["map_urls"];
    },
    myId() {
      return this.$store.state.myId;
    },
  },
  methods: {
    init({ visible, opacity, zIndex }) {
      this.map_urls.forEach((url) => {
        const layer = new ol.layer.Tile({
          source: new ol.source.XYZ({ url: url }),
          opacity: 1,
          visible: false,
        });
        layer.setZIndex(zIndex);
        layer.setOpacity(opacity);
        layer.setVisible(visible);
        this.map.addLayer(layer);
        this.layers.push(layer);
      });

      /*
      // 外部GeoJSONスタイル設定
      const style_simple = new ol.style.Style({
        fill: new ol.style.Fill({
          color: '#ADD8E6'
        }),
        stroke: new ol.style.Stroke({
          color: '#88FF00',
          width: 10
        })
      });

      function simpleStyle(feature) {
        return style_simple;
      }

      // 外部GeoJSONレイヤ設定
      const vectorLayer = new ol.layer.VectorTile({
        source: new ol.source.VectorTile({
          url: "https://cyberjapandata.gsi.go.jp/xyz/experimental_rdcl/{z}/{x}/{y}.geojson",
          // url: "https://cyberjapandata.gsi.go.jp/xyz/experimental_railcl/{z}/{x}/{y}.geojson", // 鉄道
          // url: "https://cyberjapandata.gsi.go.jp/xyz/experimental_rvrcl/{z}/{x}/{y}.geojson", // 河川
          // format: new GeoJSON()
          format: new ol.format.GeoJSON()
        }),
        style: simpleStyle
      });
      vectorLayer.setZIndex(zIndex);
      vectorLayer.setOpacity(opacity);
      vectorLayer.setVisible(visible);
      this.map.addLayer(vectorLayer);

      this.layers.push(vectorLayer);
      */
    },

    // 特定のサブメニューが開いているかチェック
    isOpenSubMenu() {
      var layers = this.$store.getters["amt/layers"];
      var memoLayer = layers["MemoLayer"];
      var shareLayer = layers["ShareLayer"];
      var drawLayer = layers["DrawLayer"];

      if (
        memoLayer.layer.getActive() ||
        shareLayer.layer.getActive() ||
        drawLayer.layer.getActive()
      ) {
        // メモ・共有・手書きのサブメニュー表示中
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    hazardMapEvent.$on("INIT", () => {
      const configs = this.$store.getters["amt/layers"];
      const config = configs["HazardMapLayer"];
      this.init(config);
    });
    hazardMapEvent.$on("SETOPACITY", (val) => {
      if (this.layers.length > 0) {
        this.layers.forEach((layer) => {
          layer.setOpacity(val);
        });
      }
    });

    hazardMapEvent.$on("SETVISIBLE", (val) => {
      if (this.layers.length > 0) {
        this.layers.forEach((layer) => {
          layer.setVisible(val);
        });
      }
    });

    hazardMapEvent.$on("SETZINDEX", (val) => {
      if (this.layers.length > 0) {
        this.layers.forEach((layer) => {
          layer.setZIndex(val);
        });
      }
    });
  },
  beforeDestroy() {},
};
</script>
