import $store from "@/store";
import trackLayer from "./TrackLayer.vue";

import Vue from "vue";
export const trackEvent = new Vue();

export function trackLayerModule() {
  const noop = () => {};

  function add() {
    $store.commit("amt/addComponent", trackLayer);
  }

  function setOpacity(val) {
    trackEvent.$emit("SETOPACITY", val);
  }

  function setVisible(val) {
    trackEvent.$emit("SETVISIBLE", val);
  }

  function setZIndex(val) {
    trackEvent.$emit("SETZINDEX", val);
  }

  function setActive(val) {
    trackEvent.$emit("SETACTIVE", val);
  }

  return {
    add, // マップ初期化時に呼び出し
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity,
    setVisible,
    setZIndex,
    setActive, // メニュー操作でトグル
  };
}
