    <div id="statusDisp" class="nosel">{{ callStatus }}</div>
    <levelField></levelField>
  </div>
</template>

<script>
import PhoneButton from "../components/PhoneButton.vue";
import LevelField from "../components/LevelField.vue";
import GroupSelect from "../components/GroupSelect";
import TimeDisp from "@/components/TimeDisp.vue";

export default {
  data() {
    return {
      phoneStatusNo: 0,
      phoneStatusStr: "",
      renewTime: new Date(),
    };
  },
  components: {
    levelField: LevelField,
    phoneButton: PhoneButton,
    GroupSelect: GroupSelect,
    TimeDisp: TimeDisp,
  },
  methods: {
    logDebug(msg) {
      this.$store.state.logger.debug(msg);
    },
    log(msg) {
      this.$store.state.logger.info(msg);
    },
    logError(msg) {
      this.$store.state.logger.error(msg);
    },
  },
  computed: {
    callStatus() {
      return this.$store.getters["ptt/callStatus"];
    },
    isLandscape() {
      return this.$store.state.screen_landscape;
    },
  },
};
</script>

