"use strict";

const send_fns = {};
const recv_fns = {};

// クライアント側
export function evEmitter_client() {
  const id = 1;
  function on(ev, fn) {
    if (recv_fns[ev]) {
      recv_fns[ev].add(fn);
    } else {
      recv_fns[ev] = new Set([fn]);
    }
    console.log(`クライアント側onで登録: ${recv_fns}`);
  }
  function off(ev, fn) {
    if (recv_fns[ev]) {
      recv_fns[ev].delete(fn);
    }
  }
  function emit(ev, args) {
    for (const fn of send_fns[ev] || []) {
      // console.log(`クライアント側で発火: ${args}`)
      fn(args);
    }
  }
  function disconnect() {
    console.log("DISCONNECT");
  }
  return { on, off, emit, id, disconnect };
}

// サーバ側
export function evEmitter_serv() {
  function on(ev, fn) {
    if (send_fns[ev]) {
      send_fns[ev].add(fn);
    } else {
      send_fns[ev] = new Set([fn]);
    }
  }
  function off(ev, fn) {
    if (send_fns[ev]) {
      send_fns[ev].delete(fn);
    }
  }
  function emit(ev, args) {
    for (const fn of recv_fns[ev] || []) {
      console.log(`サーバ側emitで発火: ${args}`);
      fn(args);
    }
  }
  return { on, off, emit };
}
