<template>
  <div></div>
</template>
<script>
import { Map, View, Feature, Overlay } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";
import * as olEasing from "ol/easing";
import * as olObservable from "ol/Observable";

import { weatherEvent } from "./index.js";

var ol = {};
ol.Map = Map;
ol.View = View;
ol.layer = olLayer;
ol.control = olControl;
ol.interaction = olInteraction;
ol.proj = olProj;
ol.source = olSource;
ol.style = olStyle;
ol.geom = olGeom;
ol.Feature = Feature;
ol.Overlay = Overlay;
ol.easing = olEasing;
ol.Observable = olObservable;

export default {
  data: function () {
    return {
      weatherLayer: null,
      clearId: {},
    };
  },
  computed: {
    map() {
      return this.$store.state.map;
    },
    myId() {
      return this.$store.state.myId;
    },
  },
  methods: {
    // 気象レイヤー
    //ナウキャスト
    createWeatherLayer() {
      return new ol.layer.Tile({
        source: new ol.source.TileWMS({
          url: "https://wms.jmarinecloud.com/ms4w/htdocs/direction_raster.php",
          params: {
            LAYERS: "area_02",
            TRANSPARENT: true,
            FORMAT: "image/png",
            KIND: "nowcast_hi",
            VERSION: "1.1.1",
            tm: new Date().getMilliseconds(),
          },
          visibility: true,
        }),
        // visible: true,
        visible: false,
      });
    },

    // 初期化
    initWeatherLayer({ visible, opacity, zIndex }) {
      this.weatherLayer = this.createWeatherLayer();
      this.weatherLayer.setZIndex(zIndex);
      this.weatherLayer.setOpacity(opacity);
      this.weatherLayer.setVisible(visible);
      this.map.addLayer(this.weatherLayer);
    },
    refreshNowcast() {
      this.refreshNowcastLayer(0);
    },
    refreshNowcastLayer(diffHour) {
      const dt = new Date(new Date().getTime() + diffHour * 60 * 60 * 1000);
      const yr = dt.getFullYear();
      const mt = ("00" + (dt.getMonth() + 1)).slice(-2);
      const dy = ("00" + dt.getDate()).slice(-2);
      const hr = ("00" + dt.getHours()).slice(-2);
      //const mn = ("00" + dt.getMinutes()).slice(-2);
      //const timeStr = yr + mt + dy + hr + mn + "00_000_m"
      const timeStr = yr + mt + dy + hr + "0000_000_m";

      const source = this.weatherLayer.getSource();
      let params = source.getParams();
      params.tm = new Date().getMilliseconds();
      if (diffHour == 0) {
        delete params.TIME;
      } else {
        params.TIME = timeStr;
      }
      source.updateParams(params);
    },

    // 特定のサブメニューが開いているかチェック
    isOpenSubMenu() {
      var layers = this.$store.getters["amt/layers"];
      var memoLayer = layers["MemoLayer"];
      var shareLayer = layers["ShareLayer"];
      var drawLayer = layers["DrawLayer"];

      if (
        memoLayer.layer.getActive() ||
        shareLayer.layer.getActive() ||
        drawLayer.layer.getActive()
      ) {
        // メモ・共有・手書きのサブメニュー表示中
        return true;
      } else {
        return false;
      }
    },
  },
  mounted() {
    weatherEvent.$on("INIT", () => {
      const configs = this.$store.getters["amt/layers"];
      const config = configs["WeatherLayer"];
      this.initWeatherLayer(config);
    });
    weatherEvent.$on("SETOPACITY", (val) => {
      if (this.weatherLayer) {
        this.weatherLayer.setOpacity(val);
      }
    });

    weatherEvent.$on("SETVISIBLE", (val) => {
      if (this.weatherLayer) {
        this.weatherLayer.setVisible(val);
      }
    });

    weatherEvent.$on("SETZINDEX", (val) => {
      if (this.weatherLayer) {
        this.weatherLayer.setZIndex(val);
      }
    });
    this.clearId["refresh"] = setInterval(() => {
      if (this.weatherLayer) {
        if (this.weatherLayer.getVisible()) {
          this.refreshNowcast();
        }
      } else {
        console.log("出た");
      }
    }, 600 * 1000);
  },
  beforeDestroy() {
    Object.keys(this.clearId).forEach((id) => {
      clearInterval(this.clearId[id]);
    });
  },
};
</script>
