<script>
export default {
  data() {
    return {
      msg: "登録するIDとPINを入力して下さい",
      authId: null,
      authPass: "",
      pri_pem: "",
      pub_pem: "",
    };
  },
  mounted() {
    if (this.$route.query.state == "error") {
      document
        .getElementById("alert-wrapper")
        .classList.replace("login-info", "login-error");
    }
  },
  computed: {
    isLogined() {
      return this.$store.getters("isLogined");
    },
    userId() {
      return this.$store.getters("userId");
    },
  },
  methods: {
    async msg_hash256(msg) {
      const hashBuffer = await crypto.subtle.digest(
        "SHA-256",
        new Uint8Array(new TextEncoder().encode(msg))
      );
      const hashArray = Array.from(new Uint8Array(hashBuffer)); // convert buffer to byte array
      return hashArray.map((b) => b.toString(16).padStart(2, "0")).join(""); // convert bytes to hex string
    },
    async post() {
      console.log("This is post()");
      // 鍵ペアの作成
      const privateKey = await this.generateRsaKey();
      // IDの登録
      await this.ldb.setItem("userId", this.authId);

      // 公開鍵の転送
      const hashHex = await this.sign(
        privateKey,
        `${this.authId}.${this.authPass}.${this.pub_pem}`
      );
      try {
        const res = await fetch("/api/regist", {
          method: "post",
          catch: "no-cache",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            id: this.authId,
            password: this.authPass,
            publicKey: this.pub_pem,
            sign: hashHex,
          }),
        });
        if (res.ok) {
          const res_json = await res.json();
          console.log(res_json);
          console.log("router : Loginに行きます。");
          this.$router.push("/Login");
        }
      } catch (error) {
        console.log(error);
      }
    },
    async generateRsaKey() {
      console.log("This is generateRsaKey");
      const keys = await crypto.subtle.generateKey(
        {
          name: "RSASSA-PKCS1-v1_5",
          modulusLength: 2048,
          publicExponent: new Uint8Array([1, 0, 1]),
          hash: "SHA-256",
        },
        true,
        ["sign", "verify"]
      );

      this.pri_pem = await this.exportKeyPem("PRIVATE", keys.privateKey);
      console.log(this.pri_pem);
      this.pub_pem = await this.exportKeyPem("PUBLIC", keys.publicKey);
      console.log(this.pub_pem);

      // // --------------- TEST : publicKey -----------------------------------
      // console.log("--------------- TEST : publicKey START-----------------------------------");
      // 公開鍵
      // const publicKey = await this.importRsaKey(this.pub_pem);
      // console.log(publicKey);
      // 秘密鍵
      // const privateKey = await this.importRsaKey(this.pri_pem);
      // console.log(privateKey);

      // // const str_signature = await this.sign(keys.privateKey, "HELLO, WORLD");
      // // const res = await this.verify(publicKey, str_signature, "HELLO, WORLD");
      // // console.log(`結果：${res}`);
      // console.log("--------------- TEST : publicKey END-----------------------------------");

      // 秘密鍵のDB登録
      await this.ldb.setItem("private_key", keys.privateKey);
      return keys.privateKey;
    },
    // async sign(key, data) {
    //   return await crypto.subtle.sign("RSASSA-PKCS1-v1_5", key, (new TextEncoder()).encode(data));
    // },
    async sign(key, data) {
      const sign_arraybuff = await crypto.subtle.sign(
        "RSASSA-PKCS1-v1_5",
        key,
        new TextEncoder().encode(data)
      );
      const sign_str = this.ab2str(sign_arraybuff);
      return window.btoa(sign_str);
    },
    async verify(key, signature, data) {
      const verified = await crypto.subtle.verify(
        "RSASSA-PKCS1-v1_5",
        key,
        signature,
        new TextEncoder().encode(data)
      );
      return verified;
    },
    str2ab(str) {
      console.log(`LEN=${str.length}`);
      const buf = new ArrayBuffer(str.length);
      const bufView = new Uint8Array(buf);
      for (let i = 0, strLen = str.length; i < strLen; i++) {
        bufView[i] = str.charCodeAt(i);
      }
      return buf;
    },
    ab2str(buf) {
      return String.fromCharCode.apply(null, new Uint8Array(buf));
    },
    async importRsaKey(pem) {
      const pemHeader = "-----BEGIN PUBLIC KEY-----";
      const pemFooter = "-----END PUBLIC KEY-----";
      const pemContents = pem.substring(
        pemHeader.length,
        pem.length - pemFooter.length
      );
      const binaryDerString = window.atob(pemContents);
      const binaryDer = this.str2ab(binaryDerString);
      console.log(binaryDer);
      const key = await crypto.subtle.importKey(
        "spki",
        binaryDer,
        {
          name: "RSASSA-PKCS1-v1_5",
          hash: "SHA-256",
        },
        true,
        ["verify"]
      );
      console.log(key);
      return key;
    },
    async exportKeyPem(key_type, key) {
      const format = key_type == "PUBLIC" ? "spki" : "pkcs8";
      const publicKey = await crypto.subtle.exportKey(format, key);
      let body = window.btoa(String.fromCharCode(...new Uint8Array(publicKey)));
      body = body.match(/.{1,64}/g).join("\n");
      return `-----BEGIN ${key_type} KEY-----\n${body}\n-----END ${key_type} KEY-----`;
    },
  },
};
</script>

