  <div class="memo_makes">
    <div class="memo_makes_header">
      <h3>メモ作成画面</h3>
      <button class="closeBtn" title="閉じる">
        <!-- 非表示化するボタン -->
        <img
          src="@/assets/close05.png"
          class="btnImg"
          @click="close_button()"
        />
      </button>
    </div>

    <div class="arow-wrap">
      <div>
        <a href="#memo_video" class="arrow-left" v-show="!left_view" />
      </div>
      <div class="memo_makes_middle">
        <div class="slides" @scroll="is_left">
          <div class="video_cont">
            <video
              id="memo_video"
              ref="video"
              muted
              playsinline
              autoplay
              :class="{ front_class: is_front }"
            ></video>
            <div class="text_fade">映像</div>
          </div>
          <textarea id="memo_text" v-model="text_memo" />
        </div>
      </div>
      <div>
        <a href="#memo_text" class="arrow-right" v-show="left_view" />
      </div>
    </div>

    <div class="status_button">
      <!-- Speech Recognition -->
      <a href="#memo_text">
        <div
          v-if="is_not_ios"
          class="memolayer_btn memolayer_btn_margin"
          :style="mic_btn_color"
          @click="on_click_mic"
        >
          <img :src="icon_mic" class="memolayer_btn_icon" />
        </div>
      </a>

      <!-- 動画 -->
      <div
        v-show="movie_btn_state == 'close'"
        @click="on_UPLOAD"
        class="memolayer_btn"
        title="アップロード"
      >
        <img :src="icon_upload" class="memolayer_btn_icon" />
      </div>
      <a href="#memo_video">
        <div
          v-show="movie_btn_state == 'REC'"
          @click="on_REC"
          class="memolayer_btn"
          title="録画開始"
        >
          REC
        </div>
      </a>
      <div
        v-show="movie_btn_state == 'STOP'"
        @click="on_STOP"
        class="memolayer_btn on_REC"
        title="録画停止"
      >
        STOP
      </div>
    </div>
    <modalDialog v-if="is_dialog_visible">
      <span slot="header">地点メモ作成</span>
      <span slot="body" style="color: black">{{ dialog_msg }}</span>
      <div slot="footer">
        <button class="btn btn-primary" @click="dialog_yes">はい</button>
        <button class="btn btn-primary" @click="dialog_no">いいえ</button>
      </div>
    </modalDialog>
  </div>
</template>

<script>
/* eslint-disable */
import { memoEvent } from "./index.js";
import { sp_start, sp_stop } from "./sp_recognition";
import ModalDialog from "@/components/ModalDialog.vue";
import { memo_common } from "./common.js";

// テキスト入力状態でボタン押下時すると次の作成タイミングで<>ボタンが出なくなる
// ※compudedのon_left_endとon_right_endが呼ばれなくなる
// テキスト入力解除と初期化処理の時間を300msec開ける(200msecではだめだった)
const CLOSE_TIMEOUT = 300;
export default {
  mixins: [memo_common],
  components: {
    modalDialog: ModalDialog,
  },
  data() {
    return {
      btn_title: "",
      // テキストメモ
      text_memo: "",
      // 動画メモ
      url: "/api/upload",
      recorder: null,
      movie_btn_state: "REC",
      movieDataBuf: null,
      moveDataChunks: [],
      // 写真メモ
      icon_upload: require("./assets/icon_upload.png"),
      icon_mic: require("@/assets/mic.png"),
      imageCapture: null,
      imageDataBuf: null,
      mic_btn_flag: false,
      left_view: true,
      is_dialog_visible: false,
      dialog_msg: "",
      on_end_recorder: null,
    };
  },
  mounted() {
    console.log("HELLO, I am MovieMake.vue");
    this.init();
  },
  methods: {
    async dialog_yes() {
      this.is_dialog_visible = false;
      if (this.mic_btn_flag) {
        this.mic_btn_flag = false;
        sp_stop();
        await this.mic_on_off("start");
        memoEvent.$emit("BACK_MEMO");
        this.init_clear();
      } else if (this.recorder && this.recorder.state == "recording") {
        this.on_end_recorder = () => {
          memoEvent.$emit("BACK_MEMO");
          this.init_clear();
        };
        this.recorder.stop();
      }
    },
    dialog_no() {
      this.is_dialog_visible = false;
    },
    init_clear() {
      this.movie_btn_state = "REC";
      this.imageDataBuf = null;
      this.moveDataChunks = [];
      this.movieDataBuf = null;
      this.file_name = "";
      this.text_memo = "";
      this.on_end_recorder = null;
      if (this.recorder && this.recorder.state == "recording") {
        return new Promise((resolve, reject) => {
          this.recorder.stop();
          console.log("recorderをSTOPしたよ");
          resolve();
        }).then(() => {
          console.log("recorderをnullにしたよ");
          this.recorder = null;
        });
      } else {
        this.recorder = null;
      }
      this.left_view = true;
      history.replaceState(null, "", location.pathname);
      console.log("end_flag: 終了だ");
      this.$emit("end_flag"); // これでコンポーネントを終了する。
    },
    on_UPLOAD() {
      // サーバへサムネイルをアップロード
      const b64 = this.drawCanvas(this.imageDataBuf);
      this.post_file(this.file_name + "_thumb.jpg", b64);
      // ムービーデータをアップロード
      this.post_file(this.file_name + ".webm", this.movieDataBuf);

      // 地点メモのピン建て
      const data = { file_name: this.file_name, info: this.text_memo };
      memoEvent.$emit("SEND_JSON_VIDEO", data);

      memoEvent.$emit("BACK_MEMO");
      this.init_clear();
    },
    async on_REC() {
      this.movie_btn_state = "STOP";
      if (!this.recorder) await this.init_recorder();
      this.recorder.start();
    },
    on_STOP() {
      this.movie_btn_state = "close";
      this.recorder.stop();
    },
    async init_recorder() {
      return new Promise((resolve, reject) => {
        try {
          const video_track = this.local_video_stream.getVideoTracks()[0];
          const audio_track = this.local_audio_stream.getAudioTracks()[0];
          this.stream = new MediaStream([video_track, audio_track]);
        } catch (e) {
          console.log(e);
          this.stream = null;
        }

        // const self = this;
        const myType_primary = "video/webm; codecs=vp8";
        const myType_secondary = "video/webm";
        const myType_3rd = "video/mp4";
        const myType_4th = "video/webm; codecs=h264";
        const ua = navigator.userAgent;

        try {
          if (
            MediaRecorder.isTypeSupported(myType_primary) &&
            ua.indexOf("Firefox") <= 0
          ) {
            // FirefoxはisTypeSupportedがtrueでもMediaRecorderインスタンス生成でエラーになる
            this.recorder = new MediaRecorder(this.stream, {
              mimeType: myType_primary,
            });
          } else if (MediaRecorder.isTypeSupported(myType_primary)) {
            this.recorder = new MediaRecorder(this.stream, {
              mimeType: myType_secondary,
            });
          } else {
            this.recorder = new MediaRecorder(this.stream, {
              mimeType: myType_3rd,
            });
          }
        } catch (error) {
          this.err_exit(`${error.name}:録画が開始出来ません`);
        }
        this.recorder.ondataavailable = (ev) => {
          this.moveDataChunks.push(ev.data);
        };
        this.recorder.onstart = () => {
          // サムネイルのサイズを設定
          var width;
          var height;
          if (window.innerHeight > window.innerWidth) {
            // 縦向き表示中は長辺を縦とした画像
            height = this.$store.state.video_width;
            // 横幅は長辺の3/4(3対4)
            width = (height / 4) * 3;
          } else {
            // 横向き表示中は長辺を横とした画像
            width = this.$store.state.video_width;
            // 高さは長辺の3/4(3対4)
            height = (width / 4) * 3;
          }
          this.$store.commit("setMemmo_recording", true);

          // サムネイル作成
          if (this.imageCapture) {
            this.imageCapture
              .takePhoto()
              .then((blob) =>
                createImageBitmap(blob, {
                  resizeWidth: width,
                  resizeHeight: height,
                })
              )
              .then((imageBitmap) => {
                // サムネイルデータを一時保存
                this.imageDataBuf = imageBitmap;
              })
              .catch((error) => {
                console.log(error);
                this.err_exit(error);
              });
          } else {
            const video = document.createElement("video");
            const canvas = document.createElement("canvas");
            this.imageCaptureTakePhoto(this.stream, canvas, video)
            // this.imageCaptureTakePhoto(this.stream)
              .then((blob) => createImageBitmap(blob))
              .then((imageBitmap) => {
                // サムネイルデータを一時保存
                this.imageDataBuf = imageBitmap;
              })
              .catch((error) => {
                console.log(error);
                this.err_exit(error);
              });
          }
        };
        this.recorder.onstop = () => {
          this.$store.commit("setMemmo_recording", false);
          // 録画停止時刻をファイル名にする
          this.file_name = this.gen_file_name();
          this.movieDataBuf = new Blob(this.moveDataChunks, {
            type: this.recorder.mimeType,
          });
          if (this.on_end_recorder) this.on_end_recorder();
        };
        this.recorder.onerror = (ev) => {
          this.err_exit(`${ev.error.name}:録画出来ません`);
        };
        resolve();
      });
    },
  },
};
</script>

