<script>
import { searchEvent } from "./index.js";

export default {
  data() {
    return {};
  },
  mounted() {
    /* 検索結果の対象を地図に表示 */
    // 軌跡
    searchEvent.$on("searchedTrack", () => {
      for (let searchTrack of this.searchTracks()) {
        searchEvent.$emit("track", JSON.stringify(searchTrack));
        this.$store.dispatch("amt/resetSearchTrack");
      }
    });
    // メモ
    searchEvent.$on("searchedMemo", () => {
      searchEvent.$emit("memo", this.$store.getters["amt/searchMemo"]);
    });
  },
  methods: {
    clickTrack(searchTrack) {
      // 軌跡を表示
      this.$store.dispatch("amt/hideSearchComponentList");
      searchEvent.$emit("track", JSON.stringify(searchTrack));
      this.$store.dispatch("amt/resetSearchTrack");
    },
    close_button() {
      this.$store.dispatch("amt/hideSearchComponentList");
      this.$store.dispatch("amt/resetSearchTrack");
      this.$store.dispatch("amt/resetSearchMemo");
    },
    searchTracks() {
      let state_colors = [
        "#e83e8c",
        "#dc3545",
        "#fd7e14",
        "#ffc107",
        "#28a745",
      ];
      let allTracks = this.$store.getters["amt/searchTrack"];
      let searchTracks = [];
      let tmpSearchTracks = [];
      for (const deviceTracks of allTracks) {
        for (const deviceTrack of deviceTracks.track) {
          let lat = deviceTrack.lat.split(",");
          let lng = deviceTrack.lng.split(",");
          for (let i = 0; i < lat.length; i++) {
            tmpSearchTracks[i] = [lng[i], lat[i]];
          }
        }
        let user_id = deviceTracks.userId;
        let track_color;
        try {
          track_color = this.$store.getters["amt/users"][user_id].track_color;
        } catch (e) {
          track_color = state_colors[user_id % state_colors.length];
        }
        let searchTrack = {
          [user_id]: {
            coords: tmpSearchTracks,
            color: track_color,
            id: "track_search",
            radius: null,
            type: "LineString",
            width: 5,
          },
        };
        searchTracks.push(searchTrack);
      }
      return searchTracks;
    },
  },
};
</script>

