import $store from "@/store";
import locationComponent from "./locationComponent.vue";

import Vue from "vue";
export const locationEvent = new Vue();

export function locationLayer() {
  const noop = () => {};

  function add() {
    $store.commit("amt/addComponent", locationComponent);
  }

  function setOpacity(val) {
    locationEvent.$emit("SETOPACITY", val);
  }

  function setVisible(val) {
    locationEvent.$emit("SETVISIBLE", val);
  }

  function setZIndex(val) {
    locationEvent.$emit("SETZINDEX", val);
  }

  return {
    add, // マップ初期化時に呼び出し
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity,
    setVisible,
    setZIndex,
    setActive: noop, // メニュー操作でトグル
  };
}
