<template>
  <div class="review_menu" ref="review_place">
    <div class="replay-icon" @click="toggleReplay">
      <img
        :src="require('./assets/replay.png')"
        class="icon-img"
        v-if="!now_on_replay"
      />
      <img :src="require('./assets/stop.png')" class="icon-img" v-else />
    </div>
    <div class="replay-text">
      <div @click="toggleStartDateTime">
        <div>
          {{
            start_time ? new Date(start_time).toLocaleDateString() : "開始時刻"
          }}
        </div>
        <div v-if="start_time">
          {{ new Date(start_time).toLocaleTimeString() }}
        </div>
      </div>
    </div>
    <div
      v-show="isVisbleStartTime"
      class="map_popup_down"
      ref="map_popup_down"
      :style="{ top: popup_top + 'px', left: popup_left + 'px' }"
    >
      <p
        v-for="stime in start_times"
        :key="stime"
        @click="selectStartTime(stime)"
      >
        {{ new Date(Number(stime)).toLocaleDateString() }}
        {{ new Date(Number(stime)).toLocaleTimeString() }}
      </p>
    </div>
    <div class="replay-slider">
      <vue-slider
        v-model="t_value"
        :min="min"
        :max="max"
        :tooltip="'always'"
        :process-style="{ backgroundColor: 'orange' }"
      >
        <template v-slot:tooltip="{ value }">
          <div
            :class="[classBox1, { no_display: value === max || value === min }]"
          >
            <div class="custom-tooltip">
              <p>
                {{
                  value === 0
                    ? "-日時-"
                    : new Date(Number(value)).toLocaleDateString()
                }}
              </p>
              <p>
                {{
                  value === 0
                    ? "-時刻-"
                    : new Date(Number(value)).toLocaleTimeString()
                }}
              </p>
            </div>
          </div>
        </template>
      </vue-slider>
    </div>
    <div class="replay-text">
      <div>
        <div>
          {{ end_time ? new Date(end_time).toLocaleDateString() : "終了時刻" }}
        </div>
        <div v-if="end_time">{{ new Date(end_time).toLocaleTimeString() }}</div>
      </div>
      <!-- <input
        type="datetime-local"
        ref="end_time"
        v-else
        @blur="toggleEndDateTime"
        v-model="end_time"
      /> -->
    </div>
    <modalDialog v-if="errMessage" @modalDialogYes="dialogClose">
      <span slot="header">時刻指定エラー</span>
      <span slot="body">{{ errMessage }}</span>
    </modalDialog>
    <div class="submenu writeItem">
      <div>
        <button
          :class="isRecord ? 'circleBtnSelected' : null"
          class="circleBtn subMenu"
          title="クロノロジー記録"
          @click="on_record"
        >
          <img src="./assets/share_desktop@1x.png" />
        </button>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import { Map, View, Feature, Overlay } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";
import * as olEasing from "ol/easing";
import * as olObservable from "ol/Observable";

import { reviewEvent, evEmitter, evEmitter_s } from "./index.js";

import ModalDialog from "@/components/ModalDialog.vue";
import VueSlider from "vue-slider-component";
import "vue-slider-component/theme/default.css";
// import { Datetime } from 'vue-datetime'
// import 'vue-datetime/dist/vue-datetime.css'

var ol = {};
ol.Map = Map;
ol.View = View;
ol.layer = olLayer;
ol.control = olControl;
ol.interaction = olInteraction;
ol.proj = olProj;
ol.source = olSource;
ol.style = olStyle;
ol.geom = olGeom;
ol.Feature = Feature;
ol.Overlay = Overlay;
ol.easing = olEasing;
ol.Observable = olObservable;

export default {
  components: {
    modalDialog: ModalDialog,
    vueSlider: VueSlider,
    // datetime: Datetime
  },
  data() {
    return {
      t_value: 0,
      t_id: null,
      now_on_replay: false,
      min: 0,
      max: 100,
      isVisbleStartTime: false,
      isVisbleEndTime: true,
      // str_start_time: "0",
      start_time: null,
      end_time: null,
      classBox1: "box1",
      errMessage: null,
      isRecord: false,
      start_times: [],
      end_times: [],
      popup_top: 0,
      popup_left: 0,
    };
  },
  computed: {
    users() {
      return this.$store.getters["amt/users"];
    },
    map() {
      return this.$store.state.map;
    },
    myId() {
      return this.$store.state.myId;
    },
    socketio() {
      return this.$store.state.socketio;
    },
  },
  methods: {
    toast(msg) {
      this.$bvToast.toast(msg, {
        title: "AirMultiTalk",
        toaster: "b-toaster-top-center",
        variant: "danger",
        autoHideDelay: 3000,
        appendToast: false,
      });
    },
    async on_record() {
      this.isRecord = !this.isRecord;
      const res = await fetch(
        `/api/record/${this.isRecord ? "start" : "stop"}`
      );
      if (res.ok) {
        const { state } = await res.json();
        if (state) {
          // console.log("クロノロジー記録が開始されました");
          this.toast("クロノロジー記録が開始されました");
        } else {
          // console.log("クロノロジー記録が停止しました");
          this.toast("クロノロジー記録が停止しました");
        }
      }
    },
    dialogClose() {
      this.errMessage = null;
    },
    // toggleStartDateTime() {
    //   this.isVisbleStartTime = !this.isVisbleStartTime;
    //   // document.querySelector("input").click();
    //   if (this.isVisbleStartTime && this.start_time) {
    //     if (Date.parse(this.start_time) >= Date.now()) {
    //       this.errMessage = "過去の時刻を設定してください";
    //       this.start_time = null;
    //     } else {
    //       this.end_time = new Date().toString();
    //     }
    //   }
    // },
    async toggleStartDateTime(ev) {
      console.log(ev);
      this.popup_top = ev.layerY;
      this.popup_left = ev.layerX - 100;

      this.isVisbleStartTime = !this.isVisbleStartTime;
      if (this.isVisbleStartTime) {
        const url = "api/record/state";
        const res = await fetch(url);
        if (res.ok) {
          const { state, files } = await res.json();
          console.log(files);
          // 時刻を取り出して、昇降順でソートする
          const file_times = files
            .map((file) => file.time - 60000)
            .sort((a, b) => {
              if (a < b) return -1;
              if (a > b) return 1;
              return 0;
            });
          this.start_times = file_times.filter((file_time, index, arr) => {
            if (index == 0 || file_time - arr[index - 1] > 60000 * 2) {
              return true;
            } else {
              return false;
            }
          });
          this.end_times = file_times
            .filter((file_time, index, arr) => {
              if (
                arr.length - 1 == index ||
                this.start_times.includes(arr[index + 1])
              ) {
                return true;
              } else {
                return false;
              }
            })
            .map((t) => t + 60000);
          // file_times.forEach(t => console.log(new Date(Number(t)).toLocaleTimeString()))
          // console.log("---------------------------------")
          // this.start_times.forEach(t => console.log(new Date(Number(t)).toLocaleTimeString()))
          // console.log("---------------------------------")
          // this.end_times.forEach(t => console.log(new Date(Number(t)).toLocaleTimeString()))
          // const start_times_el = document.getElementById("start_times");
        }
      }
    },
    selectStartTime(stime) {
      this.start_time = Number(stime);
      this.end_time = Number(this.end_times[this.start_times.indexOf(stime)]);
      this.isVisbleStartTime = false;
    },
    // toggleEndDateTime() {
    //   this.isVisbleEndTime = !this.isVisbleEndTime;
    // },
    async toggleReplay() {
      console.log("toggleReplay");
      this.now_on_replay = !this.now_on_replay;

      if (this.now_on_replay) {
        try {
          const num_start_time = this.start_time
            ? new Date(this.start_time).getTime()
            : 0;
          const num_end_time = this.end_time
            ? new Date(this.end_time).getTime()
            : 0;
          const url = `/api/review?start=${num_start_time}&end=${num_end_time}`;
          const res = await fetch(url);
          if (res.ok) {
            this.review_data = await res.json();

            // t_dataは記録された時刻の配列
            const t_data = Object.keys(this.review_data);

            console.log(t_data);
            const PERIOD = 200;

            // 割当開始時刻が保存ファイルの時刻より、後の場合、「そこから開始したい」と想定しそのまま開始する
            // 割当開始時刻が保存ファイルの時刻より、前の場合、「適当に割当した」から、保存ファイルの時刻にして開始する。
            // そもそも、割当開始時間なしで、開始した場合、全部返されるので、最古から開始する。
            let pre_t;
            // 保存ファイルが空の場合、Buttonを戻して抜ける
            if (t_data.length === 0) {
              console.log("保存ファイルがありません");
              this.now_on_replay = false;
              return;
            }
            // 割当開始時間なしで、開始した場合
            if (
              (num_start_time === 0 && num_end_time === 0) ||
              num_start_time < Number(t_data[0])
            ) {
              pre_t = Number(t_data[0]);
              this.end_time = this.max = Number(t_data[t_data.length - 1]);
            } else {
              pre_t = num_start_time;
              this.max = num_end_time;
            }
            this.start_time = this.min = pre_t;
            let cur_t = pre_t + PERIOD;

            this.t_id = setInterval(() => {
              console.log(
                `pre_t=${new Date(pre_t).toLocaleTimeString()} cur_t=${new Date(
                  cur_t
                ).toLocaleTimeString()}`
              );
              const arr_t = t_data.filter((t) => {
                return t >= pre_t && t < cur_t;
              });
              // console.log(arr_t);
              arr_t.forEach((t) => {
                const { event, msg } = this.review_data[t];
                console.log(`${event}: ${msg}`);
                evEmitter_s.emit(event, msg);
              });
              pre_t = cur_t;
              cur_t = cur_t + PERIOD;
              this.t_value = cur_t;
              if (cur_t > this.end_time) {
                clearInterval(this.t_id);
                this.t_value = this.min;
                this.now_on_replay = false;
                this.start_time = null;
                this.end_time = null;
                return;
              }
            }, PERIOD / 10);
          } else {
            console.log("データがありません ");
          }
        } catch (err) {
          console.log(err);
        }
      } else {
        if (this.t_id) {
          clearInterval(this.t_id);
        }
        this.t_value = this.min;
      }
    },
    on_record_state(msg) {
      this.isRecord = msg.state;
    },
  },
  async mounted() {
    // document.querySelector("input").style.display = "none";
    console.log("---------クロノロジー機能です---------");
    const res = await fetch("/api/record/state");
    if (res.ok) {
      const { state, files } = await res.json();
      if (state) {
        this.isRecord = true;
      }
    }
    this.socketio.on("record", (msg) => this.on_record_state(msg));
    reviewEvent.$on("SETOPACITY", (val) => {
      this.$refs.review_place.style.opacity = val;
    });
    reviewEvent.$on("SETZINDEX", (val) => {
      this.$refs.review_place.style.zIndex = val;
    });
  },
  beforeDestroy() {
    if (this.t_id) clearInterval(this.t_id);
    this.socketio.off(this.on_record_state);
  },
};
</script>
