<template>
  <div class="memo_layer">
    <StrMake v-if="show_memo" @end_flag="end_flag"></StrMake>
    <PhotoMake v-if="show_photo" @end_flag="end_flag"></PhotoMake>
    <MovieMake v-if="show_movie" @end_flag="end_flag"></MovieMake>
  </div>
</template>

<script>
import { memoEvent } from "./index.js";
import StrMake from "./StrMake.vue";
import MovieMake from "./MovieMake.vue";
import PhotoMake from "./PhotoMake.vue";
export default {
  components: {
    StrMake: StrMake,
    MovieMake: MovieMake,
    PhotoMake: PhotoMake,
  },
  data() {
    return {
      show_memo: false,
      show_photo: false,
      show_movie: false,
    };
  },
  mounted() {
    memoEvent.$on("TEXT_MEMO_MAKE", () => {
      this.show_memo = true;
      this.show_photo = false;
      this.show_movie = false;
    });
    memoEvent.$on("PHOTO_MEMO_MAKE", () => {
      this.show_memo = false;
      this.show_photo = true;
      this.show_movie = false;
    });
    memoEvent.$on("MOVIE_MEMO_MAKE", () => {
      this.show_memo = false;
      this.show_photo = false;
      this.show_movie = true;
    });
  },
  methods: {
    end_flag() {
      this.show_memo = false;
      this.show_photo = false;
      this.show_movie = false;
    },
  },
};
</script>

<style scoped>
.memo_layer {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
</style>
