<template>
  <div id="groupList">
    <ul>
      <template v-for="grp in this.myGroup">
        <li
          v-bind:key="'g' + grp.group_id"
          v-bind:style="{ backgroundColor: colorGroup(grp.group_id) }"
          v-on:click="onClickGroup(grp.group_id)"
        >
          <span class="nosel"
            >&nbsp;&nbsp;&nbsp;&nbsp;{{ grp.group_name }}</span
          >
          <img
            src="../assets/pullOpen.png"
            v-on:click.stop="togglePerson(grp.group_id)"
            v-show="grp.persons.length > 0"
            class="groupListImg"
          />
        </li>
        <li
          v-for="psn in grp.persons"
          v-bind:key="'p' + psn.user_id"
          v-show="grp.group_id == groupShowing"
          v-bind:style="{
            backgroundColor: bgPerson(psn.user_id),
            color: colorPerson(psn.user_id),
          }"
          v-on:click="onClickPerson(psn.user_id)"
          disabled="disabled"
        >
          <span class="nosel">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
          <img src="../assets/pullMark.png" style="width: 20px; height: 20px" />
          <span class="nosel">&nbsp;{{ psn.user_name }}</span>
        </li>
      </template>
    </ul>
  </div>
</template>
<script>
/* eslint-disable */
export default {
  data() {
    return {
      groupShowing: -1,
      endTimer: null,
    };
  },
  computed: {
    pttRecv() {
      return this.$store.getters["ptt/pttRecv"];
    },
  },
  methods: {
    at_pttRecv() {
      this.$bvToast.toast("現在着信です。", {
        title: "AirMultiTalk",
        toaster: "b-toaster-top-center",
        variant: "danger",
        autoHideDelay: 3000,
        appendToast: false,
      });
    },
    togglePerson(groupId) {
      if (groupId == this.groupShowing) {
        this.groupShowing = "";
      } else {
        this.groupShowing = groupId;
      }
      return false;
    },
    onClickGroup(groupId) {
      if (this.pttRecv) {
        this.at_pttRecv();
        return;
      }
      // 共有レイヤがonの時はグループの選択ができないように修正 @todo 共有レイヤが無い時の事を考慮
      if (this.$store.getters["amt/subMenu"] == "ShareLayer") {
        return;
      }
      const activeId = this.$store.state.activeId;
      if (activeId.kind == "group" && activeId.id == groupId) {
        //this.$store.commit("setActiveId", ["none", null]);
      } else {
        if (activeId.kind == "person") {
          this.$parent.phoneQueryExec(activeId.id, "end");
        }
        this.$store.commit("setActiveId", ["group", groupId]);
      }
      this.endDelay();
    },
    onClickPerson(userId) {
      // if (userId != this.$store.state.myId) {
      //   const activeId = this.$store.state.activeId;
      //   if (activeId.kind == "person" && activeId.id == userId) {
      //     //this.$store.commit("setActiveId", ["none", null]);
      //   } else {
      //     if (activeId.kind == "person") {
      //       this.$parent.phoneQueryExec(activeId.id, "end");
      //       this.$store.commit("backActiveId");
      //     }
      //     this.$store.commit("setPhoneQuering", userId);
      //   }
      //   this.endDelay();
      // }
    },
    colorGroup(groupId) {
      const activeId = this.$store.state.activeId;
      if (activeId.kind == "group" && activeId.id == groupId) {
        return "royalblue";
      } else {
        return "transparent";
      }
    },
    bgPerson(userId) {
      const activeId = this.$store.state.activeId;
      if (
        (activeId.kind == "person" && activeId.id == userId) ||
        this.$store.state.phoneQuering == userId
      ) {
        return "royalblue";
      } else {
        return "transparent";
      }
    },
    colorNoSel() {
      if (this.$store.state.activeId.kind == "none") {
        return "royalblue";
      } else {
        return "transparent";
      }
    },
    colorPerson(userId) {
      // if (userId == this.$store.state.myId) {
      //   return "gray";
      // } else {
      //   return "white";
      // }
      return "lightgray";
    },
    endDelay() {
      if (this.endTimer != null) {
        clearTimeout(this.endTimer);
      }
      this.endTimer = setTimeout(() => {
        this.$emit("groupSelected");
      }, 200);
    },
  },
  beforeCreate() {
    let users = this.$store.state.users;
    let groups = this.$store.state.groups;
    const h = {};
    for (let uid of Object.keys(users)) {
      let gid = users[uid].group_id;
      // if (uid != this.$store.state.myId) {
      if (uid != this.$store.state.myId && users[uid].user_type == "amt") {
        let user = { user_id: uid, user_name: users[uid].user_name };
        if (h[gid] == null) {
          h[gid] = [user];
        } else {
          h[gid].push(user);
        }
      }
    }
    this.myGroup = [];
    for (let g of groups) {
      let group = {
        group_id: g.group_id,
        group_name: g.group_name,
        persons: [],
      };
      if (h[g.group_id] != null) {
        for (let user of h[g.group_id]) {
          group.persons.push(user);
        }
      }
      this.myGroup.push(group);
    }
  },
  mounted() {
    const gl = document.getElementById("groupList");
    let left = (gl.parentNode.clientWidth - gl.clientWidth) / 2;
    if (left < 10) {
      left = 10;
    }
    gl.style.left = left + "px";
  },
};
</script>
<style>
div#groupList {
  position: absolute;
  top: 15vh;
  left: 0;
  width: 20rem;
  max-height: 68vh;
  min-height: 5vh;
  background-color: #39a2c0;
  font-size: 1.4rem;
  color: white;
  overflow: auto;
  z-index: 21;
  cursor: pointer;
  text-align: left;
}
div#groupList ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
div#groupList input {
  display: none;
}
div#groupList li {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}
.groupListImg {
  border: solid 2px #fff;
  padding: 6px 3px;
  margin-left: 10px;
}
</style>
