<template>
  <div>
    <map-popup :pos="pos" ref="map_popup"></map-popup>
  </div>
</template>

<script>
/* eslint-disable */
import { Map, View, Feature, Overlay } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";
import * as olEasing from "ol/easing";
import * as olObservable from "ol/Observable";
import { trackEvent } from "./index.js";

// const MapPopup = () => import("@/components/MapPopup.vue");
import MapPopup from "@/components/MapPopup.vue";

var ol = {};
ol.Map = Map;
ol.View = View;
ol.layer = olLayer;
ol.control = olControl;
ol.interaction = olInteraction;
ol.proj = olProj;
ol.source = olSource;
ol.style = olStyle;
ol.geom = olGeom;
ol.Feature = Feature;
ol.Overlay = Overlay;
ol.easing = olEasing;
ol.Observable = olObservable;

const TRACK_PERIOD = 60 * 1000; //1分
const SPEED_LIMIT = 300; // 移動速度上限（km/h）
const DOWN_TIME = 60 * 60 * 1000; // 端末停止時間（1時間）　DOWN_TIME以上軌跡記録が無い場合には、同じ軌跡として扱わない。
// const colors = ["#e83e8c", "#dc3545", "#fd7e14", "#ffc107", "#28a745"];
// const colors = [
//   "#F44336", "#E91E63", "#9C27B0", "#673AB7", "#3F51B5",
//   "#2196F3", "#03A9F4", "#00BCD4", "#009688", "#4CAF50",
//   "#8BC34A", "#CDDC39", "#FFEB3B", "#FF9800",
// ]
const colors = [
  "#F44336",
  "#9C27B0",
  "#3F51B5",
  "#03A9F4",
  "#009688",
  "#8BC34A",
  "#FFEB3B",
];

export default {
  data: function () {
    return {
      trackLayer: null,
      isVisible: false,
      isActive: false,
      tracks: {},
      last_timestamp: null,
      last_pt: {},
      // last_feature: null,
      // original_style: null
      pos: null,
    };
  },
  components: {
    mapPopup: MapPopup,
  },
  computed: {
    users() {
      return this.$store.getters["amt/users"];
    },
    map() {
      return this.$store.state.map;
    },
    myId() {
      return this.$store.state.myId;
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
    socketio() {
      return this.$store.getters["socketio"];
    },
    state_point_num() {
      return this.$store.state.track_point || 1200;
    },
    rec_flag() {
      return this.$store.getters["rec_flag"];
    },
  },
  methods: {
    // 2点間の距離（入力は緯度経度、返値はkm）
    distance(lat1, lng1, lat2, lng2) {
      const R = Math.PI / 180;
      lat1 *= R;
      lng1 *= R;
      lat2 *= R;
      lng2 *= R;
      return (
        6371 *
        Math.acos(
          Math.cos(lat1) * Math.cos(lat2) * Math.cos(lng2 - lng1) +
            Math.sin(lat1) * Math.sin(lat2)
        )
      );
    },
    // 2点間を移動した際の速度（入力は緯度経度と秒、返値は時速km/h）
    // pt = [lat, lng, timestamp];
    speed(pt1, pt2) {
      if (pt1[2] === pt2[2]) {
        return 0;
      } else {
        const L = this.distance(pt1[0], pt1[1], pt2[0], pt2[1]);
        const T = Math.abs(pt1[2] - pt2[2]) / 1000;
        console.log(`L=${L}km T=${T}s 時速=${(L / T) * 3600}`);
        return (L / T) * 3600;
      }
    },
    user_color(user_id) {
      // 色指定が無い場合、適当に決める
      const user_order = Object.keys(this.users).indexOf(user_id);
      const color = user_order === -1 ? colors[0] : colors[user_order];
      return (user_id === this.myId) & this.$store.state.myColor || color;
    },
    init() {
      this.trackLayer = new ol.layer.Vector({
        source: new ol.source.Vector({ features: [] }),
      });
      this.map.addLayer(this.trackLayer);
      this.trackLayer.setVisible(false);
      this.trackLayer.setZIndex(1); //@todo 仮で設置。今後storeか設定ファイルで管理する予定
    },

    // ------------------------------------------------------------------------------------------
    // on_renew: renew通知処理
    // TRACK_PERIOD毎にthis.tracksに位置情報を配置して描画する。
    // ------------------------------------------------------------------------------------------
    // datas = { user1: track, user2: track, ...}
    // track = [pt1, pt2, ...]
    // pt = <renew通知オブジェクト。lat,lng,timestampを含む>

    on_renew(msg) {
      const datas = typeof msg == "string" ? JSON.parse(msg) : msg;

      const on_time = datas.system.on_time;
      const on_chronology = datas.system.on_chronology;
      this.$store.commit("set_on_chronology", on_chronology);

      const user_ids = Object.keys(datas).filter((id) => {
        return id !== "system";
      });
      if (!on_time) {
        user_ids.forEach((id) => {
          if (!this.tracks[id]) this.tracks[id] = [];

          // GPSのブレ補正
          // 前回値と今回値の距離と時間で速度を算出し、SPEED_LIMIT以上の場合、
          if (!this.last_pt[id]) {
            this.last_pt[id] = [
              datas[id].lat,
              datas[id].lng,
              datas[id].timestamp,
            ];
          } else {
            const cur_track = [
              datas[id].lat,
              datas[id].lng,
              datas[id].timestamp,
            ];
            const sp = this.speed(this.last_pt[id], cur_track);
            if (sp > SPEED_LIMIT) {
              // 移動速度が上限値を越えていたら、以下の処理はやらずに抜ける。
              return;
            } else {
              // 今回値が有効値であれば、今回値を前回値として保存
              this.last_pt[id] = [
                datas[id].lat,
                datas[id].lng,
                datas[id].timestamp,
              ];
            }
          }

          const on_video_flag = datas[id].on_video_flag;
          datas[id].video_file = on_video_flag
            ? `${id}_${this.last_timestamp}.webm`
            : null;

          // timestampの重複排除
          const is_same_timestamp = this.tracks[id]
            .map((pt) => pt.timestamp)
            .includes(datas[id].timestamp);
          // if ( datas[id].is_valid && !is_same_timestamp ) {
          if (!is_same_timestamp) {
            this.tracks[id].unshift(datas[id]);
          }
          this._on_track_draw(this.tracks[id], this.last_timestamp);
        });
      } else {
        // 毎分0秒時
        trackEvent.$emit("ON_TIME", {
          timestamp: on_time,
          last_timestamp: this.last_timestamp,
        });
        this.last_timestamp = on_time;

        user_ids.forEach((id) => {
          if (!this.tracks[id]) this.tracks[id] = [];

          // this._on_track_draw(this.tracks[id], this.last_timestamp);

          // 自分が映像取得している場合には、ここで発火させる
          if (this.myId === id && this.rec_flag) {
            trackEvent.$emit("ON_VIDEO");
          }

          const on_video_flag = datas[id].on_video_flag;
          datas[id].video_file = on_video_flag
            ? `${id}_${this.last_timestamp}.webm`
            : null;
          this.tracks[id] = [datas[id], this.tracks[id][0]];
        });
      }
    },

    // datas = { user1: track, user2: track, ...}
    // track = [pt1, pt2, ...]
    // pt = <renew通知オブジェクト。lat,lng,timestampを含む>

    // on_inittrack
    // 軌跡表示ONにした際、その時にサーバ上に記録されていた軌跡を描画する。
    on_inittrack(msg) {
      const datas = typeof msg == "string" ? JSON.parse(msg) : msg;
      this.on_track_erase();

      Object.keys(datas).forEach((id) => {
        const track = datas[id];

        // （１）現時点でログインしていないユーザの軌跡は表示しない。
        if (!Object.keys(this.users).includes(id)) return;
        // （３）グループ情報を確認して自分の選択中グループでない場合は描画しない
        if (track[0].group_id !== this.group_id) return;

        // 軌跡、最終地点の記録時刻の端数処理
        const reg_time =
          Math.floor(track[0].timestamp / TRACK_PERIOD) * TRACK_PERIOD;

        // 最終地点からTRACK_PERIODまでずらした地点までを_trackに収容して描画する
        let _track = [];
        let cur_time = reg_time;
        track.forEach((pt) => {
          if (pt.timestamp > cur_time) {
            _track.push(pt);
          } else {
            this._on_track_draw(_track, cur_time);
            // （２）DOWN_TIME以上経過した経路は同じ経路として描画しない
            const last_pt = _track[_track.length - 1];
            if (pt.timestamp - last_pt.timestamp > DOWN_TIME) {
              _track = [pt];
            } else {
              _track = [last_pt, pt];
            }
            cur_time = cur_time - TRACK_PERIOD;
          }
        });
      });
    },
    // こちらは、一旦、全部消す処理
    // 一番最初に実行する。
    on_track_erase() {
      const vecLayerSource = this.trackLayer.getSource();
      vecLayerSource.getFeatures().forEach((feature) => {
        vecLayerSource.removeFeature(feature);
      });
    },
    // _on_track_draw
    // 基本的に「描画処理」だけを行う。
    _on_track_draw(pts, timestamp) {
      const end_pt = pts[0];
      const first_pt = pts.length >= 2 ? pts[pts.length - 2] : null;
      if (!end_pt || !first_pt) return;

      // console.log(JSON.stringify(end_pt));
      // console.log(end_pt.video_file);
      // 緯度経度変換
      const _pts = pts.map((pt) => {
        return ol.proj.transform([pt.lng, pt.lat], "EPSG:4326", "EPSG:3857");
      });
      // 描画レイヤからfeatureリストを取得してidが同じなら、一旦削除してから描画
      const vecLayerSource = this.trackLayer.getSource();
      vecLayerSource.getFeatures().forEach((feature) => {
        if (
          feature.get("timestamp") == timestamp &&
          feature.get("id") == end_pt.user_id
        ) {
          vecLayerSource.removeFeature(feature);
        }
      });
      let geometry = new ol.geom.LineString(_pts);
      const feature_option = {
        geometry: geometry,
        id: end_pt.user_id,
        user_name: end_pt.user_name,
        timestamp: timestamp,
        video_file:
          (end_pt ? end_pt.video_file : null) ||
          (first_pt && first_pt.video_file ? first_pt.video_file : null),
      };
      const feature = new ol.Feature(feature_option);
      const style = new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: 5,
          color: this.user_color(end_pt.user_id),
          lineDash: feature.get("video_file") ? null : [5, 10],
        }),
      });
      feature.setStyle(style);
      // 描画
      vecLayerSource.addFeature(feature);
    },
  },
  mounted() {
    console.log(
      `trackLayerが読み込まれました。${new Date().toLocaleTimeString()}`
    );
    this.init();
    this.socketio.on("renew", (msg) => {
      this.on_renew(msg);
    });
    // this.socketio.on("track", (msg) => {
    //   this.on_track(msg);
    // });
    this.socketio.on("inittrack", (msg) => {
      this.on_inittrack(msg);
    });
    trackEvent.$on("SETOPACITY", (val) => {
      if (this.trackLayer) {
        this.trackLayer.setOpacity(val);
      }
    });
    trackEvent.$on("SETVISIBLE", (val) => {
      console.log(
        `trackLayerは${
          val ? "表示状態" : "非表示状態"
        }になりました。${new Date().toLocaleTimeString()}`
      );
      if (this.trackLayer) {
        this.trackLayer.setVisible(val);
        if (val) {
          this.socketio.emit("inittrack");
        }
        this.isVisible = val;
      }
    });
    trackEvent.$on("SETZINDEX", (val) => {
      if (this.trackLayer) {
        this.trackLayer.setZIndex(val);
      }
    });
    trackEvent.$on("SETACTIVE", (val) => {
      this.isActive = val;
      if (val) {
        if (!this.isVisible) {
          this.isVisible = true;
          this.trackLayer.setVisible(true);
        }
      }
    });

    // let last_feature = null;
    // let original_style = null;
    this.map.on("click", (ev) => {
      this.pos = ev.coordinate;
      const pos = this.$refs.map_popup.getPos();
      if (pos) {
        this.$refs.map_popup.hide();
      }
      // if (this.last_feature && this.original_style) {
      //   this.last_feature.setStyle(this.original_style);
      // }
      this.$refs.map_popup.on_click(ev, this.trackLayer);
    });
  },
  beforeDestroy() {
    this.socketio.off("renew", this.on_renew);
    this.socketio.off("track", this.on_track);
  },
};
</script>
