<template>
  <div id="workList">
    <ul>
      <li
        v-for="kind in kinds"
        v-bind:key="kind.no"
        v-bind:style="{ backgroundColor: colorLine(kind.no) }"
        v-on:click="onClickKind(kind.no)"
      >
        &nbsp;
        <img v-bind:src="flagSrc(kind.no)" />
        <span>&nbsp;&nbsp;{{ kind.name }}</span>
        &nbsp;
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      selectedNo: -1,
      kinds: [
        {
          no: 1,
          name: "巡回中",
        },
        {
          no: 2,
          name: "緊急対応中",
        },
        {
          no: 3,
          name: "休憩中",
        },
      ],
    };
  },
  methods: {
    onClickKind(no) {
      this.selectedNo = no;
      this.endDelay();
    },
    colorLine(no) {
      if (no == this.selectedNo) {
        return "royalblue";
      } else {
        return "transparent";
      }
    },
    endDelay() {
      if (this.endTimer != null) {
        clearTimeout(this.endTimer);
      }
      this.$store.commit("setWorkStatus", this.selectedNo);
      this.endTimer = setTimeout(() => {
        this.$parent.workListShowing = false;
        //this.$parent.$parent.isModal = false;
        this.$store.commit("setMask", false);
      }, 400);
    },
    flagSrc(workStatus) {
      switch (workStatus) {
        case 2:
          return require("../assets/flagRed10.png");
        case 3:
          return require("../assets/flagYellow10.png");
        default:
          return require("../assets/flag10.png");
      }
    },
  },
  computed: {},
  mounted() {
    const wl = document.getElementById("workList");
    wl.style.left = window.innerWidth - wl.clientWidth + "px";
    this.selectedNo = this.$store.state.workStatus;
  },
};
</script>
<style>
div#workList {
  position: absolute;
  top: 8vh;
  left: 0;
  width: 12rem;
  max-height: 68vh;
  min-height: 10vh;
  background-color: #39a2c0;
  font-size: 1.4rem;
  color: white;
  overflow: auto;
  z-index: 21;
  filter: drop-shadow(-0.3rem 0.3rem 0.3rem rgba(0, 0, 0, 0.6));
  cursor: pointer;
}
div#workList ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
div#workList input {
  display: none;
}
div#workList img {
  width: 1.2rem;
  height: 1.2rem;
}
</style>
