<template>
  <div class="videoContainer2" ref="videoContainer2">
    <video-unit
      v-for="(user, remote_id) in remote_users"
      :key="remote_id"
      :is_showed="is_showed"
      @zIndex_up="zIndex_down"
    >
      <remote-video
        :user="user"
        :remote_id="remote_id"
        :mode="mode"
        @call_end="call_end"
      >
      </remote-video>
    </video-unit>
  </div>
</template>

<script>
import VideoUnit from "./VideoUnit";
import RemoteVideo from "./RemoteVideo";

export default {
  components: {
    videoUnit: VideoUnit,
    remoteVideo: RemoteVideo,
  },
  data() {
    return {
      mode: "group",
    };
  },
  methods: {
    call_end(id) {
      delete this.remote_users[id];
    },
    zIndex_down(vm) {
      const child_elms = this.$refs.videoContainer2.children;
      for (let i = 0; i < child_elms.length; i++) {
        child_elms[i].style.zIndex = 2;
      }
      // vm.zIndex = 10;
      vm.$refs.videoUnit.style.zIndex = 10;
    },
  },
  computed: {
    socketio() {
      return this.$store.getters["socketio"];
    },
    is_showed() {
      return this.$store.getters["rtc/remote_show"];
    },
    is_in_private() {
      return this.$store.getters["per"] == "person";
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
    remote_users() {
      const remote_users = {};

      // 自分ではなく、且つ、個別通話中でないremote_idを抽出する。
      // 2021.04.07 user_typeによる判定を追加
      Object.keys(this.users).forEach((user_id) => {
        if (
          user_id != this.myId &&
          this.users[user_id].per !== "person" &&
          this.users[user_id].user_type === "amt" &&
          this.users[user_id].group_id === this.group_id
        ) {
          remote_users[user_id] = this.users[user_id];
        }
      });

      if (this.is_in_private) {
        return {};
      } else {
        return remote_users;
      }
      // return remote_users;
    },
    users() {
      return this.$store.getters["amt/users"];
    },
    myId() {
      return this.$store.getters["amt/myId"];
    },
    myPosition() {
      return this.$store.getters["amt/myPosition"];
    },
    personal_remote_id() {
      return this.$store.getters["rtc/personal_id"];
    },
  },
};
</script>

<style>
@media screen and (min-width: 0px) and (max-width: 479px) {
  /* 縦向き */
  .videoContainer2 {
    width: 150px;
  }
}
@media screen and (min-width: 480px) {
  /* 横向き */
  .videoContainer2 {
    width: 20vw;
  }
}
.videoContainer2 {
  display: flex;
  flex-direction: column;
  position: absolute;
  z-index: 100;
  top: 0px;
  right: 0px;
}

.videoContainer2::-webkit-scrollbar {
  display: none;
}
</style>
