<template>
  <div v-if="ready" class="home">
    <amtHeader class="home_header"></amtHeader>
    <!-- <baseField class="home_base" @groupSelected="groupSelected"></baseField> -->
    <baseField class="home_base"></baseField>
    <mapField ref="map" class="home_map"></mapField>
    <div class="home_mask" v-if="$store.state.mask"></div>

    <modalDialog
      v-if="alert.show"
      v-on:modalDialogYes="$store.commit('alertOff')"
    >
      <span slot="header">{{ alert.header }}</span>
      <span slot="body">{{ alert.body }}</span>
    </modalDialog>

    <modalDialog v-if="confirm.show">
      <span slot="header">{{ confirm.header }}</span>
      <span slot="body">{{ confirm.body }}</span>
      <div slot="footer">
        <b-button variant="primary" @click="confirm.funcNo()"
          >キャンセル</b-button
        >
        <b-button variant="primary" @click="confirm.funcYes()"
          >ログアウト</b-button
        >
      </div>
    </modalDialog>

    <!-- メッセージ用モーダルダイアログ -->
    <modalDialog v-if="is_dialog_visible && user">
      <span slot="header">AirMultiTalk</span>
      <span slot="body"
        >{{ group_name }}/{{ user.user_name }}としてログインしました</span
      >
      <div slot="footer">
        <button class="btn btn-primary" @click="close_msg_dialog">はい</button>
      </div>
    </modalDialog>
    <audio ref="sound_el" :src="this.sound_file"></audio>
  </div>
</template>

<script>
/* eslint-disable */
import userLocationMaker from "@/components/userLocation.js";
import { loggerMaker } from "@/components/logger.js";
import AmtHeader from "@/components/HeaderComp.vue";
import BaseField from "@/components/BaseField.vue";
import MapField from "@/components/MapField.vue";
import ModalDialog from "@/components/ModalDialog.vue";

import io from "socket.io-client";
import { v4 as uuidv4 } from "uuid";
import { evEmitter_client, evEmitter_serv } from "../components/evemitter";

export default {
  data() {
    return {
      mqlDisp: window.matchMedia("(min-width: 480px)"),
      sound_file: require("@/assets/button.mp3"),
      is_dialog_visible: false,
      ready: false,
    };
  },
  components: {
    amtHeader: AmtHeader,
    baseField: BaseField,
    mapField: MapField,
    modalDialog: ModalDialog,
  },
  methods: {
    initExec() {
      const logger = loggerMaker(this.$store);
      this.$store.commit("setLogger", logger);
      this.$store.state.logger.setMode(this.$store.state.logMode);
      this.$store.state.logger.setLevel(0);
      const userLocation = userLocationMaker(this.$store.state.socketio);
      this.$store.commit("setUserLocation", userLocation);
      this.$store.state.userLocation.initPos(
        this.$store.state.myId,
        this.$store.state.myName
      );
    },
    async logout() {
      // location.href = "/logout";
      if (this.$store.state.renew_timerId) {
        console.log(
          `-------CLEAR TIMER (RENEW ID:${this.$store.state.renew_timerId})-------`
        );
        clearInterval(this.$store.state.renew_timerId);
        this.$store.state.socketio.disconnect();
      }
      await this.ldb.clear();
      this.$router.push("/Regist");
    },
    toFullScreen() {
      let doc = window.document;
      let docEl = doc.documentElement;
      let requestFullScreen =
        docEl.requestFullscreen ||
        docEl.mozRequestFullScreen ||
        docEl.webkitRequestFullScreen ||
        docEl.msRequestFullscreen;
      if (
        !doc.fullscreenElement &&
        !doc.mozFullScreenElement &&
        !doc.webkitFullscreenElement &&
        !doc.msFullscreenElement
      ) {
        requestFullScreen.call(docEl);
      }
    },
    display() {
      this.mqlDisp.addEventListener("change", (ev) => {
        this.$store.commit("set_screen_landscape", ev.matches);
      });
    },
    logDebug(msg) {
      this.$store.state.logger.debug(msg);
    },
    log(msg) {
      this.$store.state.logger.info(msg);
    },
    logError(msg) {
      this.$store.state.logger.error(msg);
    },
    sound_on(url) {
      if (url) {
        this.$refs.sound_el.src = url;
        this.$refs.sound_el.onended = () => {
          this.$store.commit("rtc/set_remote_audio_stream", null);
          // this.$store.commit("ptt/set_recv_active", false);
          this.$refs.sound_el.src = this.sound_file;
        };
        this.$refs.sound_el.onloadedmetadata = () => {
          this.$store.commit(
            "rtc/set_remote_audio_stream",
            this.$refs.sound_el.captureStream()
          );
          // this.$store.commit("ptt/set_recv_active", true);
          this.$refs.sound_el.play();
          this.$refs.sound_el.onloadedmetadata = null;
        };
      } else {
        this.$refs.sound_el.play();
      }
    },
    toast_on(txt, variant) {
      this.$bvToast.toast(txt, {
        title: "AirMultiTalk",
        toaster: "b-toaster-top-center",
        variant: variant || "danger",
        autoHideDelay: 3000,
        appendToast: true,
      });
    },
    close_msg_dialog() {
      this.is_dialog_visible = false;
      this.sound_on();
    },
  },
  async mounted() {
    // -------------------------------------------------------------
    // App.vueから移動　設定情報の取得
    // 多重ログインの識別に使用するブラウザのタブごとにユニークなID
    // const client_id = uuidv4();
    // const socketio = io.connect();
    // // const socketio = io.connect({
    // //   withCredentials: true,
    // // });
    // socketio.on("connect", () => {
    //   socketio.emit("join", { client_id });
    // });
    // this.$store.commit("setSocketio", socketio);

    if (this.review) {
      const evEmitter = evEmitter_client();
      this.$store.commit("setSocketio", evEmitter);
    } else {
      const client_id = uuidv4();
      const socketio = io.connect();
      // const socketio = io.connect({
      //   withCredentials: true,
      // });
      socketio.on("connect", () => {
        socketio.emit("join", { client_id });
      });
      this.$store.commit("setSocketio", socketio);
    }

    // サーバから設定取得
    await this.$store.dispatch("init");
    // いろいろと初期化
    this.initExec();
    this.ready = true;
    // -------------------------------------------------------------
    const prevPage = this.$store.state.page;
    this.$store.commit("setPage", "map");
    this.$store.commit("set_sound_on", this.sound_on);
    this.$store.commit("set_toast_on", this.toast_on);

    const on_renew = (msg) => {
      // console.log(msg);
      // if (!this.review && this.local_audio_stream == null) return;

      const newUsers = JSON.parse(msg);
      if (!this.review) {
        if (this.local_audio_stream == null) return;

        const mySid = this.$store.state.socketio.id;

        // if (!Object.values(newUsers).some((u) => u.sid === mySid && !this.review)) {
        if (!Object.values(newUsers).some((u) => u.sid === mySid)) {
          // 自分のsocket.idが見つからない場合、
          // 自端末のrenew通知がまだサーバに届いていないか、認証エラーで破棄されている
          return;
        }
      }
      // 追加：自分のgroup_idと一致するモノだけ表示する2022/07/25
      const _newUsers = {};
      Object.keys(newUsers)
        .filter((id) => {
          return id !== "system" && newUsers[id].group_id === this.group_id;
        })
        .forEach((id) => {
          _newUsers[id] = newUsers[id];
        });
      this.$refs.map.renew(_newUsers);

      // サーバーのデータを現在ユーザデータとする。
      this.$store.commit("setUsers", newUsers);
    };
    this.$store.state.socketio.on("renew", on_renew);
    this.display();
    this.is_dialog_visible = true;
  },
  beforeDestroy() {
    // this.$store.state.socketio.off("renew");
    // evEmitter.off("review", on_review);
  },
  computed: {
    myId() {
      return this.$store.getters["amt/myId"];
    },
    group_id() {
      return this.$store.getters["group_id"];
    },
    users() {
      return this.$store.getters["amt/users"];
    },
    user() {
      return this.myId && this.users[this.myId];
    },
    group_name() {
      const groups = this.$store.state.groups;
      const group = Object.values(groups).find((gr) => {
        return gr.group_id == this.group_id;
      });
      return group && group.group_name ? group.group_name : "";
    },
    local_audio_stream() {
      return this.$store.getters["rtc/local_audio_stream"];
    },
    ptt_type() {
      return this.$store.state.ptt_type;
    },
    ptt_type_socketio() {
      return this.ptt_type == "socketio";
    },
    confirm() {
      return this.$store.state.confirm;
    },
    alert() {
      return this.$store.state.alert;
    },
    review() {
      return this.$store.getters["review"];
    },
  },
};
</script>

<style>
@media screen and (min-width: 0px) and (max-width: 479px) {
  .home {
    display: grid;
    height: 100%;
    grid-template-rows: 10% 90%;
    grid-template-columns: 0% 100%;
    transition: all 0.3s ease-in-out 0s;
  }
  .home_base {
    display: none;
  }
}
@media screen and (min-width: 480px) {
  .home {
    display: grid;
    height: 100%;
    grid-template-rows: 10% 90%;
    grid-template-columns: 15% 85%;
  }
  .home_base {
    display: block;
  }
}

.home_header {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
}
.home_base {
  grid-row: 2 / 3;
  grid-column: 1 / 2;
  position: relative;
}
.home_map {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
  position: relative;
}
.home_mask {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  display: table;
  transition: opacity 0.3s ease;
}
</style>
