import $store from "@/store";
import reviewComponent from "./reviewComponent.vue";
// import mitt from "mitt";
import { evEmitter_client, evEmitter_serv } from "../../components/evemitter";

import Vue from "vue";
export const reviewEvent = new Vue();
// export const evEmitter = mitt();
export const evEmitter = evEmitter_client();
export const evEmitter_s = evEmitter_serv();

export function reviewLayer() {
  const noop = () => {};
  let isActive = false;

  function add() {
    // $store.commit("amt/addComponent", reviewComponent);
  }

  function setOpacity(val) {
    reviewEvent.$emit("SETOPACITY", val);
  }

  function setVisible(val) {
    reviewEvent.$emit("SETVISIBLE", val);
  }

  function setZIndex(val) {
    reviewEvent.$emit("SETZINDEX", val);
  }

  function setActive(active) {
    isActive = active;

    if (isActive) {
      $store.commit("amt/addComponent", reviewComponent);
      $store.commit("amt/setSubMenu", "ReviewLayer");
    } else {
      $store.commit("amt/removeComponent", reviewComponent);
      $store.commit("amt/setSubMenu", null);
    }
  }

  return {
    add, // マップ初期化時に呼び出し
    remove: noop, // マップ破棄時に呼び出し
    update: noop, // 現在のバージョンでは呼び出されない
    setOpacity,
    setVisible,
    setZIndex,
    setActive,
  };
}
