<template>
  <div class="memo_makes">
    <div class="memo_makes_header">
      <h3>メモ作成画面</h3>
      <button class="closeBtn" title="閉じる">
        <!-- 非表示化するボタン -->
        <img
          src="@/assets/close05.png"
          class="btnImg"
          @click="close_button()"
        />
      </button>
    </div>

    <div class="arow-wrap">
      <div>
        <a href="#memo_video" class="arrow-left" v-show="!left_view" />
      </div>
      <div class="memo_makes_middle">
        <div class="slides" @scroll="is_left">
          <div class="video_cont">
            <video
              id="memo_video"
              ref="video"
              muted
              playsinline
              autoplay
              :class="{ front_class: is_front }"
            ></video>
            <div class="text_fade">写真</div>
          </div>
          <textarea id="memo_text" v-model="text_memo" />
        </div>
      </div>
      <div>
        <a href="#memo_text" class="arrow-right" v-show="left_view" />
      </div>
    </div>

    <div class="status_button">
      <!-- Speech Recognition -->
      <a href="#memo_text">
        <div
          v-if="is_not_ios"
          class="memolayer_btn memolayer_btn_margin"
          :style="mic_btn_color"
          @click="on_click_mic"
        >
          <img :src="icon_mic" class="memolayer_btn_icon" />
        </div>
      </a>

      <!-- 写真 -->
      <div
        v-show="photo_btn_state == 'close'"
        @click="on_UPLOAD"
        class="memolayer_btn"
        title="アップロード"
      >
        <img :src="icon_upload" class="memolayer_btn_icon" />
      </div>
      <a href="#memo_video">
        <div
          v-show="photo_btn_state == 'PHOTO'"
          @click="on_PHOTO"
          class="memolayer_btn"
          title="撮影"
        >
          <img :src="icon_shatter" class="memolayer_btn_icon" />
        </div>
      </a>
    </div>
    <modalDialog v-if="is_dialog_visible">
      <span slot="header">地点メモ作成</span>
      <span slot="body" style="color: black">{{ dialog_msg }}</span>
      <div slot="footer">
        <button class="btn btn-primary" @click="dialog_yes">はい</button>
        <button class="btn btn-primary" @click="dialog_no">いいえ</button>
      </div>
    </modalDialog>
  </div>
</template>

<script>
/* eslint-disable */
import { memoEvent } from "./index.js";
import { sp_start, sp_stop } from "./sp_recognition";
import ModalDialog from "@/components/ModalDialog.vue";
import { memo_common } from "./common.js";

// テキスト入力状態でボタン押下時すると次の作成タイミングで<>ボタンが出なくなる
// ※compudedのon_left_endとon_right_endが呼ばれなくなる
// テキスト入力解除と初期化処理の時間を300msec開ける(200msecではだめだった)
const CLOSE_TIMEOUT = 300;
export default {
  mixins: [memo_common],
  components: {
    modalDialog: ModalDialog,
  },
  data() {
    return {
      btn_title: "",
      // テキストメモ
      text_memo: "",
      // 動画メモ
      url: "/api/upload",
      photo_btn_state: "PHOTO",
      movieDataBuf: null,
      moveDataChunks: [],
      // 写真メモ
      icon_upload: require("./assets/icon_upload.png"),
      icon_shatter: require("@/assets/photo_shatter.png"),
      icon_mic: require("@/assets/mic.png"),
      imageCapture: null,
      now_keep_out: false,
      imageDataBuf: null,
      mic_btn_flag: false,
      left_view: true,
      is_dialog_visible: false,
      dialog_msg: "",
    };
  },
  mounted() {
    console.log("HELLO, I am PhotoMake.vue");
    this.init();
  },
  methods: {
    async dialog_yes() {
      this.is_dialog_visible = false;
      if (this.mic_btn_flag) {
        this.mic_btn_flag = false;
        sp_stop();
        await this.mic_on_off("start");
      }
      memoEvent.$emit("BACK_MEMO");
      this.init_clear();
    },
    dialog_no() {
      this.is_dialog_visible = false;
    },
    init_clear() {
      this.photo_btn_state = "PHOTO"; // 初期化へ戻す
      this.imageDataBuf = null;
      this.moveDataChunks = [];
      this.movieDataBuf = null;
      this.file_name = "";
      this.text_memo = "";
      this.left_view = true;
      history.replaceState(null, "", location.pathname);
      console.log("end_flag: 終了だ");
      this.$emit("end_flag"); // これでコンポーネントを終了する。
    },
    on_UPLOAD() {
      // サーバへアップロード
      const b64 = this.drawCanvas(this.imageDataBuf);
      this.post_file(this.file_name + ".jpg", b64);
      // 地点メモのピン建て
      const data = { file_name: this.file_name, info: this.text_memo };
      memoEvent.$emit("SEND_JSON_PHOTO", data);

      memoEvent.$emit("BACK_MEMO");
      this.init_clear();
    },
    on_PHOTO() {
      this.$store.state.sound_on();
      // 画像変換中はクリックされても処理しない。
      if (this.now_keep_out) return;

      // シャッターを押した感じとして、映像を一時停止
      this.$refs.video.pause();
      this.now_keep_out = true;

      const video = document.createElement("video");
      const canvas = document.createElement("canvas");
      // this.imageCaptureTakePhoto(this.local_video_stream)
      this.imageCaptureTakePhoto(this.local_video_stream, canvas, video)
        .then((blob) => {
          return createImageBitmap(blob)
        })
        .then((imageBitmap) => {
          // 画像変換中フラグ解除
          this.now_keep_out = false;
          // シャッターを押した時点がファイル名の日時
          this.file_name = this.gen_file_name() + ".jpg";
          // シャッターを切った時点で一旦データ保存
          this.imageDataBuf = imageBitmap;
          this.photo_btn_state = "close";
        })
        .catch((error) => {
          console.log(error);
          this.err_exit(error);
        });
    },
  },
};
</script>

