<template>
  <!-- POPUP -->
  <div id="map_popup_elm" class="map_popup">
    <div>
      <p>{{ popup_msg_username }}</p>
      <p>{{ popup_msg_timestamp }}</p>
      <video
        class="popup_video"
        controls
        v-if="popup_msg_url"
        :src="popup_msg_url"
      ></video>
    </div>
  </div>
</template>

<script>
import { Map, View, Feature, Overlay } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";
import * as olEasing from "ol/easing";
import * as olObservable from "ol/Observable";

var ol = {};
ol.Map = Map;
ol.View = View;
ol.layer = olLayer;
ol.control = olControl;
ol.interaction = olInteraction;
ol.proj = olProj;
ol.source = olSource;
ol.style = olStyle;
ol.geom = olGeom;
ol.Feature = Feature;
ol.Overlay = Overlay;
ol.easing = olEasing;
ol.Observable = olObservable;

export default {
  props: ["pos"],
  data() {
    return {
      map_popup: null,
      popup_msg_username: null,
      popup_msg_timestamp: null,
      popup_msg_url: null,
      last_feature: null,
    };
  },
  // watch: {
  //   pos() {
  //     const cur_pos = this.getPos();
  //     if (!cur_pos) {
  //       this.show(this.pos);
  //     } else {
  //       this.hide();
  //     }
  //   }
  // },
  computed: {
    map() {
      return this.$store.state.map;
    },
  },
  methods: {
    init_map_popup() {
      // const map = this.$store.getters["amt/map"];
      const map_popup_elm = document.getElementById("map_popup_elm");
      this.map_popup = new ol.Overlay({
        element: map_popup_elm,
        positioning: "bottom-center",
        autoPan: true,
        autoPanAnimation: {
          duration: 250,
        },
      });
      this.map.addOverlay(this.map_popup);
      this.map_popup.setPosition(undefined);
    },
    getPos() {
      return this.map_popup.getPosition();
    },
    show(pos) {
      // this.popup_message = content;
      this.map_popup.setPosition(pos);
    },
    hide() {
      this.map_popup.setPosition(undefined);
      this.last_feature.setStyle(this.original_style);
      this.last_feature = null;
    },

    // DrawComponent.vueからのパクリ
    // LineStringでは、feature側サイズが極小になるため、指によるタッチが困難なため、
    // 指によるタッチイベント発生時に周囲で閾値以内のfeatureにイベントを発火させる。
    on_click(ev, layer) {
      const myCoord = ev.coordinate;
      // 閾値
      const max = 250 / 2 ** (this.map.getView().getZoom() - 14);
      let minFeature;
      let minDist = -1;

      // この処理バッカリ。。。。
      const vecLayerSource = layer.getSource();
      vecLayerSource.getFeatures().forEach((feature) => {
        // 最短距離の場所にある座標を取得
        const geometry = feature.get("geometry");
        const closest = geometry.getClosestPoint(myCoord);

        // 各座標間の距離
        const xDist = Math.abs(closest[0] - myCoord[0]);
        const yDist = Math.abs(closest[1] - myCoord[1]);
        if (xDist + yDist <= max) {
          // 自座標と一番近い点のLineStringを作成し、距離を取得する
          const ls = new ol.geom.LineString([closest, myCoord]);
          const dist = ls.getLength();
          if (minDist < 0 || dist < minDist) {
            // 一番近くの点の情報に更新
            minDist = dist;
            minFeature = feature;
          }
        }
      });
      // 最小距離が0以上(=該当するものがあった判定)なら描画レイヤーからその描画単位で削除する
      if (minDist >= 0) {
        // alert(minFeature.get("id"));
        console.log(`あったよ: ${minFeature.get("id")}`);
        this.last_feature = minFeature;
        this.original_style = minFeature.getStyle();
        this.on_featureTouch(ev, minFeature);
      }
    },
    on_featureTouch(ev, feature) {
      const user_name = feature.get("user_name");
      const timestamp = feature.get("timestamp");
      const video_file = feature.get("video_file");
      console.log(`user_name=${user_name} timestamp=${timestamp}`);
      if (user_name && timestamp) {
        // Style
        const color = feature.getStyle().stroke_.color_;
        const style = new ol.style.Style({
          stroke: new ol.style.Stroke({
            color: color,
            width: 10,
          }),
        });
        feature.setStyle(style);

        // Popup
        this.popup_msg_username = user_name;
        // this.popup_msg_timestamp = `${new Date(
        //   timestamp
        // ).toLocaleDateString()} ${new Date(timestamp).toLocaleTimeString()}`;
        this.popup_msg_timestamp = new Date(timestamp).toLocaleString();
        this.popup_msg_url = video_file ? `/callrec/${video_file}` : null;
        this.show(ev.coordinate);
      } else {
        this.popup_msg_username = null;
        this.popup_msg_timestamp = null;
        this.popup_msg_url = null;
        this.hide();
      }
    },
  },
  mounted() {
    this.init_map_popup();
  },
};
</script>

<style>
@media screen and (min-width: 0px) and (max-width: 479px) {
  /* スマホ */
  .map_popup {
    width: 200px;
  }
}
@media screen and (min-width: 480px) {
  /* スマホ以上 */
  .map_popup {
    width: 300px;
  }
}
.map_popup {
  /* width: 300px; */
  position: relative;
  /* display: inline-block; */
  margin: 1.5em 0;
  /* padding: 7px 10px; */
  padding: 1px;
  min-width: 120px;
  max-width: 100%;
  color: #555;
  font-size: 16px;
  background: #eeffff;
  box-sizing: border-box;
  border-radius: 10px;
  filter: drop-shadow(8px 8px 5px #333);
}
.map_popup:before {
  content: "";
  position: absolute;
  bottom: -24px;
  left: 50%;
  margin-left: -15px;
  border: 12px solid transparent;
  border-top: 12px solid #eeffff;
  z-index: 2;
  filter: drop-shadow(8px 8px 5px #333);
}

.map_popup:after {
  content: "";
  position: absolute;
  bottom: -30px;
  left: 50%;
  margin-left: -17px;
  border: 14px solid transparent;
  border-top: 14px solid #555;
  z-index: 1;
}
.popup_video {
  width: 80%;
}
</style>
