<template>
  <div id="memo_popup_elm" class="memo_popup">
    <!-- 最初に表示される選択ボタン -->
    <div v-if="show_select_btns" class="box_padding10">
      <div class="classify_name_view">分類 : {{ memo_classify }}</div>
      <div style="display: flex; justify-content: space-around">
        <div
          class="memolayer_btn"
          @click="on_click_memo"
          title="テキストメモ作成"
        >
          <img :src="icon_memo" class="select_btns_icon" />
        </div>
        <div class="memolayer_btn" @click="on_click_photo" title="写真メモ作成">
          <img :src="icon_photo" class="select_btns_icon" />
        </div>
        <div class="memolayer_btn" @click="on_click_movie" title="映像メモ作成">
          <img :src="icon_movie" class="select_btns_icon" />
        </div>
      </div>
    </div>
    <modalDialog v-if="is_dialog_visible">
      <span slot="header"></span>
      <span slot="body"
        >ビデオデバイスが機能していないため、{{
          dialog_msg
        }}は使用出来ません。</span
      >
      <div slot="footer">
        <button class="btn btn-primary" @click="close_msg_dialog">はい</button>
      </div>
    </modalDialog>

    <!-- <MemoBtns v-if="show_memo_box"></MemoBtns>
    <PhotoBtns v-if="show_photo_box" ref="photo"></PhotoBtns>
    <MovieBtns v-if="show_video_box" ref="video"></MovieBtns> -->
  </div>
</template>

<script>
import $store from "@/store";
import { memoEvent } from "./index.js";
// import BtnMemo from "./BtnMemo";
// import MemoBtns from "./MemoBtns";
// import PhotoBtns from "./PhotoBtns";
// import MovieBtns from "./MovieBtns";
import ModalDialog from "@/components/ModalDialog.vue";

import { Map, View, Overlay, Feature } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";

const ol = {};
ol.Map = Map;
ol.View = View;
ol.Overlay = Overlay;
ol.Feature = Feature;
ol.layer = olLayer;
ol.control = olControl;
ol.interaction = olInteraction;
ol.proj = olProj;
ol.source = olSource;
ol.style = olStyle;
ol.geom = olGeom;

export default {
  data() {
    return {
      // stream: null,
      icon_memo: require("./assets/memo_text@1x.png"),
      icon_photo: require("./assets/memo_photo@1x.png"),
      icon_movie: require("./assets/memo_movie@1x.png"),
      show_memo_box: false,
      show_video_box: false,
      show_photo_box: false,
      show_select_btns: true,
      is_dialog_visible: false,
      dialog_msg: "",
      video_track: null,
    };
  },
  components: {
    // MemoBtns: MemoBtns,
    // PhotoBtns: PhotoBtns,
    // MovieBtns: MovieBtns,
    modalDialog: ModalDialog,
  },
  mounted() {
    memoEvent.$on("BACK_MEMO", () => {
      this.reset();
    });

    const map = $store.getters["amt/map"];
    let memo_popup_elm = document.getElementById("memo_popup_elm");
    let memoPop = new ol.Overlay({
      element: memo_popup_elm,
      positioning: "bottom-center",
      autoPan: true,
      autoPanAnimation: {
        duration: 250,
      },
    });
    map.addOverlay(memoPop);
    memoEvent.$emit("INIT_MEMO_POP", memoPop);
  },
  methods: {
    close_msg_dialog() {
      this.is_dialog_visible = false;
      this.dialog_msg = "";
    },
    re_stream() {
      // if (this.show_video_box) this.on_click_movie();
      // if (this.show_photo_box) this.on_click_photo();
      // if (this.show_video_box) this.$refs.video.init();
      // if (this.show_photo_box) this.$refs.photo.init();
    },
    reset() {
      // this.show_select_btns = true;
      this.show_memo_box = false;
      this.show_video_box = false;
      this.show_photo_box = false;
    },
    on_click_memo() {
      this.$store.state.sound_on();
      // this.$store.commit("memo/setMemoMakesView", true);
      memoEvent.$emit("TEXT_MEMO_MAKE");
      // 次の画面に行った時は作成ポップアップを非表示
      memoEvent.$emit("BACK_MEMO");
    },
    on_click_photo() {
      [this.track] = this.local_video_stream.getVideoTracks();
      if (this.track) {
        this.$store.state.sound_on();
        // this.$store.commit("memo/setMemoMakesView", true);
        memoEvent.$emit("PHOTO_MEMO_MAKE");
        // 次の画面に行った時は作成ポップアップを非表示
        memoEvent.$emit("BACK_MEMO");
      } else {
        this.dialog_msg = "写真メモ作成";
        this.is_dialog_visible = true;
      }
    },
    on_click_movie() {
      [this.track] = this.local_video_stream.getVideoTracks();
      if (this.track) {
        this.$store.state.sound_on();
        // this.$store.commit("memo/setMemoMakesView", true);
        memoEvent.$emit("MOVIE_MEMO_MAKE");
        // 次の画面に行った時は作成ポップアップを非表示
        memoEvent.$emit("BACK_MEMO");
      } else {
        this.dialog_msg = "映像メモ作成";
        this.is_dialog_visible = true;
      }
    },
  },
  computed: {
    memo_classify() {
      return this.$store.state.memo_classify;
    },
    local_video_stream() {
      return this.$store.getters["rtc/local_video_stream"];
    },
    socketio() {
      return this.$store.state.socketio;
    },
  },
  beforeDestroy() {
    // this.stream.getTracks().forEach(track => {
    //   track.stop();
    // });
  },
};
</script>

