//     type: type,                  /* 種類 audio | video | both (MUST)*/
//     stream: stream,              /* 自デバイスのstream (option)*/
//     socket: socket,              /* 外部にあるsocketを使用する場合に指定する(option) */
//     EVENT_KEY: event_key,        /* socket.ioでのメッセージキー。指定しないとpublishになる */
//     options: options,            /* 別途iceサーバを使用する場合に指定する(option) */
// });
