// import io from "socket.io-client";
export function P2P(args) {
  this.name = args.name;
  //--------------------------------------------------
  // LOG
  //--------------------------------------------------
  const self = this;
  this.LOG = function (title, s, style) {
    // const NO_DISP = ["OFFER", "ANSWER", "ICE"]
    const NO_DISP = [];
    if (NO_DISP.includes(title)) return;

    if (style) {
      console.log(`${self.my_id} in ${this.name}: ${title}: %c${s}`, style, "");
    } else {
      console.log(`${self.my_id} in ${this.name}: ${title}: ${s}`);
    }
  };
  //--------------------------------------------------
  // Setting
  //--------------------------------------------------
  // const options = args.options || {
  //   iceServers: [{ urls: `stun:${args.stun_server}` }]
  // };
  const options = args.options || {
    iceServers: args.stun_server,
  };

  if (!args.my_id || !args.remote_id || !args.type) {
    throw new Error("missing setting(args.XXX)");
  }

  this.closed = false;
  this.socketio = args.socket;
  // this.socketio = io.connect();
  this.my_id = args.my_id;
  this.remote_id = args.remote_id;
  this.group_id = args.group_id || "g1";
  this.stream = args.stream;
  this.direction = args.direction;
  this.type = args.type;
  this.EVENT_KEY = args.EVENT_KEY || "publish";

  this.recv_audio_track = null;
  this.recv_video_track = null;
  this.timerIds = {};

  //--------------------------------------------------
  // Regist
  // 本モジュール外部でサーバ側に登録している場合には不要
  //--------------------------------------------------
  // this.socketio.emit("regist", JSON.stringify({ id: this.my_id, group_id: this.group_id }));

  //--------------------------------------------------
  // PeerConnection
  //--------------------------------------------------
  this.pc = new RTCPeerConnection(options);
  this.check_types = {
    "inbound-rtp": ["packetsLost"],
    "outbound-rtp": ["bytesSent"],
    "remote-inbound-rtp": ["packetsLost"],
    "remote-outbound-rtp": ["bytesSent"],
  };
  this.timerIds["getStats"] = setInterval(async () => {
    const reports = await this.pc
      .getStats(null)
      .catch((err) => console.log(err));
    let flag = false;
    let res_str = "";
    let check_items = [];
    reports.forEach((report) => {
      Object.keys(this.check_types).forEach((check_type) => {
        if (check_type == report.type) {
          flag = true;
          res_str = `${new Date(report.timestamp).toLocaleTimeString()}: ${
            report.type
          } : ${report.id}  `;
          console.log(
            `-------------------${res_str}-------------------------------`
          );

          check_items = this.check_types[check_type];
        } else {
          flag = false;
          check_items = [];
        }

        if (flag) {
          check_items.forEach((item) => {
            if (
              // this.check_states.includes(statName) &&
              item !== "id" &&
              item !== "timestamp" &&
              item !== "type"
            ) {
              console.log(`${item} : ${report[item]}`);
            }
          });
        }
      });
    });
  }, 1000);

  //--------------------------------------------------
  // destroy
  //--------------------------------------------------
  this.close = () => {
    Object.keys(this.timerIds).forEach((id) => {
      clearInterval(this.timerIds[id]);
    });
    // this.pc.close();
    this.addTrack = null;
    this.pc.onicecandidate = null;
    this.pc.onsignalingstatechange = null;
    this.pc.oniceconnectionstatechange = null;
    this.pc.ontrack = null;
    this.call_in = null;
    this.call_out = null;
    this.callback = null;
    this.on = null;
    this.offer_addTrack = null;
    this.answer_addTrack = null;
    this.LOG = null;
    this.pc = null;
    this.closed = true;
    // this.socketio.off(this.EVENT_KEY);
    this.socketio.off(this.EVENT_KEY, this.recv_msg);
    this.socketio = null;
    this.my_id = null;
    this.remote_id = null;
    this.group_id = null;
    this.stream = null;
    this.direction = null;
    this.type = null;
    this.EVENT_KEY = null;
    this.recv_audio_track = null;
    this.recv_video_track = null;
  };

  //--------------------------------------------------
  // setStream
  //--------------------------------------------------
  this.setStream = (stream) => {
    this.stream = stream;
  };
  //--------------------------------------------------
  // addTrack for Offer
  //--------------------------------------------------
  this.offer_addTrack = (local_direction) => {
    const audio_track = this.stream ? this.stream.getAudioTracks()[0] : null;
    const video_track = this.stream ? this.stream.getVideoTracks()[0] : null;
    //--------------------SEND RECV OR ONLY------------------------------
    if (local_direction == "sendrecv" || local_direction == "sendonly") {
      if (this.type == "both" || this.type == "audio") {
        if (this.a_transceiver) {
          this.a_transceiver.sender.replaceTrack(audio_track);
        } else {
          const sender = this.pc.addTrack(audio_track, this.stream);
          this.a_transceiver = this.pc
            .getTransceivers()
            .find((tr) => tr.sender === sender);
        }
        this.a_transceiver.direction = local_direction;
      }
      if (this.type == "both" || this.type == "video") {
        if (this.v_transceiver) {
          this.v_transceiver.sender.replaceTrack(video_track);
        } else {
          const sender = this.pc.addTrack(video_track, this.stream);
          this.v_transceiver = this.pc
            .getTransceivers()
            .find((tr) => tr.sender === sender);
        }
        this.v_transceiver.direction = local_direction;
      }
      //--------------------RECV ONLY------------------------------
    } else if (local_direction == "recvonly") {
      if (this.type == "both" || this.type == "audio") {
        if (this.a_transceiver) {
          this.a_transceiver.sender.replaceTrack(null);
        } else {
          this.a_transceiver = this.pc.addTransceiver("audio", {
            streams: [new MediaStream()],
            direction: "recvonly",
          });
        }
        this.a_transceiver.direction = "recvonly";
      }
      if (this.type == "both" || this.type == "video") {
        if (this.v_transceiver) {
          this.v_transceiver.sender.replaceTrack(null);
        } else {
          this.v_transceiver = this.pc.addTransceiver("video", {
            streams: [new MediaStream()],
            direction: "recvonly",
          });
        }
        this.v_transceiver.direction = "recvonly";
      }
    }
  };

  //--------------------------------------------------
  // addTrack for Answer
  //--------------------------------------------------
  this.answer_addTrack = (local_direction) => {
    const audio_track = this.stream ? this.stream.getAudioTracks()[0] : null;
    const video_track = this.stream ? this.stream.getVideoTracks()[0] : null;
    //------------- SEND RECV ---------------------
    if (local_direction == "sendrecv" || local_direction == "sendonly") {
      if (this.type == "both" || this.type == "audio") {
        if (this.a_transceiver) {
          this.a_transceiver.sender.replaceTrack(audio_track);
        } else {
          const sender = this.pc.addTrack(audio_track, this.stream);
          this.a_transceiver = this.pc
            .getTransceivers()
            .find((tr) => tr.sender === sender);
        }
        this.a_transceiver.direction = local_direction;
      }
      if (this.type == "both" || this.type == "video") {
        if (this.v_transceiver) {
          this.v_transceiver.sender.replaceTrack(video_track);
        } else {
          const sender = this.pc.addTrack(video_track, this.stream);
          this.v_transceiver = this.pc
            .getTransceivers()
            .find((tr) => tr.sender === sender);
        }
        this.v_transceiver.direction = local_direction;
      }
    } else if (local_direction == "recvonly") {
      if (this.a_transceiver) {
        this.a_transceiver.direction =
          this.type == "both" || this.type == "audio" ? local_direction : null;
      } else {
        this.a_transceiver = this.pc
          .getTransceivers()
          .find((tr) => tr.receiver.track.kind == "audio");
      }
      if (this.v_transceiver) {
        this.v_transceiver.direction =
          this.type == "both" || this.type == "video" ? local_direction : null;
      } else {
        this.v_transceiver = this.pc
          .getTransceivers()
          .find((tr) => tr.receiver.track.kind == "video");
      }
    }
  };

  this.addTrack = (local_direction) => {
    const audio_track = this.stream ? this.stream.getAudioTracks()[0] : null;
    const video_track = this.stream ? this.stream.getVideoTracks()[0] : null;
    //------------- SEND RECV ---------------------
    if (local_direction == "sendrecv" || local_direction == "sendonly") {
      if (this.type == "both" || this.type == "audio") {
        if (this.a_transceiver) {
          this.a_transceiver.sender.replaceTrack(audio_track);
          this.a_transceiver.direction = local_direction;
        } else {
          const sender = this.pc.addTrack(audio_track, this.stream);
          this.a_transceiver = this.pc
            .getTransceivers()
            .find((tr) => tr.sender === sender);
        }
        this.a_transceiver.direction = local_direction;
      }
      if (this.type == "both" || this.type == "video") {
        if (this.v_transceiver) {
          this.v_transceiver.sender.replaceTrack(video_track);
          this.v_transceiver.direction = local_direction;
        } else {
          const sender = this.pc.addTrack(video_track, this.stream);
          this.v_transceiver = this.pc
            .getTransceivers()
            .find((tr) => tr.sender === sender);
        }
        this.v_transceiver.direction = local_direction;
      }

      //------------- RECV ONLY ---------------------
    } else if (local_direction == "recvonly") {
      if (this.type == "both" || this.type == "audio") {
        if (this.a_transceiver) {
          this.a_transceiver.sender.replaceTrack(null);
          this.a_transceiver.direction = "recvonly";
        } else {
          this.a_transceiver = this.pc.addTransceiver("audio", {
            streams: [new MediaStream()],
            direction: "recvonly",
          });
        }
      } else {
        if (this.v_transceiver) {
          this.v_transceiver.sender.replaceTrack(null);
          this.v_transceiver.direction = "recvonly";
        } else {
          this.v_transceiver = this.pc.addTransceiver("video", {
            streams: [new MediaStream()],
            direction: "recvonly",
          });
        }
      }
    }
  };

  this.pc.onicecandidate = (ev) => {
    if (ev.candidate) {
      const data = {
        dest: this.remote_id,
        src: this.my_id,
        type: "candidate",
        candidate: ev.candidate,
      };
      this.LOG("ICE", JSON.stringify(data));
      this.LOG("ICE", `SEND to ${data.dest}`);
      this.socketio.emit(this.EVENT_KEY, JSON.stringify(data));
    }
  };
  this.pc.addEventListener("signalingstatechange", () => {
    if (this.pc) {
      this.LOG("SIGNAL", this.pc.signalingState);
    } else {
      console.dir(this);
    }
  });
  this.pc.addEventListener("iceconnectionstatechange", () => {
    if (this.pc) {
      this.LOG("CONNECT", this.pc.iceConnectionState);
    }
  });

  this.recv_msg = async (msg) => {
    if (this.closed) {
      return;
    }

    const data = JSON.parse(msg);
    if (!(data.src == this.remote_id && data.dest == this.my_id)) return;

    if (data.type == "offer") {
      this.LOG("着信OFFER", msg);
      try {
        if (this.pc.signalingState != "stable") return;
      } catch (err) {
        this.LOG(
          "ERR",
          `this.my_id=${this.my_id}`,
          "color:red; font-size: large;"
        );
      }

      await this.pc.setRemoteDescription(data);
      await this.answer_addTrack(data.direction);
      await this.pc
        .createAnswer()
        .then((answer) => this.pc.setLocalDescription(answer));
      const _data = {
        dest: this.remote_id,
        src: this.my_id,
        type: "answer",
        sdp: this.pc.localDescription.sdp,
      };
      this.socketio.emit(this.EVENT_KEY, JSON.stringify(_data));
      this.LOG("SDP", `応答SEND to ${_data.dest}`);
    } else if (data.type == "answer") {
      this.LOG("着信ANSWER", msg);
      if (this.pc.signalingState != "have-local-offer") return;
      await this.pc.setRemoteDescription(data);
    } else if (data.type == "candidate") {
      this.LOG("着信ICE", msg);
      try {
        await this.pc.addIceCandidate(data.candidate);
      } catch (err) {
        this.LOG("ERR", `CANDIDATE ERRO=${err}`);
        // console.log(JSON.stringify(data.candidate));
      }
    }
  };
  this.socketio.on(this.EVENT_KEY, this.recv_msg);

  //--------------------------------------------------
  // remote_direction
  //--------------------------------------------------
  this.remote_direction = (local_direction) => {
    if (local_direction == "sendrecv") return "sendrecv";
    else if (local_direction == "sendonly") return "recvonly";
    else if (local_direction == "recvonly") return "sendonly";
    else return "inactive";
  };

  //--------------------------------------------------
  // 発呼
  //--------------------------------------------------
  this.call_in = async (local_direction) => {
    console.log(
      `---------CALL_IN:${this.my_id} > ${this.remote_id}:------------------`
    );
    this.offer_addTrack(local_direction);
    await this.pc
      .createOffer()
      .then((offer) => this.pc.setLocalDescription(offer));
    const data = {
      dest: this.remote_id,
      src: this.my_id,
      type: "offer",
      direction: this.remote_direction(local_direction),
      sdp: this.pc.localDescription.sdp,
    };
    // this.LOG("LOCAL SDP", JSON.stringify(data));
    this.LOG("SDP", `発呼SEND to ${data.dest}`);
    this.socketio.emit(this.EVENT_KEY, JSON.stringify(data));
    // this.socketio.emit("publish", data);
  };

  //--------------------------------------------------
  // 切断
  //--------------------------------------------------
  this.call_out = async () => {
    console.log(
      `---------CALL_OUT:${this.my_id} > ${this.remote_id}:------------------`
    );
    this.addTrack("recvonly");
    await this.pc
      .createOffer()
      .then((offer) => this.pc.setLocalDescription(offer));
    const data = {
      dest: this.remote_id,
      src: this.my_id,
      type: "offer",
      direction: "recvonly",
      sdp: this.pc.localDescription.sdp,
    };
    // this.LOG("LOCAL SDP", JSON.stringify(data));
    this.LOG("SDP", `切断SEND to ${data.dest}`);
    this.socketio.emit(this.EVENT_KEY, JSON.stringify(data));
  };

  this.callback = {};
  this.on = (key, func_name) => {
    this.callback[key] = func_name;
  };
  this.last_stream;
  this.pc.ontrack = (ev) => {
    this.LOG(
      "CALL_IN",
      `ev.track.kind=${ev.track.kind}`,
      "color: blue; font-size: large;"
    );
    // if (this.last_stream === ev.streams[0]) return;
    if (this.type == "both" || this.type == "audio") {
      if (ev.track.kind == "audio") {
        this.recv_audio_track = ev.track;
        this.recv_audio_track.onmute = () => {
          if (this.callback && this.callback.call_out) this.callback.call_out();
        };
      }
    } else {
      this.recv_audio_track = true;
    }

    if (this.type == "both" || this.type == "video") {
      if (ev.track.kind == "video") {
        this.recv_video_track = ev.track;
        this.recv_video_track.onmute = () => {
          if (this.callback && this.callback.call_out) this.callback.call_out();
        };
      }
    } else {
      this.recv_video_track = true;
    }

    if (this.recv_audio_track && this.recv_video_track) {
      if (this.type == "both") {
        this.callback.call_in(
          new MediaStream([this.recv_audio_track, this.recv_video_track])
        );
      } else if (this.type == "audio") {
        this.callback.call_in(new MediaStream([this.recv_audio_track]));
      } else if (this.type == "video") {
        this.callback.call_in(new MediaStream([this.recv_video_track]));
      }
    }

    // ev.streams[0].onremovetrack = () => this.callback.call_out();
    // ev.track.onmute = () => {
    //   this.callback.call_out();
    //   // ev.track.onmute = null;
    // };
    // this.last_stream = ev.streams[0];
  };
}

// <<使い方>>
//
// import {getStream} from "./P2P.js"; /* モジュールのインポート */
// const stream = getStream (          /* 返値:MediaStream */
//     elm,                            /* MediaStreamを表示するvideo要素 or audio要素 */
//     mConstruction,                  /* 制約(デフォルト値: {video:true, audio:true}) */
// );

export async function getStream(elm, mConstruction) {
  const construction = mConstruction || {
    video: true,
    audio: true,
  };
  const stream = await navigator.mediaDevices.getUserMedia(construction);
  if (elm) {
    elm.srcObject = stream;
    await elm.play();
    elm.style = "display: block";
  }
  return stream;
}

export async function getDesktopStream(elm, mConstruction) {
  const construction = mConstruction || {
    video: {
      displaySurface: "application",
      frameRate: 3,
      // width: 640
    },
    audio: false,
  };
  elm.style = "display: none";
  const stream = await navigator.mediaDevices.getDisplayMedia(construction);
  if (elm) {
    elm.srcObject = stream;
    await elm.play();
    elm.style = "display: block";
  }
  return stream;
}

// <<使い方>>
//
// import {stats} from "./P2P.js";     /* モジュールのインポート */
// stats (                             /* 返値:無し */
//     pc,                             /* ステータスを取得する対象:PeerConnection */
//     elm,                            /* 結果を表示するdiv要素相当 */
// );

export function stats(pc, elm) {
  this.timerId = setInterval(function () {
    pc.getStats(null).then((stats) => {
      let statsOutput = "";

      stats.forEach((report) => {
        statsOutput +=
          `<h2>Report: ${report.type}</h3>\n<strong>ID:</strong> ${report.id}<br>\n` +
          `<strong>Timestamp:</strong> ${report.timestamp}<br>\n`;

        // Now the statistics for this report; we intentially drop the ones we
        // sorted to the top above

        Object.keys(report).forEach((statName) => {
          if (
            statName !== "id" &&
            statName !== "timestamp" &&
            statName !== "type"
          ) {
            statsOutput += `<strong>${statName}:</strong> ${report[statName]}<br>\n`;
          }
        });
      });

      elm.innerHTML = statsOutput;
    });
  }, 1000);
  this.clear = () => {
    clearInterval(this.timerId);
  };
}
