<template>
  <div>
    <canvas
      ref="cvsRef"
      :width="width"
      :height="height"
      class="cvsRef"
    ></canvas>
  </div>
</template>

<script>
/* eslint-disable */
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import { DeviceOrientationControls } from "three/examples/jsm/controls/DeviceOrientationControls.js";

export default {
  props: ["videoElm", "width", "height"],
  data() {
    return {
      scene: null,
      camera: null,
      controls: null,
      renderer: null,
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    async init() {
      //windowサイズを画面サイズに合わせる
      // シーンの作成
      this.scene = new THREE.Scene();
      // カメラの作成
      this.camera = new THREE.PerspectiveCamera(
        75,
        this.width / this.height,
        1,
        1000
      );
      this.camera.position.set(0, 0, 0);
      this.scene.add(this.camera);
      // 球体の形状を作成
      const geometry = new THREE.SphereGeometry(5, 60, 40);
      geometry.scale(-1, 1, 1);
      // テクスチャー
      const texture = new THREE.VideoTexture(this.videoElm);
      texture.minFilter = THREE.LinearFilter;
      texture.magFilter = THREE.LinearFilter;
      texture.format = THREE.RGBFormat;
      //球体のマテリアルを生成
      const material = new THREE.MeshBasicMaterial({ map: texture });
      // 球体(形状)にマテリアル(質感)を貼り付けて物体を作成
      const sphere = new THREE.Mesh(geometry, material);
      //　シーンに追加
      this.scene.add(sphere);
      // レンダラーの作成
      this.renderer = new THREE.WebGLRenderer({ canvas: this.$refs.cvsRef });
      // レンダラーをサイズに合わせる
      this.renderer.setSize(this.width, this.height);
      this.renderer.setClearColor({ color: 0x000000 });
      // const element = this.renderer.domElement;
      // // document.getElementById("stage").appendChild(element);
      // element.style.width = "500";
      // element.style.height = "500";
      // elm.appendChild(element);
      this.renderer.render(this.scene, this.camera);

      // デバイスの判別
      let isAndroid = false;
      let isIOS = false;
      if (navigator.userAgent.indexOf("Android") != -1) {
        //　デバイスがAndroidの場合
        isAndroid = true;
      } else if (
        /(iPad|iPhone|iPod|macintosh)/g.test(navigator.userAgent) &&
        "ontouchend" in document
      ) {
        //　デバイスがiOSの場合
        isIOS = true;
      }
      // if (isAndroid || isIOS) {
      //   // デバイスがスマートフォンまたはタブレット端末の場合
      //   // ジャイロセンサーで視点操作を可能にする
      //   window.addEventListener("deviceorientation", this.setOrientationControls, true);
      // } else {
      //   // パソコンの場合
      //   // マウスドラッグで視点操作を可能にする
      //   this.setOrbitControls();
      // }
      if (isAndroid) {
        // デバイスがスマートフォンまたはタブレット端末の場合
        // ジャイロセンサーで視点操作を可能にする
        window.addEventListener(
          "deviceorientation",
          this.setOrientationControls,
          true
        );
        // } else if (isIOS) {
        //   const res = await DeviceOrientationEvent.requestPermission();
        //   if (res === "granted") {
        //     window.addEventListener("deviceorientation", this.setOrientationControls, true);
        //   } else {
        //     this.setOrbitControls();
        //   }
      } else {
        // パソコンの場合
        // マウスドラッグで視点操作を可能にする
        this.setOrbitControls();
      }
      this.render();
    },
    // リサイズ処理
    handleResize() {
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();
    },
    setOrbitControls() {
      // パソコン閲覧時マウスドラッグで視点操作する
      // controls = new THREE.OrbitControls(camera, element);
      this.controls = new OrbitControls(this.camera, this.$refs.cvsRef);
      this.controls.target.set(
        this.camera.position.x + 0.15,
        this.camera.position.y,
        this.camera.position.z
      );
      // 視点操作のイージングをONにする
      this.controls.enableDamping = true;
      // 視点操作のイージングの値
      this.controls.dampingFactor = 0.2;
      // 視点変更の速さ
      // this.controls.rotateSpeed = 0.1;
      this.controls.rotateSpeed = -0.2;
      // ズーム禁止
      this.controls.noZoom = false;
      // パン操作禁止
      this.controls.noPan = false;
    },
    // ジャイロセンサーで視点操作する
    setOrientationControls(e) {
      // スマートフォン以外で処理させない
      if (!e.alpha) {
        // ジャイロが効いていない場合
        return;
      }
      // controls = new THREE.DeviceOrientationControls(camera, true);
      this.controls = new DeviceOrientationControls(this.camera, true);
      this.controls.connect();
      this.controls.update();
      window.removeEventListener(
        "deviceorientation",
        this.setOrientationControls,
        true
      );
    },
    render() {
      requestAnimationFrame(this.render);
      this.renderer.render(this.scene, this.camera);
      this.controls.update();
    },
  },
};
</script>

<style>
.cvsRef {
  width: 100% !important;
  height: 100% !important;
}
</style>
