    async init({ state, dispatch }) {
      // サーバから設定取得 @todo 例外処理
      // const res = await fetch("/api/config", { method: "POST" });
      try {
        const res = await fetch("/api/config", {
          method: "POST",
          body: JSON.stringify({ user: state.userId }),
          headers: {
            "Content-Type": "application/json",
          },
        });
        // JSONデータを取得
        const info = await res.json();
        console.dir(info);

        // 位置情報が取れるまでの初期表示
        if (info.fixed_pos || !info.last_position) {
          // 固定局の場合、軌跡データがない場合はuser_configの設定値
          state.initial_lng = info.default_lng;
          state.initial_lat = info.default_lat;
          state.initial_time = null;
        } else {
          // 最終位置情報
          // 現在はサーバの軌跡保持期間が24hのためそれ以上経過で無効になる
          // last_position.datetimeをチェックすることで保持期間よりも短い時間で
          // 有効期限切れとすることも可能
          state.initial_lng = info.last_position.lng;
          state.initial_lat = info.last_position.lat;
          state.initial_time = info.last_position.datetime;
        }
        // グループリスト初期化
        state.groups = info.group.map((g) => {
          return {
            group_id: g.group_id,
            group_name: g.group_name,
            is_active: false,
            is_person: false,
          };
        });
        // グループ初期選択
        state.activeId = { kind: "group", id: info.group_id };
        // 設定値をコピー
        state.myId = info.user_id;
        state.user_type = info.user_type;
        state.myName = info.user_name;
        state.myFullName = info.user_full_name;
        state.myCompanyId = info.company_id;
        state.myCompanyName = info.company_name;
        state.group_id = info.group_id;
        state.gps_timeout_ms = info.gps_timeout_ms;
        state.default_lng = info.default_lng;
        state.default_lat = info.default_lat;
        state.logMode = info.logMode;
        state.logLevel = info.logLevel;
        state.myIcon = info.myIcon;
        state.myColor = info.myColor;
        state.stun_server = info.stun_server;
        state.track_point = info.track_point;
        state.center_trace = info.center_trace;
        state.fixed_pos = info.fixed_pos;
        state.gps_server = info.gps_server;
        state.volume = info.volume;
        state.tiles_url = info.tiles_url;
        state.video_width = info.video_width;
        state.video_frame = info.video_frame;
        state.video_rate = info.video_rate;
        state.video_encode = info.video_encode;
        state.callrec = info.callrec;
        state.callrec_display = info.callrec_display;
        state.ptt_type = info.ptt_type;
        state.memo_classify = info.memo_classify;
        state.color_picker = info.color_picker;
        state.tel_type = info.tel_type;
        state.tel_code = info.tel_code;
        state.sip_server = info.sip_server;
        state.sip = info.sip;
        state.speech_recognition = info.speech_recognition;
        state.auto_play = info.auto_play;
        state.map_urls = info.map_urls;
        state.check_connection_period = isNaN(info.check_connection_period)
          ? 10000
          : info.check_connection_period;
        state.memo_downloadable = 0 != info.memo_downloadable ? true : false;
        // 手書きの色指定
        try {
          if (
            info.draw_color.match(/^#([A-Fa-f0-9]{6}|[A-Fa-f0-9]{3})$/) !== null
          ) {
            state.draw_color = info.draw_color;
          } else {
            state.draw_color = "#D33115";
          }
        } catch (e) {
          state.draw_color = "#D33115";
        }

        // jsonからPTT対応のキーコード(16進数)を取得
        if (info.ptt_keycode) {
          ptt.state.ptt_keycode = parseInt(info.ptt_keycode, 16);
        } else {
          ptt.state.ptt_keycode = 0x1b;
        }

        // PTTモジュール初期化
        dispatch("ptt/initPTT");
      } catch (err) {
        console.log(err);
      }
    },
